import type {FC} from 'react'
import React, {useMemo} from 'react'

import {formatDate, isSame, MS_IN_HOUR, MS_IN_MINUTE} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {StatsAPICalendarEvent, SupervisorWidgetConfigAgenda} from '@bpinc/lib-widgets-urls'

import {useAgendaQuery} from '../../queries'
import type {WidgetProps} from '../../types'


const EVENT_AMOUNT_LIMIT = 6

function getEventColor(startTime: number) {
    if (aboutToStartOrInProgress()) {
        return 'bg-[--wallboard-agenda-in-progress-event-color]'
    }

    if (twoHoursToStart()) {
        return 'bg-[--wallboard-agenda-soon-event-color]'
    }

    return 'bg-[--wallboard-agenda-future-event-color]'

    function aboutToStartOrInProgress() {
        return startTime - Date.now() < 10 * MS_IN_MINUTE
    }

    function twoHoursToStart() {
        return startTime - Date.now() < 2 * MS_IN_HOUR
    }
}

export const AgendaWidget: FC<WidgetProps<SupervisorWidgetConfigAgenda>> = ({
    config,
}) => {
    const {data: agenda} = useAgendaQuery(config.user_id)

    const eventsList = useMemo(() => {
        return agenda?.[config.user_id]?.slice(0, EVENT_AMOUNT_LIMIT) ?? []
    }, [agenda, config.user_id])

    const eventsListByDates = useMemo(() => {
        return eventsList.reduce<Record<string, StatsAPICalendarEvent[]>>(
            (acc, event) => {
                const formattedDate = formatDate(
                    event.startTime,
                    'dddd, MMMM D',
                )

                if (!acc[formattedDate]) {
                    acc[formattedDate] = []
                }
                acc[formattedDate].push(event)
                return acc
            },
            {},
        )
    }, [eventsList])

    return (
        <WithNoData
            noData={!eventsList.length}
            className="!justify-start !overflow-auto"
        >
            {Object.entries(eventsListByDates).map(([date, events]) => (
                <div key={date} className="flex w-full flex-col">
                    <div className="mx-5 mb-2.5 mt-5 text-[1.5vmin] text-[--wallboard-text-secondary]">
                        {date}
                    </div>
                    {events.map((event, index) => {
                        const sameDayEvent = isSame(
                            new Date(event.startTime),
                            new Date(event.endTime),
                            'day',
                        )
                        return (
                            <div
                                key={event.id}
                                className={tcn('flex flex-auto', {
                                    'bg-[--wallboard-table-odd-row-bg]': (index + 1) % 2,
                                    'bg-[--wallboard-table-even-row-bg:]': !((index + 1) % 2),
                                })}
                            >
                                <div className="w-2/5 py-1 pl-5">
                                    <div>
                                        {formatDate(event.startTime, sameDayEvent
                                            ? 'hh:mm'
                                            : 'MMM DD, hh:mm')}
                                    </div>
                                    <div>
                                        {formatDate(
                                            event.endTime,
                                            sameDayEvent
                                                ? 'hh:mm'
                                                : 'MMM DD, hh:mm',
                                        )}
                                    </div>
                                </div>
                                <div
                                    className={tcn(
                                        getEventColor(event.startTime),
                                        'h-full w-[5px]',
                                    )}
                                />
                                <div className="flex grow items-center px-2.5 py-1">
                                    {event.title}
                                </div>
                            </div>
                        )
                    })}
                </div>
            ))}
        </WithNoData>
    )
}
