import {useCallback} from 'react'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalsStateMethods} from '@bpinc/state-modals-context'

import {useRemoveWidget as useSimpleRemoveWidget} from '../../editing-context'


export function useRemoveWidget() {
    const {openModal} = useModalsStateMethods()
    const {t} = useLocalizationState()
    const removeWidget = useSimpleRemoveWidget()

    return useCallback(
        (widget: SupervisorWidget) => {
            assertDefined(widget)

            openModal({
                title: t.WALLBOARD.confirm_delete_title(widget.title),
                body: t.WALLBOARD_WIDGET_EMPTY.confirm_delete_widget(
                    widget.title,
                ),
                actions: {
                    ok: {
                        action() {
                            removeWidget(widget.id)
                        },
                        title: t.WALLBOARD.delete(),
                    },
                    cancel: {
                        title: t.WALLBOARD.cancel(),
                    },
                },
            })
        },
        [openModal, t.WALLBOARD, removeWidget, t.WALLBOARD_WIDGET_EMPTY],
    )
}
