import React from 'react'
import {QueryClient, QueryClientProvider} from '@tanstack/react-query'

import {MS_IN_HOUR} from '@bpinc/lib-helpers'
import {Modals} from '@bpinc/lib-modals-ui'

import {Content, Header} from '../layout'
import {RouterContextProvider} from '../layout/context/RouterContext'
import {UIProvider as WallboardUIContextProvider} from '../ui-context/UIContext'
import {PagePreloader} from './PagePreloader'


const DEFAULT_CACHE_TIME = 8 * MS_IN_HOUR
const queryClient = new QueryClient({
    defaultOptions: {
        queries: {
            gcTime: DEFAULT_CACHE_TIME,
        },
    },
})

export const WallboardPage: React.FC = () => {
    return (
        <QueryClientProvider client={queryClient}>
            <WallboardUIContextProvider>
                <RouterContextProvider>
                    <PagePreloader>
                        <div className="relative flex h-full w-full min-w-0 flex-auto flex-col">
                            <Header />
                            <Content />
                        </div>
                        <Modals />
                    </PagePreloader>
                </RouterContextProvider>
            </WallboardUIContextProvider>
        </QueryClientProvider>
    )
}
