import type {CSSProperties} from 'react'
import React, {useMemo} from 'react'

import type {SupervisorWidgetConfigImage} from '@bpinc/lib-widgets-urls'

import type {WidgetProps} from '../../types'


export const ImageWidget: React.FC<WidgetProps<SupervisorWidgetConfigImage>> = ({
    config,
}) => {
    const style = useMemo<CSSProperties>(() => ({
        backgroundImage: `url(${config.imageUrl})`,
        backgroundSize: config.style,
    }), [config.imageUrl, config.style])

    return (
        <div
            className="h-full bg-center bg-no-repeat"
            style={style}
        />
    )
}

