import React from 'react'

import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'


export const WithNoAvailable: FCWithChildren = ({
    children,
}) => {
    const {t} = useLocalizationState()

    return (
        <>
            {
                !children ? t.WALLBOARD.no_available() : children
            }
        </>
    )
}
