import type {FC} from 'react'
import React from 'react'

import {SmButtonGroup, SmIcon} from '@bpinc/lib-external-ui-elements'
import {useBoolean} from '@bpinc/lib-helpers'
import {PopupButton} from '@bpinc/lib-ui-elements'

import {useDarkTheme, useEditMode, usePageConfig} from '../../ui-context/UIContext'
import {HeaderButton} from './HeaderButton'
import {useMenuOptions} from './useMenuOptions'
import {useSelectWallboard} from './useSelectWallboard'
import {WallboardSelect} from './WallboardSelect'


export const BaseHeaderContent: FC = () => {
    const darkTheme = useDarkTheme()
    const editMode = useEditMode()
    const pageConfig = usePageConfig()
    const selectWallboard = useSelectWallboard()
    const showMenuOptions = useBoolean()
    const menuOptions = useMenuOptions(showMenuOptions)

    return (
        <>
            <section className="flex items-center">
                <WallboardSelect />
                <SmButtonGroup className="ml-6 flex items-center">
                    <HeaderButton
                        icon="chevron left"
                        onClick={selectWallboard?.previous}
                        className="border border-solid border-main-border !bg-white"
                    />
                    <HeaderButton
                        icon="chevron right"
                        onClick={selectWallboard?.next}
                        className="border border-solid border-main-border !bg-white"
                    />
                </SmButtonGroup>
            </section>
            <section className="flex gap-3">
                {!pageConfig?.deniedActions?.changeTheme && (
                    <HeaderButton
                        icon={darkTheme.value ? 'sun outline' : 'moon outline'}
                        onClick={darkTheme.toggle}
                    />
                )}
                <HeaderButton
                    icon="edit outline"
                    onClick={editMode.setTrue}
                />
                <PopupButton
                    options={menuOptions}
                    show={showMenuOptions.value}
                    toggleShow={showMenuOptions.toggle}
                    optionClassName="hover:text-gray-800"
                    className="flex h-8 w-8 items-center justify-center !bg-white"
                >
                    <SmIcon name="bars" className="flex items-center justify-center text-xl" />
                </PopupButton>
            </section>
        </>
    )
}
