import {useQuery} from '@tanstack/react-query'

import {MS_IN_DAY} from '@bpinc/lib-helpers'
import {SupervisorWidgetsApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from './index'


export function useTenantConfigQuery() {
    return useQuery({
        queryKey: queryKey.tenant_config(),
        queryFn: SupervisorWidgetsApiServices.getTenantConfiguration,
        gcTime: MS_IN_DAY,
        staleTime: MS_IN_DAY,
    })

}
