import {useMemo} from 'react'

import type {SupervisorWidgetConfigMyStats, SupervisorWidgetsSubscriptionDataItem} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {FormatType} from '../../charts'
import {barOptions, formatSeries, formatTooltip} from '../../charts'


const chartColors = {myStats: '#BAD027', teamStats: '#00A6E6'}

const myStatsChartOptions = (
    title: string,
    height: number,
    formatType?: FormatType,
): Partial<Highcharts.Options> => {
    function labelsFormatter(
        this: Highcharts.SeriesDataLabelsFormatterContextObject,
    ) {
        return formatSeries(this, formatType)
    }

    function tooltipFormatter(this: Highcharts.TooltipFormatterContextObject) {
        return formatTooltip(this, formatType)
    }

    return {
        chart: {
            height,
            width: undefined,
        },
        plotOptions: {
            series: {
                dataLabels: {
                    overflow: undefined,
                    crop: false,
                    formatter: labelsFormatter,
                },
            },
        },
        title: {
            align: 'left',
            useHTML: true,
            margin: 8,
            x: 5,
            style: {
                fontSize: '14px',
                fontWeight: 'normal',
                whiteSpace: 'nowrap',
            },
            text: title,
        },
        xAxis: {
            labels: {
                enabled: false,
            },
        },
        tooltip: {
            formatter: tooltipFormatter,
        },
        colors: Object.values(chartColors),
    }
}


function calculateChartHeight(cardHeight: number, legendHeight: number) {
    return (cardHeight - legendHeight) * 0.5
}

type MyStatsData = {
    inCalls: { agentValue: number; teamValue: number }
    outCalls: { agentValue: number; teamValue: number }
    successRatio: { agentValue: number; teamValue: number }
    averageHandlingTime: { agentValue: number; teamValue: number }
}

const myStatsDefaultData = {
    inCalls: {agentValue: 0, teamValue: 0},
    outCalls: {agentValue: 0, teamValue: 0},
    successRatio: {agentValue: 0, teamValue: 0},
    averageHandlingTime: {agentValue: 0, teamValue: 0},
}

interface MyStatsOptions {
    subscriptionData: SupervisorWidgetsSubscriptionDataItem | undefined
    config: SupervisorWidgetConfigMyStats
    cardHeight: number
}

export function useMyStatsOptions({subscriptionData, config, cardHeight}: MyStatsOptions) {
    const {t} = useLocalizationState()
    const myStatsData = useMemo(() => {
        if (!subscriptionData) {
            return undefined
        }

        return (config.subscription?.req.columns ?? []).reduce<MyStatsData>(
            (result, column) => {
                const agentValue = Number(
                    subscriptionData?.agent_grids?.[0].values?.[0]?.[
                        Number(column.id ?? 0)
                    ] ?? 0,
                )

                switch (column.statName) {
                    case 'in_calls_handled_per_day': {
                        const teamValue = Number(
                            subscriptionData?.team_stats
                                ?.in_calls_handled_per_day ?? 0,
                        )
                        result.inCalls = {agentValue, teamValue}
                        break
                    }
                    case 'out_calls_handled_per_day': {
                        const teamValue = Number(
                            subscriptionData?.team_stats
                                ?.out_calls_handled_per_day ?? 0,
                        )
                        result.outCalls = {agentValue, teamValue}
                        break
                    }
                    case 'agent_success_ratio_per_day': {
                        const teamValue = Number(
                            subscriptionData?.team_stats
                                ?.team_success_ratio_per_day ?? 0,
                        )
                        result.successRatio = {agentValue, teamValue}
                        break
                    }
                    case 'calls_average_handling_time_per_day': {
                        const teamValue = Number(
                            subscriptionData?.team_stats
                                ?.calls_average_handling_time_per_day ?? 0,
                        )
                        result.averageHandlingTime = {agentValue, teamValue}
                        break
                    }
                }

                return result
            },
            {...myStatsDefaultData},
        )
    }, [config.subscription?.req.columns, subscriptionData])


    return useMemo(() => {
        if (!myStatsData) {
            return undefined
        }

        const handledTitle = t.WALLBOARD_MY_STATS_WIDGET.handled()
        const averageHandlingTimeTitle =
        t.WALLBOARD_MY_STATS_WIDGET.average_handling_time()
        const successRatioTitle =
        t.WALLBOARD_MY_STATS_WIDGET.successful_interactions()

        return {
            handled: barOptions(
                {
                    dataName: handledTitle,
                    data: [
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.my_stats(),
                            color: chartColors.myStats,
                            value:
                            myStatsData.inCalls.agentValue +
                            myStatsData.outCalls.agentValue,
                        },
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.teams_stats(),
                            color: chartColors.teamStats,
                            value:
                            myStatsData.inCalls.teamValue +
                            myStatsData.outCalls.teamValue,
                        },
                    ],
                },
                myStatsChartOptions(handledTitle, calculateChartHeight(cardHeight, 17)),
            ),
            averageHandlingTime: barOptions(
                {
                    dataName: averageHandlingTimeTitle,
                    data: [
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.my_stats(),
                            color: chartColors.myStats,
                            value: myStatsData.averageHandlingTime.agentValue,
                        },
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.teams_stats(),
                            color: chartColors.teamStats,
                            value: myStatsData.averageHandlingTime.teamValue,
                        },
                    ],
                },
                myStatsChartOptions(averageHandlingTimeTitle, calculateChartHeight(cardHeight, 17), 'minutes_seconds'),
            ),
            successRatio: barOptions(
                {
                    dataName: successRatioTitle,
                    data: [
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.my_stats(),
                            color: chartColors.myStats,
                            value: myStatsData.successRatio.agentValue,
                        },
                        {
                            name: t.WALLBOARD_MY_STATS_WIDGET.teams_stats(),
                            color: chartColors.teamStats,
                            value: myStatsData.successRatio.teamValue,
                        },
                    ],
                },
                myStatsChartOptions(successRatioTitle, calculateChartHeight(cardHeight, 17), 'percent'),
            ),
            legend: [
                {
                    name: t.WALLBOARD_MY_STATS_WIDGET.my_stats(),
                    color: chartColors.myStats,
                },
                {
                    name: t.WALLBOARD_MY_STATS_WIDGET.teams_stats(),
                    color: chartColors.teamStats,
                },
            ],
        }
    }, [cardHeight, myStatsData, t.WALLBOARD_MY_STATS_WIDGET])

}
