import React, {useMemo} from 'react'

import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SelectField} from '@bpinc/lib-forms-ui'
import {MS_IN_DAY, MS_IN_HOUR, MS_IN_MINUTE} from '@bpinc/lib-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'


export const TimeRangeField = <TValues, >({control}: FieldControlProps<TValues, { timeRange: number }>) => {
    const {t} = useLocalizationState()
    const options = useMemo(() => [
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.mins_5(), 'value': MS_IN_MINUTE * 5},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.mins_15(), 'value': MS_IN_MINUTE * 15},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.hours_1(), 'value': MS_IN_HOUR},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.hours_3(), 'value': MS_IN_HOUR * 3},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.hours_6(), 'value': MS_IN_HOUR * 6},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.hours_12(), 'value': MS_IN_HOUR * 12},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.hours_24(), 'value': MS_IN_DAY},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.days_2(), 'value': MS_IN_DAY * 2},
        {'text': t.WALLBOARD_WIDGET_TIME_SERIES.days_7(), 'value': MS_IN_DAY * 7},
    ], [t.WALLBOARD_WIDGET_TIME_SERIES])
    return (
        <GridRow>
            <FormLabel text={t.WALLBOARD_WIDGET_TIME_SERIES.series_type()} />
            <SelectField search options={options} fluid name="timeRange" control={control} />
        </GridRow>
    )
}
