import {toMMSS} from '@bpinc/lib-helpers'


export type FormatType = 'percent' | 'minutes_seconds'

export function formatSeries(context: Highcharts.SeriesDataLabelsFormatterContextObject, formatType?: FormatType) {
    switch (formatType) {
        case 'percent':
            return `${context.y}%`
        case 'minutes_seconds':
            return toMMSS(context.y)
        default:
            return `${context.y}`
    }
}

export function formatTooltip(context: Highcharts.TooltipFormatterContextObject, formatType?: FormatType) {
    switch (formatType) {
        case 'percent':
            return `${context.x}: ${context.y}%`
        case 'minutes_seconds':
            return `${context.x}: ${toMMSS(context.y)}`
        default:
            return `${context.x}: ${context.y}`
    }
}
