import React, {useEffect, useMemo, useRef} from 'react'
import {useSetAtom} from 'jotai'
import {throttle} from 'lodash-es'

import {audioNotifyAtom} from '../../../atoms'
import notificationMp3 from './sounds/notification.mp3'
import notificationWav from './sounds/notification.wav'


export const AudioAlert = () => {
    const audioRef = useRef<HTMLAudioElement>(null)

    const notify = useMemo(() => throttle(() => {
        audioRef.current?.play()
    }, 10000, {leading: true}), [])
    const setAudioNotify = useSetAtom(audioNotifyAtom)

    useEffect(() => {
        setAudioNotify({
            start: () => {
                notify()
                const id = setInterval(() => notify(), 10000)

                return {
                    stop: () => clearInterval(id),
                }
            },
        })
    }, [notify, setAudioNotify])

    return (
        <audio ref={audioRef}>
            <source src={notificationWav} />
            <source src={notificationMp3} />
        </audio>
    )
}
