import React, {useEffect, useMemo} from 'react'
import {useAtomValue} from 'jotai'

import {Chart} from '@bpinc/lib-highcharts'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigGauge} from '@bpinc/lib-widgets-urls'

import {audioNotifyAtom} from '../../atoms'
import {gaugeChartOptions} from '../../charts'
import {formatStatValue} from '../../stats/helpers'
import {useGetStatsConfig} from '../../stats/useGetStatsConfig'
import type {WidgetProps} from '../../types'


export const GaugeWidget: React.FC<WidgetProps<SupervisorWidgetConfigGauge>> = ({
    config,
    subscriptionData,
}) => {
    const getStatsConfig = useGetStatsConfig()
    const audioAlert = useAtomValue(audioNotifyAtom)

    const statConfig = useMemo(() => {
        if (!config.subscription?.req) {
            return undefined
        }
        return getStatsConfig('statsSingle', config.subscription.req[0])
    }, [config.subscription?.req, getStatsConfig])


    const gaugeValue = useMemo(() => {
        if (!subscriptionData || !config.subscription) {
            return undefined
        }

        const stat = config.subscription.req[0]
        const value = subscriptionData.stats_totals?.[stat]
        return Number(value ?? 0)
    }, [config.subscription, subscriptionData])

    useEffect(() => {
        if (!config.warning_sound) {
            return
        }
        const inverted = statConfig?.alertInverted || false
        const value = gaugeValue || 0
        const shouldNotify =
            (!inverted && value >= config.alertThreshold) ||
            (inverted && value <= config.alertThreshold)

        if (!shouldNotify || !audioAlert) {
            return
        }

        const {stop} = audioAlert.start()

        return () => {
            stop()
        }
    }, [audioAlert, config.alertThreshold, config.warning_sound, gaugeValue, statConfig?.alertInverted])

    const options: Highcharts.Options | undefined = useMemo(() => {
        if (gaugeValue === undefined) {
            return undefined
        }

        return gaugeChartOptions({
            value: gaugeValue,
            config,
            inverted: statConfig?.alertInverted || false,
            getFormattedValue: value => {
                return formatStatValue(value, statConfig?.format)
            },
        })
    }, [config, gaugeValue, statConfig])

    return (
        <WithNoData noData={options === undefined} className="h-full">
            <Chart options={options} />
        </WithNoData>
    )
}
