import React, {useMemo} from 'react'

import {MS_IN_HOUR, MS_IN_MINUTE, useBoolean} from '@bpinc/lib-helpers'
import {Chart} from '@bpinc/lib-highcharts'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigTimeSeries} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {DataType} from '../../charts/splineChartOptions'
import {getChartConfig} from '../../charts/splineChartOptions'
import {useTimeSeriesDataQuery} from '../../queries'


interface TimeSeriesProps {
    config: SupervisorWidgetConfigTimeSeries
}

function getTimeInterval(timeRange: number) {
    let timeInterval = '20s'
    if (timeRange >= MS_IN_HOUR) timeInterval = '1m'
    if (timeRange >= MS_IN_HOUR * 3) timeInterval = '5m'
    if (timeRange >= MS_IN_HOUR * 12) timeInterval = '10m'
    if (timeRange >= MS_IN_HOUR * 12 * 7) timeInterval = '30m'
    return timeInterval
}

const DEFAULT_RANGE_MS = 1000000

function calcQueryParams(config: SupervisorWidgetConfigTimeSeries) {
    const timeRangeEnd = new Date().getTime()
    const seriesType = config.series_type
    const statistic = config.value
    const timeRangeStart = timeRangeEnd - parseInt(config.timeRange)

    return {
        measurement: seriesType,
        statistic: statistic,
        start: String(timeRangeStart * DEFAULT_RANGE_MS),
        end: String(timeRangeEnd * DEFAULT_RANGE_MS),
        timeInterval: getTimeInterval(Number(config.timeRange)),
    }
}

export const TimeSeriesWidget: React.FC<TimeSeriesProps> = ({
    config,
}) => {
    const {t} = useLocalizationState()
    const {value: yGridVisible, toggle: toggleYGrid} = useBoolean(true)
    const {value: xGridVisible, toggle: toggleXGrid} = useBoolean(true)

    const params = calcQueryParams(config)
    const {data: timeSeriesData} = useTimeSeriesDataQuery(params)

    const parsedData = useMemo(() => {
        if (timeSeriesData && timeSeriesData.results && timeSeriesData.results.length && timeSeriesData.results[0].series) {
            const tempResult: { [key: string]: { x: number; y: number }[] } = {}
            const series = timeSeriesData.results[0].series
            series.forEach(function(s) {
                const name = s.tags?.name || ''
                tempResult[name] = []
                s.values.forEach(function(row) {
                    if (row[1] !== null) {
                        const date = new Date(row[0])
                        const correctTime = date.getTime() - (date.getTimezoneOffset() * MS_IN_MINUTE)
                        tempResult[name].push({x: correctTime, y: row[1]})
                    }
                })
            })

            const resultArr: DataType = []
            for (const key in tempResult) {
                const d = tempResult[key]
                const value = {name: key, data: d}
                resultArr.push(value)
            }

            return resultArr
        }
    }, [timeSeriesData])


    const options = useMemo(() => {
        if (parsedData === undefined) {
            return undefined
        }

        return getChartConfig(parsedData, yGridVisible, xGridVisible)
    }, [parsedData, yGridVisible, xGridVisible])


    return (
        <WithNoData noData={!parsedData?.length}>
            <Chart options={options} />
            <div className="flex w-8/12 justify-around">
                <label htmlFor={`xGridSwitch-${config.id}`} className="flex">
                    <span>{t.WALLBOARD_WIDGET_TIME_SERIES.show_x_grid()}</span>
                    <input className="ml-[5px] self-center" checked={xGridVisible} id={`xGridSwitch-${config.id}`} type="checkbox" onChange={toggleXGrid} />
                </label>
                <label htmlFor={`yGridSwitch-${config.id}`} className="flex">
                    <span>{t.WALLBOARD_WIDGET_TIME_SERIES.show_y_grid()}</span>
                    <input className="ml-[5px] self-center" checked={yGridVisible} id={`yGridSwitch-${config.id}`} type="checkbox" onChange={toggleYGrid} />
                </label>
            </div>
        </WithNoData>
    )
}

