import {useCallback} from 'react'
import {useSetAtom} from 'jotai'
import {noop} from 'lodash-es'

import type {
    SupervisorWidget,
    SupervisorWidgetAgentGridSubscriptionData,
    SupervisorWidgetConfigGrid,
    SupervisorWidgetServicesGridSubscriptionData,
} from '@bpinc/lib-widgets-urls'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {updatingWidgetDataAtom} from '../../atoms'
import {AgentGridModal} from '../../settings-modals/AgentGridModal'
import {
    EmptyWidgetSettingsModal,
    useAgendaWidget,
    useBreakTimeWidget,
    useDispositionsWidget,
    useMyStatsWidget,
    useSatisfactionWidget,
    useSingleOrGaugeWidget,
    useSummaryWidget,
} from '../../settings-modals/EmptyWidgetSettingsModal'
import {ImageSettingsModal} from '../../settings-modals/ImageSettingsModal'
import {NewsSettingsModal} from '../../settings-modals/NewsSettingsModal'
import {ServiceGridModal} from '../../settings-modals/ServiceGridModal'
import {TextSettingsModal} from '../../settings-modals/TextSettingsModal'
import {TimeSeriesModal} from '../../settings-modals/TimeSeriesModal'


type ConfigAgentGrid = SupervisorWidgetConfigGrid & {
    subscription: SupervisorWidgetAgentGridSubscriptionData
}
type ConfigServiceGrid = SupervisorWidgetConfigGrid & {
    subscription: SupervisorWidgetServicesGridSubscriptionData
}

type Params = {
    updateWidget: (widget: SupervisorWidget) => void
}


export function useOpenWidgetSettings({updateWidget}: Params) {
    const setUpdateWidgetAtom = useSetAtom(updatingWidgetDataAtom)

    const openEmptyWidgetSettingsModal = useModalComponentOpener(EmptyWidgetSettingsModal)

    const openServiceGridModal = useModalComponentOpener(ServiceGridModal)
    const openAgentGridModal = useModalComponentOpener(AgentGridModal)
    const openImageSettingsModal = useModalComponentOpener(ImageSettingsModal)
    const openNewsSettingsModal = useModalComponentOpener(NewsSettingsModal)
    const openTextSettingsModal = useModalComponentOpener(TextSettingsModal)
    const openTimeSeriesModal = useModalComponentOpener(TimeSeriesModal)

    const updateAgendaWidget = useAgendaWidget({onSaved: noop})
    const updateDispositionsWidget = useDispositionsWidget({onSaved: noop})
    const updateSingleOrGaugeWidget = useSingleOrGaugeWidget({onSaved: noop})
    const updateBreakTimeWidget = useBreakTimeWidget({onSaved: noop})
    const updateMyStatsWidget = useMyStatsWidget({onSaved: noop})
    const updateSatisfaction = useSatisfactionWidget({onSaved: noop})
    const updateSummaryWidget = useSummaryWidget({onSaved: noop})


    return useCallback(
        (widget: SupervisorWidget) => {
            setUpdateWidgetAtom({updateWidget, widget})
            switch (widget.type) {
                case 'agenda':
                    updateAgendaWidget(widget)
                    break
                case 'break_time':
                    updateBreakTimeWidget(widget)
                    break
                case 'dispositions':
                    updateDispositionsWidget(widget)
                    break
                case 'gauge':
                case 'single':
                    updateSingleOrGaugeWidget(widget.type, widget)
                    break
                case 'grid':
                    switch (widget.subscription.type) {
                        case 'agent_grid':
                            openAgentGridModal({
                                replacingWidgetId: widget.id,
                                formValues: widget as ConfigAgentGrid,
                            })
                            break
                        case 'service_grid':
                            openServiceGridModal({
                                replacingWidgetId: widget.id,
                                formValues: widget as ConfigServiceGrid,
                            })
                            break
                    }
                    break
                case 'image':
                    openImageSettingsModal({
                        replacingWidgetId: widget.id,
                        formValues: widget,
                    })
                    break
                case 'my_stats':
                    updateMyStatsWidget(widget)
                    break
                case 'news':
                    openNewsSettingsModal({
                        replacingWidgetId: widget.id,
                        formValues: {
                            title: widget.title || '',
                            newsCount: widget.subscription?.req.limit,
                        },
                    })
                    break
                case 'satisfaction':
                    updateSatisfaction(widget)
                    break
                case 'summary':
                    updateSummaryWidget(widget)
                    break
                case 'text':
                    openTextSettingsModal({
                        replacingWidgetId: widget.id,
                        formValues: widget,
                    })
                    break
                case 'time_series':
                    openTimeSeriesModal({
                        replacingWidgetId: widget.id,
                        formValues: widget,
                    })
                    break
                default:
                    openEmptyWidgetSettingsModal({
                        replacingWidgetId: widget.id,
                    })
            }
        },
        [
            setUpdateWidgetAtom,
            updateWidget,
            updateAgendaWidget,
            updateBreakTimeWidget,
            updateDispositionsWidget,
            updateSingleOrGaugeWidget,
            openImageSettingsModal,
            updateMyStatsWidget,
            openNewsSettingsModal,
            updateSatisfaction,
            updateSummaryWidget,
            openTextSettingsModal,
            openTimeSeriesModal,
            openEmptyWidgetSettingsModal,
            openAgentGridModal,
            openServiceGridModal,
        ],
    )
}
