import {useQuery} from '@tanstack/react-query'

import {MS_IN_MINUTE} from '@bpinc/lib-helpers'
import {SupervisorWidgetsApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from './index'


type Params = {
    measurement: string
    statistic: string
    start: string
    end: string
    timeInterval: string
}

export function useTimeSeriesDataQuery(params: Params) {
    return useQuery({
        queryKey: queryKey.time_series(params.statistic, params.timeInterval),
        queryFn: () => SupervisorWidgetsApiServices.getTimeSeriesData(params),
        refetchInterval: MS_IN_MINUTE,
    })
}
