import {useMutation, useQueryClient} from '@tanstack/react-query'
import produce from 'immer'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {WallboardInfo} from '@bpinc/lib-widgets-urls'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {useGoFromRemovedWallboard} from '../layout/context/RouterContext'
import {queryKey} from '../queries'


export function useDeleteWallboardMutation() {
    const queryClient = useQueryClient()
    const goFromRemovedWallboard = useGoFromRemovedWallboard()
    return useMutation({
        mutationFn: WallboardApiServices.deleteWallboard,
        onMutate({wallboardId}) {
            queryClient.removeQueries({queryKey: queryKey.wallboard(wallboardId), exact: true})

            queryClient.setQueryData<WallboardInfo[]>(queryKey.wallboards(), produce(draft => {
                assertDefined(draft)

                const wallboardIndex = draft?.findIndex(w => w.id === wallboardId)

                if (wallboardIndex === -1) {
                    return
                }

                draft.splice(wallboardIndex, 1)
                goFromRemovedWallboard(wallboardId)
            }))
        },
    })
}
