import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'

import {CheckboxField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorAgentStats} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'


type Limit = {
    subteam?: boolean
    logged_in_only?: boolean
}

interface AgentStatesModalValues {
    agentStats: SupervisorAgentStats
    limit: Limit
}

export interface AgentStatesModalParams extends ModalBase {
    type: 'agent_states'
    values: AgentStatesModalValues
    onApply: (values: AgentStatesModalValues) => void
}

type AgentStatesModalProps = CommonModalProps & {
    values: AgentStatesModalValues
    onApply: (values: AgentStatesModalValues) => void
}

export const AgentStatesModal: React.FC<AgentStatesModalProps> = ({values, onApply, onClose, ...commonModalProps}) => {
    const {t} = useLocalizationState()
    const {
        control,
        handleSubmit,
    } = useForm<AgentStatesModalValues>({
        defaultValues: values,
    })

    const save = useMemo(() => handleSubmit(async formData => {
        onApply(formData)
        onClose?.()
    }), [handleSubmit, onApply, onClose])

    return (
        <Modal
            onClose={onClose}
            size="tiny"
            className="max-w-full"
            {...commonModalProps}
        >
            <Modal.Header>
                {t.WALLBOARD_STATES_MODAL.title()}
            </Modal.Header>
            <Modal.Content>
                <h4>
                    {t.WALLBOARD_STATES_MODAL.states_header()}
                </h4>
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.state_busy()}
                    name="agentStats.busy"
                    control={control}
                />
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.state_ready()}
                    name="agentStats.ready"
                    control={control}
                />
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.state_after_call_work()}
                    name="agentStats.after_call_work"
                    control={control}
                />
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.state_not_ready()}
                    name="agentStats.not_ready"
                    control={control}
                />
                <h4>
                    {t.WALLBOARD_STATES_MODAL.limits_header()}
                </h4>
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.limit_my_subteam()}
                    name="limit.subteam"
                    control={control}
                />
                <CheckboxField
                    label={t.WALLBOARD_STATES_MODAL.limit_logged_in()}
                    name="limit.logged_in_only"
                    control={control}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    primary
                    onClick={save}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
