import {useCallback} from 'react'
import {useQueryClient} from '@tanstack/react-query'

import {assertDefined} from '@bpinc/lib-types-helpers'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey, useWallboardsQuery} from '../../queries'
import type {WidgetToCopy} from '../../types'


export function useGetWidgetsToCopy() {
    const {data: allWallboards} = useWallboardsQuery()
    const queryClient = useQueryClient()

    return useCallback(async () => {
        assertDefined(allWallboards)
        const allWallboardsDetailsQueries = allWallboards?.map(w => {
            return queryClient.fetchQuery({
                queryKey: queryKey.wallboard(w.id),
                queryFn: () => WallboardApiServices.getWallboardDetails({wallboardId: w.id}),
            })
        })
        const allWallboardsDetailsQueriesData = await Promise.allSettled(allWallboardsDetailsQueries)
        return allWallboardsDetailsQueriesData.reduce<WidgetToCopy[]>((acc, result) => {
            if (result.status !== 'fulfilled') {
                return acc
            }
            const wallboardDetails = result.value

            const wallboardItemOptions = wallboardDetails.config.widgets
                .filter(w => w.config.type !== 'empty')
                .map<WidgetToCopy>(widgetItem => ({
                    config: widgetItem.config,
                    title: `(${wallboardDetails.name}) ${widgetItem.config.title}`,
                    wallboardId: wallboardDetails.id,
                }))

            return [
                ...acc,
                ...wallboardItemOptions,
            ]
        }, [])
    }, [allWallboards, queryClient])
}

