import React, {useCallback, useState} from 'react'
import {useFieldArray} from 'react-hook-form'
import type {UniqueIdentifier} from '@dnd-kit/core'
import {DndContext} from '@dnd-kit/core'
import {SortableContext, verticalListSortingStrategy} from '@dnd-kit/sortable'

import {SmIcon} from '@bpinc/lib-external-ui-elements'
import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {ColumnField} from './ColumnField'
import {ColumnFieldRow} from './ColumnFieldRow'
import type {ColumnsFiledType} from './utils'
import {createEmptyColumn} from './utils'


type ColumnsFieldProps = {
    type: 'agent_grid' | 'service_grid'
}

export const ColumnsField = <TValues, >({
    control,
    setValue,
    type,
}: FieldControlProps<TValues, ColumnsFiledType, 'control' | 'setValue'> & ColumnsFieldProps) => {
    const {t} = useLocalizationState()
    const {fields, append, remove, move} = useFieldArray({
        control,
        name: 'columns',
    })
    const onAddColumn = useCallback(() => {
        append(createEmptyColumn(fields.length, type))
    }, [append, fields.length, type])

    const [activeId, setActiveId] = useState<UniqueIdentifier | null>(null)
    const getIndex = (id: UniqueIdentifier) => fields.findIndex(field => field.id === id)
    const activeIndex = activeId ? getIndex(activeId) : -1

    return (
        <div>
            <ColumnFieldRow>
                <p className="font-bold">
                    {t.GLOBAL.statistic()}
                </p>
                <p className="font-bold">
                    {t.GLOBAL.sort()}
                </p>
                <p className="font-bold">
                    {t.GLOBAL.title()}
                </p>
            </ColumnFieldRow>
            <div className="mb-1 flex flex-col">
                <DndContext
                    onDragStart={({active}) => {
                        if (!active) {
                            return
                        }

                        setActiveId(active.id)
                    }}
                    onDragEnd={({over}) => {
                        setActiveId(null)

                        if (over) {
                            const overIndex = getIndex(over.id)
                            if (activeIndex !== overIndex) {
                                move(activeIndex, overIndex)
                            }
                        }
                    }}
                    onDragCancel={() => setActiveId(null)}
                >
                    <SortableContext items={fields} strategy={verticalListSortingStrategy}>
                        {fields?.map((field, index) => {
                            return (
                                <ColumnField
                                    type={type}
                                    key={field.id}
                                    id={field.id}
                                    control={control}
                                    setValue={setValue}
                                    index={index}
                                    remove={remove}
                                />
                            )
                        })}
                    </SortableContext>
                </DndContext>
            </div>
            <SemanticButton onClick={onAddColumn} basic icon enabledInOffline>
                <SmIcon name="plus" />
                {t.WALLBOARD_WIDGETS_GRIDS.add_column()}
            </SemanticButton>
        </div>
    )
}
