import React, {useEffect, useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'
import {cloneDeep, pick} from 'lodash-es'

import {CheckboxField, TextField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigGrid, SupervisorWidgetServicesGridSubscriptionData} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {useServicesQuery} from '../../queries'
import {assertCorrectWidget} from '../helpers'
import type {ColumnsFiledType} from '../shared/GridFormsComponents/ColumnsField'
import {ColumnsField, createEmptyColumn} from '../shared/GridFormsComponents/ColumnsField'
import type {FormValues} from '../types'
import {ServicesSelect} from './ServicesSelect'


type ServiceGridModalSettingsFormValues = {
    title: string
    serviceIds: string[]
    columnTitleEnabled: boolean
    maxItemsToShow: number
} & ColumnsFiledType

type ConfigServiceGrid = SupervisorWidgetConfigGrid & {subscription: SupervisorWidgetServicesGridSubscriptionData}

export function assertFormValuesValidForConfigServiceGrid(
    fv: FormValues,
): asserts fv is ConfigServiceGrid {
    if (!(
        'title' in fv &&
        'columtitle' in fv &&
        'limit' in fv &&
        'service_ids' in fv &&
        'columns' in fv &&
        'subscription' in fv &&
        typeof fv.subscription === 'object' &&
        fv.subscription !== null &&
        'type' in fv.subscription &&
        fv.subscription.type === 'service_grid'
    )) {
        throw new Error('Form values is not valid for config service grid')
    }
}

export interface ServiceGridWidgetSettingsModalParams extends ModalBase {
    type: 'service_grid_settings'
    replacingWidgetId: string
    formValues?: ConfigServiceGrid
    onSaved?: () => void
}

type ServiceGridModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: ConfigServiceGrid
    onSaved?: () => void
}

const convertToForm = (config: ConfigServiceGrid): Partial<ServiceGridModalSettingsFormValues> => {
    const newColumns = config?.subscription?.req?.columns.map((col, index) => {
        const existingOrder = config?.subscription?.req?.order?.find(({by}) => {
            return Number(by) === index + 1
        })
        return {
            ...col,
            dir: (existingOrder?.dir as 'DESC' | 'ASC') || 'EMPT',
            order: index + 1,
        }
    })
    return {
        title: config?.title,
        columnTitleEnabled: config?.columtitle,
        serviceIds: cloneDeep(config?.subscription?.req?.service_ids),
        maxItemsToShow: cloneDeep(config?.subscription?.req?.limit),
        columns: newColumns,
    }

}

type FormWidgetConfig = Pick<SupervisorWidgetConfigGrid, 'title' | 'columtitle' | 'subscription'>

const convertToBackend = (formValues: ServiceGridModalSettingsFormValues, id: string): FormWidgetConfig => {
    const newOrder = formValues.columns.reduce<{ by: string; dir: 'ASC' | 'DESC' }[]>((acc, column, index) => {
        if (column.dir && column.dir !== 'EMPT') {
            return [...acc, {by: String(index + 1), dir: column.dir}]
        }
        return acc
    }, [])
    return {
        title: formValues.title || '',
        columtitle: formValues.columnTitleEnabled,
        subscription: {
            type: 'service_grid',
            req: {
                id,
                service_ids: formValues.serviceIds,
                limit: formValues.maxItemsToShow,
                columns: formValues.columns.map((col, ind) => ({...col, id: String(ind + 1)})),
                order: newOrder,
            },
        },
    }
}

const DEFAULT_COLUMN_MAX = 6

export const ServiceGridModal: React.FC<ServiceGridModalProps> = ({
    onClose,
    replacingWidgetId,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()

    const formValuesParsed = useMemo(() => {
        if (formValues) {
            return convertToForm(formValues)
        }

    }, [formValues])

    const {
        control,
        handleSubmit,
        setValue,
    } = useForm<ServiceGridModalSettingsFormValues>({
        defaultValues: {
            title: formValuesParsed?.title || t.WALLBOARD_WIDGET_EMPTY.service_grid(),
            serviceIds: formValuesParsed?.serviceIds || [],
            columnTitleEnabled: formValuesParsed?.columnTitleEnabled !== false,
            maxItemsToShow: formValuesParsed?.maxItemsToShow || DEFAULT_COLUMN_MAX,
            columns: formValuesParsed?.columns || [createEmptyColumn(0, 'service_grid')],
        },
    })

    const {data: services} = useServicesQuery()

    useEffect(() => {
        const servicesId = (services || []).map(({id}) => id)
        setValue('serviceIds', servicesId)
    }, [control, services, setValue])

    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            type: 'grid',
            ...convertToBackend(formData, widget.id),
            ...pick(
                widget,
                'id',
                'x',
                'y',
                'width',
                'height',
                'show_settings',
            ),
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                {t.WALLBOARD_WIDGETS_GRIDS.service_grid()}
            </Modal.Header>
            <Modal.Content>
                <div className="mb-4 flex gap-4">
                    <TextField
                        fluid
                        label={t.GLOBAL.title()}
                        name="title"
                        placeholder={t.GLOBAL.title()}
                        className="grow [&>label]:font-bold"
                        control={control}
                    />
                    <TextField
                        fluid
                        label={t.WALLBOARD_WIDGETS_GRIDS.max_to_show()}
                        name="maxItemsToShow"
                        type="number"
                        className="shrink-0 [&>label]:font-bold"
                        placeholder={t.WALLBOARD_WIDGETS_GRIDS.max_to_show()}
                        control={control}
                    />
                    <CheckboxField
                        label={t.WALLBOARD_WIDGETS_GRIDS.column_headings()}
                        name="columnTitleEnabled"
                        toggle
                        className="mt-4 shrink-0 self-center"
                        control={control}
                    />
                </div>
                <ServicesSelect control={control} setValue={setValue} />
                <ColumnsField control={control} setValue={setValue} type="service_grid" />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()} enabledInOffline>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
