import {useMutation, useQueryClient} from '@tanstack/react-query'

import type {SupervisorWidget, WallboardConfig, WallboardStyle} from '@bpinc/lib-widgets-urls'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from '../queries'


type MutationArguments = {
    wallboardId: string
    updatedWallboardWidgets: SupervisorWidget[]
    updatedStyle?: WallboardStyle
}

const toServerWidgets = (widgets: SupervisorWidget[]) => widgets.map(
    widgetConfigItem => ({
        config: widgetConfigItem,
    }),
)

export function useUpdateWallboardWidgetsMutation() {
    const queryClient = useQueryClient()
    return useMutation({
        mutationFn: async ({
            wallboardId,
            updatedWallboardWidgets,
            updatedStyle,
        }: MutationArguments) => {
            return WallboardApiServices.updateWallboardConfig({
                wallboardId,
                requestBody: {
                    config: {
                        widgets: toServerWidgets(updatedWallboardWidgets),
                        style: updatedStyle,
                    },
                },
            })
        },
        onMutate({
            wallboardId,
            updatedWallboardWidgets,
            updatedStyle,
        }) {
            queryClient.setQueryData<WallboardConfig | undefined>(
                queryKey.wallboard(wallboardId),
                current => {
                    if (!current) {
                        return current
                    }
                    return {
                        ...current,
                        config: {
                            widgets: toServerWidgets(updatedWallboardWidgets),
                            style: updatedStyle,
                        },
                    }
                },
            )
        },
        onSuccess(_, {wallboardId}) {
            queryClient.invalidateQueries({queryKey: queryKey.wallboard(wallboardId)})
        },
    })
}
