import {useMemo} from 'react'

import type {AgentGridValuesColumn, SupervisorWidgetConfigSatisfaction} from '@bpinc/lib-widgets-urls'

import {solidGaugeChartOptions} from '../../charts'
import {formatStatValue} from '../../stats/helpers'
import {useGetStatsConfig} from '../../stats/useGetStatsConfig'


type StatName = 'agent_fcr' | 'agent_nps' | 'agent_csat'

interface StatOptions {
    minValue: number
    maxValue: number
    getFormattedMinMax: (value: number) => string
}
const satisfactionOption: Record<StatName, StatOptions> = {
    agent_fcr: {
        minValue: 0,
        maxValue: 100,
        getFormattedMinMax: (value: number) => `${value}%`,
    },
    agent_nps: {
        minValue: -100,
        maxValue: 100,
        getFormattedMinMax: (value: number) => `${value}%`,
    },
    agent_csat: {
        minValue: 0,
        maxValue: 10,
        getFormattedMinMax: (value: number) => `${value}`,
    },
}

export const useSatisfactionOption = (
    statName: StatName,
    config: SupervisorWidgetConfigSatisfaction,
    grids: AgentGridValuesColumn[] | undefined,
    title: string,
) => {
    const getStatsConfig = useGetStatsConfig()
    const {columns: configuredColumns, id: reqId} = config.subscription.req
    const targetCol = configuredColumns?.find(c => c.statName === statName)?.id
    const targetDataGrid = grids?.find(grid => grid?.id === reqId)
    const value = Number(targetDataGrid?.values?.[0][Number(targetCol)] ?? 0)

    return useMemo(() => {
        return solidGaugeChartOptions({
            value,
            maxValue: satisfactionOption[statName].maxValue,
            minValue: satisfactionOption[statName].minValue,
            getFormattedMinMax: satisfactionOption[statName].getFormattedMinMax,
            getFormattedValue: (rawValue: string | number | undefined) => {
                return formatStatValue(rawValue, getStatsConfig('statsAgent', statName)?.format)
            },
            title,
        })
    }, [value, statName, title, getStatsConfig])
}
