import {useEffect} from 'react'

import {useSelectedWallboardQuery} from '../queries'


export function useDocumentTitleEffect() {
    const {data: selectedWallboard} = useSelectedWallboardQuery()

    useEffect(() => {
        if (selectedWallboard?.name) {
            document.title = selectedWallboard?.name
        }
    }, [selectedWallboard?.name])
}
