import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'

import {CheckboxField, TextField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useWallboardsQuery} from '../queries'
import type {WallboardRotationType} from '../types'
import {useWallboardRotation} from '../ui-context/UIContext'


type WallboardRotationFormData = Pick<WallboardRotationType, 'enabled' | 'period'> & {
    wallboards: Record<string, boolean>
}

export const WallboardRotationModal: React.FC<CommonModalProps> = ({
    onClose,
    ...props
}) => {
    const {t} = useLocalizationState()

    const rotation = useWallboardRotation()
    const {data: wallboards} = useWallboardsQuery()

    const {control, handleSubmit} = useForm<WallboardRotationFormData>({
        defaultValues: {
            enabled: rotation.value.enabled,
            period: rotation.value.period,
            wallboards: (wallboards || []).reduce<Record<string, boolean>>((res, {id}) => {
                res[id] = rotation.value.wallboards.includes(id)
                return res
            }, {}),
        },
    })

    const onSave = useMemo(() => handleSubmit(async formData => {
        rotation.setValue({
            enabled: formData.enabled,
            period: formData.period,
            wallboards: Object.entries(formData.wallboards)
                .filter(([_, value]) => value)
                .map(([id]) => id),
        })

        onClose()
    }), [handleSubmit, onClose, rotation])

    return (
        <Modal
            {...props}
            onClose={onClose}
            size="mini"
            className="max-w-full"
            aria-labelledby="rotateWallboardsModalHeader"
        >
            <Modal.Header role="heading" id="rotateWallboardsModalHeader">
                {t.WALLBOARD.rotation_setting()}
            </Modal.Header>
            <Modal.Content>
                <div className="grid grid-cols-6 items-center gap-4">
                    <label className="col-span-2">{t.WALLBOARD.rotation_enabled()}</label>
                    <CheckboxField control={control} name="enabled" className="col-span-4 mb-0" />
                    <label className="col-span-2 self-start">{t.WALLBOARD.rotation_wallboards()}</label>
                    <div className="col-span-4 self-end pt-5">
                        {
                            wallboards?.map(({id, name}) => (
                                <CheckboxField
                                    key={id}
                                    control={control}
                                    name={`wallboards.${id}`}
                                    label={name}
                                    className="mb-2"
                                />
                            ))
                        }
                    </div>
                    <label className="col-span-2">{t.WALLBOARD.rotation_interval()}</label>
                    <TextField control={control} name="period" className="col-span-4" type="number" />
                </div>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()} enabledInOffline>
                    {t.GLOBAL.cancel()}
                </SemanticButton>
                <SemanticButton onClick={onSave} primary disabled={!wallboards?.length}>
                    {t.GLOBAL.ok()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
