import type {ChangeEventHandler} from 'react'
import React, {useCallback} from 'react'
import type {Control} from 'react-hook-form'
import {useController} from 'react-hook-form'

import {getFileDataUrl} from './getFileDataUrl'
import type {ImageWidgetSettingsModalFormValues} from './ImageSettingsModal'


interface SelectImageProps {
    control: Control<ImageWidgetSettingsModalFormValues>
}

export const SelectImage: React.FC<SelectImageProps> = ({
    control,
}) => {
    const {
        field: {
            value: imageName,
            onChange: onImageNameChange,
        },
    } = useController({
        name: 'imageName',
        control,
    })

    const {
        field: {onChange: onImageUrlChange},
    } = useController({
        name: 'imageUrl',
        control,
    })

    const handleUploadFiles: ChangeEventHandler<HTMLInputElement> = useCallback(async event => {
        const file = event.target.files?.[0]

        if (!file) {
            return
        }

        const newImageUrl = await getFileDataUrl(file)
        const newImageName = file.name

        onImageUrlChange(newImageUrl)
        onImageNameChange(newImageName)
    }, [onImageNameChange, onImageUrlChange])

    return (
        <div className="mb-2.5 flex gap-2.5">
            {imageName}

            <input
                type="file"
                onChange={handleUploadFiles}
            />
        </div>
    )
}
