import {useQuery} from '@tanstack/react-query'

import {MS_IN_SECOND} from '@bpinc/lib-helpers'
import {SupervisorWidgetsApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from './index'


export function useAgendaQuery(userId: string) {
    return useQuery({
        queryKey: queryKey.agenda(),
        queryFn: () => SupervisorWidgetsApiServices.getAgenda({requestBody: [userId]}),
        refetchInterval: 5 * MS_IN_SECOND,
    })
}
