import React, {useEffect, useMemo} from 'react'
import {useWatch} from 'react-hook-form'

import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SelectField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {StatsMessagesDeclarationTypeMapper} from '../../stats/types'
import {useStatsOptions} from '../../stats/useStatsOptions'
import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'


export type Statistics = keyof (StatsMessagesDeclarationTypeMapper<'timeSeriesTeams'> & StatsMessagesDeclarationTypeMapper<'timeSeriesServices'>)

export const TEAMS_STATISTICS_DEFAULT = 'agents_busy'
const SERVICES_STATISTICS_DEFAULT = 'in_calls_waiting'

export const StatisticsField = <TValues, >({
    control,
    setValue,
}: FieldControlProps<TValues, { statistics: Statistics; seriesType: 'teams' | 'services' }, 'control' | 'setValue'>) => {
    const {t} = useLocalizationState()
    const optionsTeams = useStatsOptions('timeSeriesTeams')
    const optionsTeamsKeys = useMemo(() => {
        return optionsTeams.reduce<{ [key: string]: 1 }>((acc, elem) => {
            acc[elem.value] = 1
            return acc
        }, {})
    }, [optionsTeams])

    const optionsServices = useStatsOptions('timeSeriesServices')

    const optionsServicesKeys = useMemo(() => {
        return optionsServices.reduce<{ [key: string]: 1 }>((acc, elem) => {
            acc[elem.value] = 1
            return acc
        }, {})
    }, [optionsServices])

    const seriesTypeValue = useWatch({name: 'seriesType', control})
    const statisticValue = useWatch({name: 'statistics', control})

    useEffect(() => {
        if (seriesTypeValue === 'teams' && statisticValue && !optionsTeamsKeys[statisticValue]) {
            setValue('statistics', TEAMS_STATISTICS_DEFAULT)
        }

        if (seriesTypeValue === 'services' && statisticValue && !optionsServicesKeys[statisticValue]) {
            setValue('statistics', SERVICES_STATISTICS_DEFAULT)
        }

    }, [seriesTypeValue, statisticValue, optionsTeamsKeys, optionsServicesKeys, setValue])

    return (
        <GridRow>
            <FormLabel text={t.WALLBOARD_WIDGET_TIME_SERIES.series_type()} />
            <SelectField search options={seriesTypeValue === 'teams' ? optionsTeams : optionsServices} fluid name="statistics" control={control} />
        </GridRow>
    )
}
