import React from 'react'
import {Route, Routes} from 'react-router-dom'

import {useUIState} from '@bpinc/ad-ui-context'
import {tcn} from '@bpinc/lib-tailwind-classnames'

import {WallboardPage} from '../page'


export const App: React.FC = () => {
    const {isTabNavHighlighted} = useUIState()
    return (
        <div
            className={tcn('relative h-full w-full', {
                '[&_*:focus-within]:!outline-none [&_*:focus]:!outline-none': !isTabNavHighlighted,
                '[&_*:focus-within]:!outline-0 [&_*:focus]:!outline-0': !isTabNavHighlighted,
            })}
        >
            <Routes>
                <Route
                    path="/:wallboardId?"
                    element={<WallboardPage />}
                />
            </Routes>
        </div>
    )
}
