import {useQuery} from '@tanstack/react-query'

import {MS_IN_SECOND} from '@bpinc/lib-helpers'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from './query-keys'


export function useWallboardsQuery() {
    return useQuery({
        queryKey: queryKey.wallboards(),
        queryFn() {
            return WallboardApiServices.getWallboards().then(data => data.wallboards)
        },
        gcTime: MS_IN_SECOND * 10,
        staleTime: MS_IN_SECOND * 10,
    })
}
