import {useMutation, useQueryClient} from '@tanstack/react-query'

import type {WallboardConfig} from '@bpinc/lib-widgets-urls'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from '../queries'


type MutationArguments = {
    wallboardId: string
    name: string
}

export function useUpdateWallboardNameMutation() {
    const queryClient = useQueryClient()
    return useMutation({
        mutationFn: async ({
            wallboardId,
            name,
        }: MutationArguments) => WallboardApiServices.updateWallboardName({
            wallboardId,
            requestBody: {
                name,
            },
        }),
        onMutate({
            wallboardId,
            name,
        }) {
            queryClient.setQueryData<WallboardConfig | undefined>(
                queryKey.wallboard(wallboardId),
                current => {
                    if (!current) {
                        return current
                    }

                    return {
                        ...current,
                        name,
                    }
                },
            )
        },
    })
}
