import {cloneDeep, uniq} from 'lodash-es'
import {createSelector} from 'reselect'

import type {SupervisorWidget, SupervisorWidgetsSubscriptionRequest, WallboardConfig} from '@bpinc/lib-widgets-urls'


const getWallboardConfig = (wallboard: Pick<WallboardConfig, 'config'> | undefined) => wallboard?.config

export const getWallboardWidgets = createSelector(
    getWallboardConfig,
    (wallboardConfig: WallboardConfig['config'] | undefined) =>
        wallboardConfig?.widgets.map(widget => widget.config) || [],
)

export const getWallboardConfigFontScale = createSelector(
    getWallboardConfig,
    (wallboardConfig: WallboardConfig['config'] | undefined) => wallboardConfig?.style?.fontScaleValue || 1,
)
export const getWidgetsSubscriptionData = createSelector(
    (widgets: SupervisorWidget[]) => widgets,
    widgets => widgets.reduce<Record<string, SupervisorWidgetsSubscriptionRequest>>((result, widget) => {
        const subscription = createSubscriptionRequest(widget)
        if (subscription) {
            result[widget.id] = subscription
        }
        return result
    }, {}),
)

function createSubscriptionRequest(config: SupervisorWidget) {
    if (!('subscription' in config) || !config.subscription || !config.subscription.type) {
        return
    }

    const widgetSubscription: SupervisorWidgetsSubscriptionRequest = {}

    switch (config.subscription.type) {
        case 'stats_total': {
            widgetSubscription.stats_totals = uniq(config.subscription.req)
            widgetSubscription.stats_totals_service_ids = config.subscription.service_ids
            widgetSubscription.stats_totals_dispositions = config.subscription.dispositions
            break
        }
        case 'agent_grid':
        case 'break_time':
        case 'dispositions':
        case 'my_stats':
        case 'satisfaction':
        case 'summary': {
            widgetSubscription.agent_grids = []

            const tempAgent = cloneDeep(config.subscription.req)
            tempAgent.limit = 1000

            let totalContactsIndex = -1
            tempAgent.columns.forEach((column, i) => {
                removeRedundantData(column)

                if (column.statName == 'name') {
                    column.statName = 'first_last_name'
                }
                if (column.statName == 'total_contacts') {
                    column.statName = 'in_calls_handled_per_day'
                    totalContactsIndex = i
                }
            })

            if (config.type === 'grid') {
                tempAgent.my_subteam_only = config.limit?.subteam
                tempAgent.logged_in_agents_only = config.limit?.logged_in_only
            }
            if (totalContactsIndex != -1) {
                addColumn(
                    tempAgent.columns,
                    totalContactsIndex + 1,
                    {
                        id: (totalContactsIndex + 2).toString(),
                        statName: 'out_calls_handled_per_day',
                    },
                )
            }

            addColumn(
                tempAgent.columns,
                tempAgent.columns.length - 1,
                {
                    id: (tempAgent.columns.length + 1).toString(),
                    statName: 'acd_state',
                },
            )

            widgetSubscription.agent_grids.push(tempAgent)

            if (config.subscription.type === 'my_stats') {
                widgetSubscription['teams_stats'] = [
                    'team_success_ratio_per_day',
                    'in_calls_handled_per_day',
                    'out_calls_handled_per_day',
                    'calls_average_handling_time_per_day',
                ]
            }

            break
        }
        case 'service_grid': {
            widgetSubscription.service_grids = []

            const tempService = cloneDeep(config.subscription.req)
            tempService.limit = 1000

            let indexCallsHandledPerDay = -1
            tempService.columns.forEach(column => {
                removeRedundantData(column)

                if (column.statName === 'calls_handled_per_day') {
                    indexCallsHandledPerDay = parseInt(column.id || '0')
                    column.statName = 'in_calls_handled_per_day'
                }
            })

            if (indexCallsHandledPerDay > -1) {
                addColumn(
                    tempService.columns,
                    tempService.columns.length - 1,
                    {
                        id: (tempService.columns.length + 1).toString(),
                        statName: 'out_calls_handled_per_day',
                    },
                )
            }

            widgetSubscription.service_grids.push(tempService)
            break
        }
        case 'chat_messages': {
            widgetSubscription.chat_messages = config.subscription.req
            break
        }
    }
    return widgetSubscription
}

function addColumn<T>(columns: T[], index: number, column: T) {
    columns.splice(index + 1, 0, column)
}

function removeRedundantData<T extends object>(column: T) {
    if ('dir' in column) {
        delete column.dir
    }

    if ('coltitle' in column) {
        delete column.coltitle
    }

    if ('order' in column) {
        delete column.order
    }
}
