import React, {useMemo} from 'react'

import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SelectField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'


export const SeriesTypeField = <TValues, >({control}: FieldControlProps<TValues, { seriesType: 'teams' | 'services' }>) => {
    const {t} = useLocalizationState()
    const options = useMemo(() => [
        {
            value: 'services',
            text: t.WALLBOARD_WIDGET_TIME_SERIES.services(),
        },
        {
            value: 'teams',
            text: t.WALLBOARD_WIDGET_TIME_SERIES.teams(),
        },
    ], [t.WALLBOARD_WIDGET_TIME_SERIES])
    return (
        <GridRow>
            <FormLabel text={t.WALLBOARD_WIDGET_TIME_SERIES.series_type()} />
            <SelectField search options={options} fluid name="seriesType" control={control} />
        </GridRow>
    )
}
