import type {
    AgentStatsType,
    ServiceStatsType,
    TimeSeriesStatsServices,
    TimeSeriesStatsTeams,
    TotalStatsType,
} from '@bpinc/lib-widgets-urls'
import type {MessagesDeclaration} from '@bpinc/state-localization'


type CustomUserStatType = 'handled' | 'averageHandlingTime' | 'successfulInteraction'
type AllStatsType = TotalStatsType | AgentStatsType | ServiceStatsType | CustomUserStatType
export type StatsFormatType = 'duration' | 'percent'

export type StatsConfigType = {
    statsSingle: StatsType<TotalStatsType, MessagesDeclaration['WALLBOARD_STATS_SINGLE']>
    statsAgent: StatsType<AgentStatsType, MessagesDeclaration['WALLBOARD_STATS_AGENT']>
    statsService: StatsType<ServiceStatsType, MessagesDeclaration['WALLBOARD_STATS_SERVICE']>
    custom: StatsType<CustomUserStatType, MessagesDeclaration['WALLBOARD_STATS_CUSTOM']>
    timeSeriesServices: StatsType<TimeSeriesStatsServices, MessagesDeclaration['WALLBOARD_STATS_TIME_SERIES_SERVICES']>
    timeSeriesTeams: StatsType<TimeSeriesStatsTeams, MessagesDeclaration['WALLBOARD_STATS_TIME_SERIES_TEAMS']>
}

export type StatsConfigTypeKeys = keyof StatsConfigType


type StatsType<StatKind extends AllStatsType, LocalizationPart extends MessagesDeclaration[keyof MessagesDeclaration]> =
    Record<StatKind, StatsDataType<LocalizationPart >>

export type StatsDataType<LocalizationPart extends MessagesDeclaration[keyof MessagesDeclaration]> = {
    localizationKey: keyof LocalizationPart
    format?: StatsFormatType
    align?: 'left'
    alertInverted?: boolean
}

export type StatsMessagesDeclarationTypeMapper<StatKey extends StatsConfigTypeKeys> =
    StatKey extends 'statsSingle' ? MessagesDeclaration['WALLBOARD_STATS_SINGLE'] :
    StatKey extends 'statsAgent' ? MessagesDeclaration['WALLBOARD_STATS_AGENT'] :
    StatKey extends 'statsService' ? MessagesDeclaration['WALLBOARD_STATS_SERVICE'] :
    StatKey extends 'timeSeriesTeams' ? MessagesDeclaration['WALLBOARD_STATS_TIME_SERIES_TEAMS'] :
    StatKey extends 'timeSeriesServices' ? MessagesDeclaration['WALLBOARD_STATS_TIME_SERIES_SERVICES'] :
    StatKey extends 'custom' ? MessagesDeclaration['WALLBOARD_STATS_CUSTOM'] :
    never

export const STAT_LOCALIZATION_MAP: Record<
    StatsConfigTypeKeys,
    keyof Pick<MessagesDeclaration,
      'WALLBOARD_STATS_SINGLE' |
      'WALLBOARD_STATS_AGENT' |
      'WALLBOARD_STATS_SERVICE' |
      'WALLBOARD_STATS_CUSTOM' |
      'WALLBOARD_STATS_TIME_SERIES_TEAMS' |
      'WALLBOARD_STATS_TIME_SERIES_SERVICES'
    >
> = {
    statsSingle: 'WALLBOARD_STATS_SINGLE',
    statsAgent: 'WALLBOARD_STATS_AGENT',
    statsService: 'WALLBOARD_STATS_SERVICE',
    custom: 'WALLBOARD_STATS_CUSTOM',
    timeSeriesTeams: 'WALLBOARD_STATS_TIME_SERIES_TEAMS',
    timeSeriesServices: 'WALLBOARD_STATS_TIME_SERIES_SERVICES',
} as const

