import React, {useCallback, useEffect, useMemo, useState} from 'react'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {DropdownItemProps, DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import type {WidgetToCopy} from '../../types'
import {useHandleWidgetSelect} from '../EmptyWidgetSettingsModal'
import {useGetWidgetsToCopy} from './useGetWidgetsToCopy'


type CopyWidgetProps = CommonModalProps & {
    replacingWidgetId: string
    onSaved?: () => void
}

export const CopyWidget: React.FC<CopyWidgetProps> = ({
    replacingWidgetId,
    onClose,
    onSaved,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()
    const [selectedWidgetId, setSelectedWidgetId] = useState('')
    const [isLoading, setIsLoading] = useState(true)
    const [widgetsToCopy, setWidgetsToCopy] = useState<WidgetToCopy[]>([])
    const getWidgetsToCopy = useGetWidgetsToCopy()

    useEffect(() => {
        async function updateWidgetsOptions() {
            const result = await getWidgetsToCopy()
            setWidgetsToCopy(result)
            setIsLoading(false)
        }
        updateWidgetsOptions()
    }, [getWidgetsToCopy])

    const options = useMemo(() => {
        return widgetsToCopy.map<DropdownItemProps>(widgetItem => ({
            value: getOptionsValue(widgetItem.wallboardId, widgetItem.config.id),
            text: widgetItem.title,
        }))
    }, [widgetsToCopy])

    const handleSelectWidgetId = useCallback((_evt: React.SyntheticEvent<HTMLElement, Event>, {value}: DropdownProps) => {
        if (typeof value !== 'string') {
            console.error('Invalid widget value', value)
            return
        }

        setSelectedWidgetId(value)
    }, [setSelectedWidgetId])

    const openWidgetOnSaved = useCallback(() => {
        onSaved?.()
        onClose()
    }, [onSaved, onClose])

    const handleOpenWidget = useHandleWidgetSelect({
        onSaved: openWidgetOnSaved,
        replacingWidgetId,
    })

    const save = useCallback(() => {
        const selectedIds = getIdsFromOptionValue(selectedWidgetId)
        const selectedWidget = widgetsToCopy
            .find(({config, wallboardId}) => selectedIds.widgetId === config.id && selectedIds.wallboardId === wallboardId)?.config
        assertDefined(selectedWidget)
        switch (selectedWidget.type) {
            case 'news':
                return handleOpenWidget('news', {
                    title: selectedWidget.title,
                    newsCount: selectedWidget.subscription?.req.limit,
                })
            case 'empty':
                return
            case 'grid':
                return handleOpenWidget(selectedWidget.subscription.type === 'service_grid' ? 'service_grid' : 'agent_grid', selectedWidget)
            default:
                return handleOpenWidget(selectedWidget.type, selectedWidget)
        }
    }, [handleOpenWidget, selectedWidgetId, widgetsToCopy])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                { t.WALLBOARD_WIDGET_EMPTY.copy_from_other() }
            </Modal.Header>
            <Modal.Content>
                <Dropdown
                    selection
                    search
                    options={options}
                    value={selectedWidgetId}
                    onChange={handleSelectWidgetId}
                    loading={isLoading}
                    disabled={isLoading}
                    className="w-full"
                    openOnFocus={false}
                    selectOnBlur={false}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton enabledInOffline onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    disabled={!selectedWidgetId}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}

function getOptionsValue(wallboardId: string, widgetId: string) {
    return `${wallboardId}/${widgetId}`
}

function getIdsFromOptionValue(value: string) {
    const [wallboardId, widgetId] = value.split('/')

    return {
        wallboardId,
        widgetId,
    }
}
