import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {updatingWidgetDataAtom} from '../../../../atoms'
import {createSatisfactionWidgetSubscription} from '../../../helpers'
import {SelectUserModal} from '../../../SelectUserModal'


export const useSatisfactionWidget = ({
    onSaved,
}: {
    onSaved: () => void
}) => {
    const {updateWidget} = useAtomValue(updatingWidgetDataAtom)
    const openSelectUserModal = useModalComponentOpener(SelectUserModal)
    const {t} = useLocalizationState()

    return useCallback((currentWidgetConfig: SupervisorWidget) => {
        openSelectUserModal({
            onSelected: (userId, userName) => {
                updateWidget({
                    ...currentWidgetConfig,
                    type: 'satisfaction',
                    title: t.WALLBOARD_SATISFACTION_WIDGET.name(userName),
                    subscription: createSatisfactionWidgetSubscription(currentWidgetConfig.id, userId),
                    user_id: userId,
                    userName,
                })
            },
            currentWidgetConfig,
        })
        onSaved()
    }, [onSaved, openSelectUserModal, t.WALLBOARD_SATISFACTION_WIDGET, updateWidget])
}
