import React, {useCallback} from 'react'
import {useController} from 'react-hook-form'
import {cloneDeep} from 'lodash-es'

import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorAgentStats} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {AgentStatesModal} from '../AgentStatesModal'
import {TeamsSelectModal} from '../TeamsSelectModal'


export type Limit = {
    subteam?: boolean
    logged_in_only?: boolean
}

type AgentsSelectFieldType = {
    teamIds: string[]
    agentStats?: SupervisorAgentStats
    limit?: Limit
}

export const AgentsSelect = <TValues, >({control, setValue}: FieldControlProps<TValues, AgentsSelectFieldType, 'control' | 'setValue'>) => {
    const {
        field: {
            value: teamIds,
        },
    } = useController({
        name: 'teamIds',
        control,
    })

    const {
        field: {
            value: agentStates,
        },
    } = useController({
        name: 'agentStats',
        control,
    })
    const {
        field: {
            value: limit,
        },
    } = useController({
        name: 'limit',
        control,
    })
    const openTeamsSelectModal = useModalComponentOpener(TeamsSelectModal)
    const openAgentStatesModal = useModalComponentOpener(AgentStatesModal)
    const handleSelectTeams = useCallback(() => {
        openTeamsSelectModal({
            teams: teamIds || [],
            onSelect: updatedTeams => {
                setValue('teamIds', updatedTeams)
            },
        })
    }, [openTeamsSelectModal, setValue, teamIds])

    const handleSelectStates = useCallback(() => {
        if (!agentStates || !limit) {
            return
        }
        openAgentStatesModal({
            values: {
                agentStats: cloneDeep(agentStates),
                limit: cloneDeep(limit),
            },
            onApply: values => {
                setValue('limit', values.limit)
                setValue('agentStats', values.agentStats)
            },
        })
    }, [openAgentStatesModal, agentStates, limit, setValue])

    const {t} = useLocalizationState()
    return (
        <div className="mb-4 flex gap-4">
            <SemanticButton
                basic
                onClick={handleSelectTeams}
                className="mr-0"
                enabledInOffline
            >
                {t.WALLBOARD_WIDGETS_GRIDS.teams()}
            </SemanticButton>
            <SemanticButton
                basic
                onClick={handleSelectStates}
                enabledInOffline
            >
                {t.WALLBOARD_WIDGETS_GRIDS.states()}
            </SemanticButton>
        </div>
    )
}
