import React, {useEffect, useRef, useState} from 'react'
import {BpWallboard} from '@servicepattern/jskit'

import {tcn} from '@bpinc/lib-tailwind-classnames'

import {GRID_SIZE, MIN_CARD_SIZE} from '../../constants'
import {useAddWidget, useFontScale, useUpdateWidget, useWidgets} from '../../editing-context'
import {useWidgetsSubscriptionQuery} from '../../queries'
import type {Size} from '../../types'
import {useEditMode} from '../../ui-context/UIContext'
import {EditWidgetResolver, ViewWidgetResolver} from '../../widgets'
import {AudioAlert} from './AudioAlert'
import {useCreateEmptyWidget} from './useCreateEmptyWidget'
import {useOpenWidgetSettings} from './useOpenWidgetSettings'
import {useRemoveWidget} from './useRemoveWidget'

import styles from './Content.module.less'


export const Content: React.FC = () => {
    const ref = useRef<HTMLElement>(null)
    const editMode = useEditMode()
    const fontScale = useFontScale()

    useEffect(() => {
        ref.current?.style.setProperty('--widget-font-size', fontScale + 'vmin')
    }, [fontScale])

    const widgets = useWidgets()
    const updateWidget = useUpdateWidget()
    const addWidget = useAddWidget()
    const removeWidget = useRemoveWidget()

    const [cellSize, setCellSize] = useState<Size>({width: 0, height: 0})
    const {data: subscriptionData} = useWidgetsSubscriptionQuery(widgets)
    const createEmptyWidget = useCreateEmptyWidget({addWidget, minCardSize: MIN_CARD_SIZE})
    const openSettings = useOpenWidgetSettings({updateWidget})

    return (
        <main
            ref={ref}
            className={tcn(styles.content, 'relative m-3 flex min-w-0 flex-auto flex-col')}
            data-testid="wallboard-content"
        >
            <BpWallboard
                editMode={editMode.value}
                subscriptionData={subscriptionData}
                createEmptyWidget={createEmptyWidget}
                widgets={widgets}
                cellSize={cellSize}
                setCellSize={setCellSize}
                minCardSize={MIN_CARD_SIZE}
                gridSize={GRID_SIZE}
                WidgetResolverEdit={EditWidgetResolver}
                WidgetResolverView={ViewWidgetResolver}
                openSettings={openSettings}
                removeWidget={removeWidget}
                updateWidget={updateWidget}
            />
            <AudioAlert />
        </main>
    )
}
