/* istanbul ignore file */
/* tslint:disable */
/* eslint-disable */
import type { WallboardConfig } from '../models/WallboardConfig';
import type { WallboardConfigType } from '../models/WallboardConfigType';
import type { WallboardInfo } from '../models/WallboardInfo';
import type { WallboardNewConfig } from '../models/WallboardNewConfig';

import type { CancelablePromise } from '../core/CancelablePromise';
import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

export class WallboardApiServices {

    /**
     * @returns any 
     * @throws ApiError
     */
    public static getWallboards(): CancelablePromise<{
wallboards: Array<WallboardInfo>;
}> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/wallboards',
        });
    }

    /**
     * @returns any 
     * @throws ApiError
     */
    public static createWallboard({
requestBody,
}: {
requestBody: WallboardNewConfig,
}): CancelablePromise<{
id: string;
}> {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/statsapi/cfg/wallboards',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @returns WallboardConfig 
     * @throws ApiError
     */
    public static getWallboardDetails({
wallboardId,
}: {
wallboardId: string,
}): CancelablePromise<WallboardConfig> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/wallboards/{wallboardId}',
            path: {
                'wallboardId': wallboardId,
            },
        });
    }

    /**
     * @throws ApiError
     */
    public static deleteWallboard({
wallboardId,
}: {
wallboardId: string,
}): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'DELETE',
            url: '/statsapi/cfg/wallboards/{wallboardId}',
            path: {
                'wallboardId': wallboardId,
            },
        });
    }

    /**
     * @throws ApiError
     */
    public static updateWallboardIsGlobal({
wallboardId,
requestBody,
}: {
wallboardId: string,
requestBody: {
is_global: boolean;
},
}): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'PUT',
            url: '/statsapi/cfg/wallboards/{wallboardId}/is_global',
            path: {
                'wallboardId': wallboardId,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @throws ApiError
     */
    public static updateWallboardConfig({
wallboardId,
requestBody,
}: {
wallboardId: string,
requestBody: {
config: WallboardConfigType;
},
}): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'PUT',
            url: '/statsapi/cfg/wallboards/{wallboardId}/config',
            path: {
                'wallboardId': wallboardId,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * @throws ApiError
     */
    public static updateWallboardName({
wallboardId,
requestBody,
}: {
wallboardId: string,
requestBody: {
name: string;
},
}): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'PUT',
            url: '/statsapi/cfg/wallboards/{wallboardId}/name',
            path: {
                'wallboardId': wallboardId,
            },
            body: requestBody,
            mediaType: 'application/json',
        });
    }

}
