import React, {useCallback, useMemo} from 'react'
import {useController, useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'

import {CheckboxField, RadioFieldRaw, SelectField, TextField} from '@bpinc/lib-forms-ui'
import {truthy} from '@bpinc/lib-types-helpers'
import {LinkLikeButton, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigGauge, SupervisorWidgetConfigSingle, TotalStatsType} from '@bpinc/lib-widgets-urls'
import type {MessagesDeclaration} from '@bpinc/state-localization'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {useServicesQuery} from '../../queries'
import {useGetStatsConfig} from '../../stats/useGetStatsConfig'
import {useStatsOptions} from '../../stats/useStatsOptions'
import type {SupervisorWidgetTypes} from '../../types'
import {assertCorrectWidget} from '../helpers'
import {ServicesSelectModal} from '../ServicesSelectModal'
import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'
import {useHandleCancelWidgetModal} from '../useHandleCancelWidgetModal'


type SingleOrGaugeUpdateType = Omit<SupervisorWidgetConfigGauge | SupervisorWidgetConfigSingle, 'id' | 'x' | 'y' | 'width' | 'height' | 'show_settings'>
type SingleOrGaugeWidgetsTypes = Extract<SupervisorWidgetTypes, 'gauge' | 'single'>

type SingleOrGaugeSettingsModalFormValues = {
    type: SingleOrGaugeWidgetsTypes
    title: string
    value: TotalStatsType
    optValue?: TotalStatsType
    alertThreshold: number
    warningThreshold: number
    maxThreshold: number
    warningSound: boolean
    serviceIds: string[]
    dispositions: string[]
}

type SingleOrGaugeSettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
    widgetType: SingleOrGaugeWidgetsTypes
    onSaved?: () => void
}

export const SingleOrGaugeSettingsModal: React.FC<SingleOrGaugeSettingsModalProps> = ({
    replacingWidgetId,
    onClose,
    onSaved,
    widgetType,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()
    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)
    const openSelectService = useModalComponentOpener(ServicesSelectModal)
    const statOptions = useStatsOptions('statsSingle')
    const getStatsConfig = useGetStatsConfig()
    const {data: services} = useServicesQuery()
    const serviceIds = useMemo(() => services?.map(({id}) => id) || [], [services])


    const defaultValues = useMemo(() => {
        assertCorrectWidget(widget, replacingWidgetId)
        if (widget.type !== 'single' && widget.type !== 'gauge') {
            return getWidgetDefaultFormData(widgetType, t, serviceIds)
        }

        return {
            type: widget.type,
            title: widget.title,
            serviceIds: [...(widget.subscription?.service_ids || [])],
            dispositions: [...(widget.subscription?.dispositions || [])],
            alertThreshold: widget.alertThreshold,
            warningThreshold: widget.warningThreshold,
            warningSound: widget.warning_sound,
            maxThreshold: widget.maxThreshold,
            value: convertValueToStatType(widget.value),
            optValue: widget.optValue ? convertValueToStatType(widget.optValue) : undefined,
        }

        function convertValueToStatType(value: string): TotalStatsType {
            return value.split('|')[0].trim() as TotalStatsType
        }
    }, [widget, replacingWidgetId, widgetType, t, serviceIds])

    const {
        control,
        handleSubmit,
        setValue,
    } = useForm<SingleOrGaugeSettingsModalFormValues>({
        defaultValues,
    })

    const {
        field: typeField,
    } = useController({
        name: 'type',
        control,
        defaultValue: 'gauge',
    })

    const {
        field: serviceIdsField,
    } = useController({
        name: 'serviceIds',
        control,
    })

    const handleCancelWidgetModal = useHandleCancelWidgetModal({onClose, widget})

    const handleSave = useMemo(() => handleSubmit(async (formData: SingleOrGaugeSettingsModalFormValues) => {
        const valueFormat = getStatsConfig('statsSingle', formData.value).format

        const reqData = truthy([formData.value, formData.optValue])

        const data: SingleOrGaugeUpdateType = {
            type: formData.type,
            title: formData.title,
            value: convertStatToValue(formData.value, valueFormat),
            optValue: formData.optValue
                ? convertStatToValue(formData.optValue, getStatsConfig('statsSingle', formData.optValue).format)
                : undefined,
            warning_sound: formData.warningSound,
            alertThreshold: formData.alertThreshold,
            warningThreshold: formData.warningThreshold,
            maxThreshold: valueFormat == 'percent' ? 100 : formData.maxThreshold,
            subscription: {
                type: 'stats_total',
                req: reqData,
                service_ids: formData.serviceIds,
                dispositions: formData.dispositions,
            },
        }
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            id: widget.id,
            x: widget.x,
            y: widget.y,
            width: widget.width,
            height: widget.height,
            show_settings: widget.show_settings,
            ...data,
        })

        onSaved?.()
        onClose()

        function convertStatToValue(stat: TotalStatsType, format: string | undefined) {
            if (!format) {
                return stat
            }

            return `${stat} | sp_${format}`
        }
    }), [handleSubmit, getStatsConfig, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    const handleSelectServices = useCallback(() => {
        openSelectService({
            services: serviceIdsField.value,
            onSelect: updatedServices => {
                setValue('serviceIds', updatedServices)
            },
        })
    }, [openSelectService, serviceIdsField.value, setValue])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                { typeField.value === 'gauge' ? t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_gauge() : t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_single() }
            </Modal.Header>
            <Modal.Content>
                <GridRow>
                    <FormLabel text={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.title()} />
                    <TextField defaultValue={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_gauge()} name="title" control={control} fluid />
                    <FormLabel text={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.mode()} />
                    <div>
                        <RadioFieldRaw
                            label={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_gauge()}
                            field={typeField}
                            radioValue="gauge"
                        />
                        <RadioFieldRaw
                            label={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_single()}
                            field={typeField}
                            radioValue="single"
                        />
                    </div>
                    <FormLabel text={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.primary_statistic()} />
                    <div className="grid grid-cols-[1fr_auto] items-center gap-4">
                        <SelectField name="value" control={control} options={statOptions} search fluid />
                        <LinkLikeButton onClick={handleSelectServices}>
                            {t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.services()}
                        </LinkLikeButton>
                    </div>
                    <div className="col-start-2 grid grid-cols-[auto_1fr_auto] items-center gap-4">
                        {typeField.value === 'gauge' && (
                            <>
                                <label>{t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.warning_threshold()}</label>
                                <TextField name="warningThreshold" control={control} fluid type="number" />
                            </>
                        )}
                        <label className="col-start-1">{t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.alert_threshold()}</label>
                        <TextField name="alertThreshold" control={control} fluid type="number" />
                        <CheckboxField name="warningSound" control={control} label={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.enable_warning_sound()} />
                        {typeField.value === 'gauge' && (
                            <>
                                <label>{t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.max_threshold()}</label>
                                <TextField name="maxThreshold" control={control} fluid type="number" />
                            </>
                        )}
                    </div>
                    {typeField.value === 'single' && (
                        <>
                            <FormLabel text={t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.secondary_statistic()} />
                            <SelectField name="optValue" control={control} options={statOptions} search fluid />
                        </>
                    )}
                </GridRow>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton enabledInOffline onClick={handleCancelWidgetModal}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={handleSave}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}

const getWidgetDefaultFormData = (
    type: SingleOrGaugeWidgetsTypes,
    t: MessagesDeclaration,
    serviceIds: string[],
): SingleOrGaugeSettingsModalFormValues => {
    const title = type === 'gauge' ? t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_gauge() : t.WALLBOARD_SINGLE_OR_GAUGE_WIDGET.name_single()

    return {
        type,
        title,
        serviceIds,
        value: 'in_calls_active_on_agents',
        optValue: undefined,
        alertThreshold: 180,
        warningThreshold: 100,
        maxThreshold: 200,
        warningSound: false,
        dispositions: [],
    }
}
