import {useMutation, useQueryClient} from '@tanstack/react-query'
import produce from 'immer'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {WallboardConfig, WallboardInfo} from '@bpinc/lib-widgets-urls'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from '../queries'


interface ToggleGlobal {wallboardId: string; isGlobal: boolean}

export function useToggleGlobalMutation() {
    const queryClient = useQueryClient()
    return useMutation({
        mutationFn({wallboardId, isGlobal}: ToggleGlobal) {
            return WallboardApiServices.updateWallboardIsGlobal({wallboardId, requestBody: {is_global: isGlobal}})
        },
        onMutate({wallboardId, isGlobal}) {
            queryClient.setQueryData<WallboardInfo[]>(queryKey.wallboards(), produce(draft => {
                assertDefined(draft)

                const wallboardIndex = draft?.findIndex(w => w.id === wallboardId)

                draft[wallboardIndex].is_global = isGlobal
            }))
            queryClient.setQueryData<WallboardConfig>(queryKey.wallboard(wallboardId), produce(draft => {
                assertDefined(draft)
                draft.is_global = isGlobal
            }))
        },
    })
}
