import {useMemo} from 'react'

import {useSelectedWallboardQuery, useWallboardsQuery} from '../../queries'
import {useGoToWallboard} from '../context/RouterContext'


export function useSelectWallboard() {
    const {data: wallboard} = useSelectedWallboardQuery()
    const {data: wallboards} = useWallboardsQuery()
    const goToWallboard = useGoToWallboard()

    return useMemo(() => {
        if (!wallboards || !wallboard) {
            return
        }

        const wallboardIndex = wallboards.findIndex(w => w.id === wallboard?.id)

        return {
            next: () => {
                let nextIndex = wallboardIndex + 1
                if (nextIndex > wallboards.length - 1) {
                    nextIndex = 0
                }

                goToWallboard(wallboards[nextIndex].id)
            },
            previous: () => {
                let prevIndex = wallboardIndex - 1
                if (prevIndex < 0) {
                    prevIndex = wallboards.length - 1
                }
                goToWallboard(wallboards[prevIndex].id)
            },
        }
    }, [goToWallboard, wallboard, wallboards])
}
