import type {FC} from 'react'
import React, {useState} from 'react'

import {useResizeObserver} from '@bpinc/lib-helpers'
import {Chart, ChartLegend} from '@bpinc/lib-highcharts'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigMyStats} from '@bpinc/lib-widgets-urls'

import type {WidgetProps} from '../../types'
import {useMyStatsOptions} from './useMyStatsOptions'


const legendClassName = 'h-[17px]'

export const MyStatsWidget: FC<WidgetProps<SupervisorWidgetConfigMyStats>> = ({
    config,
    subscriptionData,
}) => {
    const [cardContent, setCardContent] = useState<HTMLElement | null>(null)
    const resizeObserver = useResizeObserver(cardContent)
    const cardHeight = resizeObserver?.contentRect.height ?? 0

    const options = useMyStatsOptions({config, subscriptionData, cardHeight})

    return (
        <WithNoData
            noData={!options}
            className="flex flex-auto flex-col !items-start"
        >
            <section ref={setCardContent} className="mb-2 h-full w-full">
                <div className="relative flex flex-auto justify-center">
                    <Chart
                        options={options?.handled}
                        className="relative !h-auto w-1/2"
                    />
                    <Chart
                        options={options?.averageHandlingTime}
                        className="relative !h-auto w-1/2"
                    />
                </div>
                <Chart
                    options={options?.successRatio}
                    className="relative !h-auto w-1/2 flex-auto"
                />
                <div className="flex w-full justify-center">
                    <ChartLegend options={options?.legend} className={legendClassName} />
                </div>
            </section>
        </WithNoData>
    )
}
