import React from 'react'

import type {highcharts} from '@bpinc/lib-highcharts'
import {Chart} from '@bpinc/lib-highcharts'
import {tcn} from '@bpinc/lib-tailwind-classnames'


interface SatisfactionChartProps {
    options: highcharts.Options
    className?: string
}
export const SatisfactionChart = ({options, className}: SatisfactionChartProps) => {
    return (
        <div
            className={tcn('absolute flex h-1/2 w-1/2 flex-col items-center justify-center overflow-hidden', className)}
        >
            <Chart options={options} />
        </div>
    )

}
