import type {FC} from 'react'
import React, {useMemo} from 'react'

import {Chart} from '@bpinc/lib-highcharts'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigBreakTime} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {pieChartOptions} from '../../charts'
import {useTenantConfigQuery} from '../../queries'
import type {WidgetProps} from '../../types'


export const BreakTimeWidget: FC<WidgetProps<SupervisorWidgetConfigBreakTime>> = ({
    config,
    subscriptionData,
}) => {
    const breakTimeData = useMemo(() => {
        if (!subscriptionData) {
            return undefined
        }
        const breakTimeSubscriptionConfig =
            config.subscription?.req.columns.find(
                c => c.statName === 'agent_total_break_time',
            )

        if (!breakTimeSubscriptionConfig?.id) {
            return undefined
        }

        return Number(
            subscriptionData?.agent_grids?.[0].values?.[0]?.[
                +breakTimeSubscriptionConfig.id
            ] ?? 0,
        )
    }, [config.subscription?.req.columns, subscriptionData])

    const {data: tenantConfig} = useTenantConfigQuery()

    const {t} = useLocalizationState()

    const options: Highcharts.Options = useMemo(() => {
        const agentDailyBreakTime = tenantConfig?.agentConfiguration.agentDailyBreaktime ?? 0
        const usedTimeout = Math.min(Math.round((breakTimeData ?? 0) / 60), agentDailyBreakTime)
        const remainingBreak = Math.max(agentDailyBreakTime - usedTimeout, 0)

        const dataNames: string[] = [
            t.WALLBOARD_BREAK_TIME_WIDGET.remaining(),
            t.WALLBOARD_BREAK_TIME_WIDGET.used(),
        ]
        const dataValues: number[] = [remainingBreak, usedTimeout]

        function formatter(
            this: Highcharts.TooltipFormatterContextObject,
        ): string {
            const value = this.y
            const category = this.key
            if (typeof value === 'undefined' || typeof category === 'undefined') {
                return ''
            }

            return t.WALLBOARD_BREAK_TIME_WIDGET.minutes_label(
                `${value}`,
                `${category}`,
            )
        }

        return pieChartOptions({
            names: dataNames,
            values: dataValues,
            formatter,
            enableDataLabels: true,
        })
    }, [
        breakTimeData,
        t.WALLBOARD_BREAK_TIME_WIDGET,
        tenantConfig?.agentConfiguration.agentDailyBreaktime,
    ])

    return (
        <WithNoData noData={breakTimeData === undefined}>
            <Chart options={options} />
        </WithNoData>
    )
}
