interface Options {
    value: number
    getFormattedValue: (value: number) => string | undefined
    minValue: number | undefined
    maxValue: number | undefined
    getFormattedMinMax: (value: number) => string
    title: string
}

export function solidGaugeChartOptions({
    value,
    getFormattedValue,
    minValue,
    maxValue,
    getFormattedMinMax,
    title,
}: Options): Highcharts.Options {
    return {
        chart: {
            type: 'solidgauge',
            animation: false,
            backgroundColor: undefined,
        },
        title: {
            text: `<span id="gaugeValue" style="font-size:1.5vmin;color: var(--wallboard-widget-gauge-color)">${title}</span>`,
            align: 'center',
            verticalAlign: 'top',
        },
        subtitle: {
            text: `<span id="solidGaugeValue-${title}" style="font-size:16px;color: var(--wallboard-widget-gauge-color)">${getFormattedValue(
                value,
            )}</span>`,
            align: 'center',
            verticalAlign: 'bottom',
            style: {
                transform: 'translateY(-25%)',
            },
        },
        pane: {
            center: ['50%', '85%'],
            size: '100%',
            startAngle: -90,
            endAngle: 90,
            background: [
                {
                    backgroundColor: '#EEE',
                    innerRadius: '60%',
                    outerRadius: '100%',
                    shape: 'arc',
                },
            ],
        },
        tooltip: {
            enabled: false,
        },
        yAxis: {
            min: minValue,
            max: maxValue,
            title: {
                y: -70,
                text: undefined,
            },
            lineWidth: 0,
            minorTickInterval: null,
            tickAmount: 2,
            tickPositions:
                minValue && maxValue ? [minValue, maxValue] : undefined,
            endOnTick: false,
            labels: {
                y: 16,
                distance: -10,
                style: {
                    color: 'var(--wallboard-widget-gauge-color)',
                    fontSize: '14px',
                },
                padding: 5,
                formatter: function(
                    this: Highcharts.AxisLabelsFormatterContextObject,
                ) {
                    return getFormattedMinMax(this.value)
                },
            },
        },
        credits: {
            enabled: false,
        },
        plotOptions: {
            solidgauge: {
                dataLabels: {
                    y: 5,
                    borderWidth: 0,
                    useHTML: true,
                },
            },
        },
        colors: ['#BAD027'],
        series: [
            {
                type: 'solidgauge',
                data: [value],
                dataLabels: {
                    enabled: false,
                },
            },
        ],
        exporting: {enabled: false},
    }
}
