import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {updatingWidgetDataAtom} from '../../../../atoms'
import type {SupervisorWidgetView} from '../../../../types'
import {AgentGridModal, assertFormValuesValidForConfigAgentGrid} from '../../../AgentGridModal'
import {CopyWidget} from '../../../CopyWidget'
import {assertCorrectWidget} from '../../../helpers'
import {
    assertFormValuesValidForImageWidgetSettingsModal,
    ImageSettingsModal,
} from '../../../ImageSettingsModal/ImageSettingsModal'
import {
    assertFormValuesValidForNewsWidgetSettingsModal,
    NewsSettingsModal,
} from '../../../NewsSettingsModal/NewsSettingsModal'
import {assertFormValuesValidForConfigServiceGrid, ServiceGridModal} from '../../../ServiceGridModal'
import {
    assertFormValuesValidForTextWidgetSettingsModal,
    TextSettingsModal,
} from '../../../TextSettingsModal/TextSettingsModal'
import {assertFormValuesValidForTimeSeriesModal, TimeSeriesModal} from '../../../TimeSeriesModal'
import type {FormValues} from '../../../types'
import {useAgendaWidget} from './useAgendaWidget'
import {useBreakTimeWidget} from './useBreakTimeWidget'
import {useCreateTimeWidget} from './useCreateTimeWidget'
import {useDispositionsWidget} from './useDispositionsWidget'
import {useMyStatsWidget} from './useMyStatsWidget'
import {useSatisfactionWidget} from './useSatisfactionWidget'
import {useSingleOrGaugeWidget} from './useSingleOrGaugeWidget'
import {useSummaryWidget} from './useSummaryWidget'


const exhaustiveCheck = (optionType: never) => {
    throw new Error(`Unhandled option type: ${optionType}`)
}

export const useHandleWidgetSelect = ({
    onSaved,
    replacingWidgetId,
}: {
    onSaved: () => void
    replacingWidgetId: string
}) => {
    const openAgentGridModal = useModalComponentOpener(AgentGridModal)
    const openServiceGridModal = useModalComponentOpener(ServiceGridModal)
    const openImageSettingsModal = useModalComponentOpener(ImageSettingsModal)
    const openNewsSettingsModal = useModalComponentOpener(NewsSettingsModal)
    const openTextSettingsModal = useModalComponentOpener(TextSettingsModal)
    const openTimeSeriesModal = useModalComponentOpener(TimeSeriesModal)
    const openCopyWidget = useModalComponentOpener(CopyWidget)
    const {widget} = useAtomValue(updatingWidgetDataAtom)

    const createTimeWidget = useCreateTimeWidget({
        onSaved,
        replacingWidgetId,
    })

    const createBreakTimeWidget = useBreakTimeWidget({
        onSaved,
    })

    const createSingleOrGaugeWidget = useSingleOrGaugeWidget({
        onSaved,
    })

    const createDispositionsWidget = useDispositionsWidget({
        onSaved,
    })

    const createAgendaWidget = useAgendaWidget({
        onSaved,
    })

    const createMyStatsWidget = useMyStatsWidget({
        onSaved,
    })

    const createSummaryWidget = useSummaryWidget({
        onSaved,
    })

    const createSatisfactionWidget = useSatisfactionWidget({
        onSaved,
    })

    return useCallback((widgetType: SupervisorWidgetView, formValues?: FormValues) => {
        assertCorrectWidget(widget, replacingWidgetId)
        switch (widgetType) {
            case 'image': {
                if (formValues) {
                    assertFormValuesValidForImageWidgetSettingsModal(formValues)
                }

                openImageSettingsModal({
                    replacingWidgetId,
                    onSaved,
                    formValues,
                })
            }

                break

            case 'single':
            case 'gauge':
                createSingleOrGaugeWidget(widgetType, widget)
                break

            case 'agent_grid': {
                if (formValues) {
                    assertFormValuesValidForConfigAgentGrid(formValues)
                }

                openAgentGridModal({
                    replacingWidgetId,
                    formValues,
                    onSaved,
                })
            }
                break

            case 'service_grid': {
                if (formValues) {
                    assertFormValuesValidForConfigServiceGrid(formValues)
                }

                openServiceGridModal({
                    replacingWidgetId,
                    formValues,
                    onSaved,
                })
                break
            }

            case 'news': {
                if (formValues) {
                    assertFormValuesValidForNewsWidgetSettingsModal(formValues)
                }

                openNewsSettingsModal({
                    replacingWidgetId,
                    formValues,
                    onSaved,
                })
                break
            }

            case 'summary':
                createSummaryWidget(widget)
                break

            case 'text': {
                if (formValues) {
                    assertFormValuesValidForTextWidgetSettingsModal(formValues)
                }

                openTextSettingsModal({
                    replacingWidgetId,
                    formValues,
                    onSaved,
                })
                break
            }
            case 'datetime':
                createTimeWidget()
                break

            case 'time_series': {
                if (formValues) {
                    assertFormValuesValidForTimeSeriesModal(formValues)
                }

                openTimeSeriesModal({
                    replacingWidgetId,
                    formValues,
                    onSaved,
                })
                break
            }
            case 'copy_from_other':
                openCopyWidget({
                    replacingWidgetId,
                    onSaved,
                })
                break

            case 'break_time':
                createBreakTimeWidget(widget)
                break
            case 'dispositions':
                createDispositionsWidget(widget)
                break
            case 'my_stats':
                createMyStatsWidget(widget)
                break
            case 'satisfaction':
                createSatisfactionWidget(widget)
                break
            case 'agenda':
                createAgendaWidget(widget)
                break

            default:
                exhaustiveCheck(widgetType)
                break
        }
    }, [
        widget,
        openCopyWidget,
        openAgentGridModal,
        openImageSettingsModal,
        openTextSettingsModal,
        openNewsSettingsModal,
        openServiceGridModal,
        openTimeSeriesModal,
        replacingWidgetId,
        onSaved,
        createSingleOrGaugeWidget,
        createSummaryWidget,
        createTimeWidget,
        createBreakTimeWidget,
        createDispositionsWidget,
        createMyStatsWidget,
        createSatisfactionWidget,
        createAgendaWidget,
    ])
}
