import type {FC} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {SemanticButtonProps} from '@bpinc/lib-ui-elements'
import {SemanticButton} from '@bpinc/lib-ui-elements'


type HeaderButtonProps = Pick<SemanticButtonProps, 'onClick' | 'className' | 'icon' | 'color' | 'loading' | 'disabled'>

export const HeaderButton: FC<HeaderButtonProps> = ({className, ...rest}) => {
    return (
        <SemanticButton
            {...rest}
            basic
            className={tcn(
                'm-0 h-8 w-8 p-0',
                'flex items-center justify-center !bg-white',
                '[&>i]:flex [&>i]:items-center [&>i]:justify-center [&>i]:text-xl [&>i]:outline-none',
                className,
            )}
        />
    )
}
