import React from 'react'

import {createPlatformSpecific} from '@bpinc/ad-platform-specific-web-impl'
import {AuthorizationModule} from '@bpinc/lib-authorization-module'

import {App} from './App'


export const FullscreenWallboardApp: React.FC = () => {
    return (
        <AuthorizationModule
            platformSpecific={createPlatformSpecific('wallboard')}
            app="wallboard"
            appRemoteName="wallboard"
        >
            {() => (
                <App />
            )}
        </AuthorizationModule>
    )
}
