import {useMemo} from 'react'

import type {SupervisorWidgetAgentGridSubscriptionData} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {
    createBreakTimeWidgetSubscription,
    createDispositionsWidgetSubscription,
    createMyStatsWidgetSubscription,
    createSatisfactionWidgetSubscription,
} from './helpers'
import type {UserWidgetsType} from './types'


interface UserWidgetOptions {
    title: (userName: string) => string
    subscription: (widgetId: string, userId: string) => SupervisorWidgetAgentGridSubscriptionData | undefined
}

export const useWidgetOptions: () => Record<UserWidgetsType, UserWidgetOptions> = () => {
    const {t} = useLocalizationState()
    return useMemo(() => {
        return {
            dispositions: {
                title: (userName: string) => {
                    return t.WALLBOARD_DISPOSITION_WIDGET.name(userName)
                },
                subscription: (widgetId: string, userId: string) => {
                    return createDispositionsWidgetSubscription(widgetId, userId)
                },
            },
            satisfaction: {
                title: (userName: string) => {
                    return t.WALLBOARD_SATISFACTION_WIDGET.name(userName)
                },
                subscription: (widgetId: string, userId: string) => {
                    return createSatisfactionWidgetSubscription(widgetId, userId)
                },
            },
            break_time: {
                title: (userName: string) => {
                    return t.WALLBOARD_BREAK_TIME_WIDGET.name(userName)
                },
                subscription: (widgetId: string, userId: string) => {
                    return createBreakTimeWidgetSubscription(widgetId, userId)
                },
            },
            my_stats: {
                title: (userName: string) => {
                    return t.WALLBOARD_MY_STATS_WIDGET.name(userName)
                },
                subscription: (widgetId: string, userId: string) => {
                    return createMyStatsWidgetSubscription(widgetId, userId)
                },
            },
            agenda: {
                title: (userName: string) => {
                    return t.WALLBOARD_AGENDA_WIDGET.name(userName)
                },
                subscription: (widgetId: string, userId: string) => {
                    return undefined
                },
            },
        }
    }, [
        t.WALLBOARD_MY_STATS_WIDGET,
        t.WALLBOARD_SATISFACTION_WIDGET,
        t.WALLBOARD_BREAK_TIME_WIDGET,
        t.WALLBOARD_DISPOSITION_WIDGET,
        t.WALLBOARD_AGENDA_WIDGET,
    ])
}
