import {useParams} from 'react-router-dom'
import {useQuery} from '@tanstack/react-query'

import {assertDefined} from '@bpinc/lib-types-helpers'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {NEW_WALLBOARD_ID} from '../constants'
import {queryKey} from './query-keys'


export function useSelectedWallboardQuery() {
    const {wallboardId} = useParams()
    assertDefined(wallboardId)

    return useQuery({
        queryKey: queryKey.wallboard(wallboardId),
        queryFn() {
            return WallboardApiServices.getWallboardDetails({wallboardId})
        },
        enabled: wallboardId !== NEW_WALLBOARD_ID,
    })
}

