import React, {useCallback, useMemo} from 'react'
import {useController, useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'

import {TextField} from '@bpinc/lib-forms-ui'
import {LinkLikeButton, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {assertCorrectWidget, getAgentGridSubscriptionDefaults} from '../helpers'
import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'
import {TeamsSelectModal} from '../TeamsSelectModal'
import {useHandleCancelWidgetModal} from '../useHandleCancelWidgetModal'


interface FormValues {
    title: string
    teams: string[]
}

type SummarySettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
}

type SummarySubscription = {
    teams: string[]
    id: string
}
function getSummarySubscription({id, teams}: SummarySubscription) {
    return {
        type: 'summary' as const,
        req: {
            ...getAgentGridSubscriptionDefaults(),
            columns: [
                {
                    id: '1',
                    statName: 'acd_state',
                },
            ],
            id,
            team_ids: teams,
        },
    }
}
export const SummarySettingsModal: React.FC<SummarySettingsModalProps> = ({
    onClose,
    replacingWidgetId,
    ...props
}) => {
    const {t} = useLocalizationState()
    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const emptyValues: FormValues = useMemo(
        () => ({
            title: t.WALLBOARD_WIDGET_SUMMARY.default_name(),
            teams: [],
        }),
        [t.WALLBOARD_WIDGET_SUMMARY],
    )

    const defaultValues = useMemo(() => {
        assertCorrectWidget(widget, replacingWidgetId)
        if (widget.type !== 'summary') {
            return emptyValues
        }

        return {
            title: widget.title || emptyValues.title,
            teams:
                widget.subscription.req.team_ids ||
                emptyValues.teams,
        }
    }, [widget, replacingWidgetId, emptyValues])

    const {control, handleSubmit, setValue} = useForm<FormValues>({
        defaultValues,
    })

    const {field: teamsField} = useController({
        name: 'teams',
        control,
    })

    const openTeamsSelectModal = useModalComponentOpener(TeamsSelectModal)

    const handleSelectTeams = useCallback(() => {
        openTeamsSelectModal({
            teams: teamsField.value,
            onSelect: updatedTeams => {
                setValue('teams', updatedTeams)
            },
        })
    }, [openTeamsSelectModal, setValue, teamsField.value])

    const handleSave = useMemo(
        () =>
            handleSubmit(async (formData: FormValues) => {
                assertCorrectWidget(widget, replacingWidgetId)
                updateWidget({
                    ...widget,
                    title: formData.title,
                    type: 'summary',
                    subscription: getSummarySubscription({id: widget.id, teams: formData.teams}),
                })

                onClose()
            }),
        [handleSubmit, widget, replacingWidgetId, updateWidget, onClose],
    )
    const handleCancelWidgetModal = useHandleCancelWidgetModal({onClose, widget})

    return (
        <Modal
            {...props}
            aria-labelledby="summarySettings"
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading" id="summarySettings">
                {t.WALLBOARD.select_teams()}
            </Modal.Header>
            <Modal.Content>
                <GridRow>
                    <FormLabel text={t.WALLBOARD_WIDGET_SUMMARY.title_label()} />
                    <TextField
                        fluid
                        name="title"
                        control={control}
                    />
                </GridRow>
                <GridRow>
                    <FormLabel text={t.WALLBOARD_WIDGET_SUMMARY.teams_label()} />
                    <LinkLikeButton onClick={handleSelectTeams}>
                        {t.WALLBOARD_WIDGET_SUMMARY.selected_teams()}
                    </LinkLikeButton>
                </GridRow>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={handleCancelWidgetModal} enabledInOffline>
                    {t.GLOBAL.cancel()}
                </SemanticButton>
                <SemanticButton onClick={handleSave} primary>
                    {t.GLOBAL.ok()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
