import React, {useMemo} from 'react'
import type {BpWallboardWidgetResolverEditProps, BpWallboardWidgetResolverViewProps} from '@servicepattern/jskit'

import {IconTrash} from '@bpinc/lib-icons'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {Button} from '@bpinc/lib-ui-elements'
import type {SupervisorWidget, SupervisorWidgetsSubscriptionDataItem} from '@bpinc/lib-widgets-urls'

import type {SupervisorWidgetTypes} from '../types'
import {AgendaWidget} from './Agenda'
import {BreakTimeWidget} from './BreakTime'
import {DateTimeWidget} from './DateTime'
import {DispositionsWidget} from './Dispositions'
import {EmptyWidget} from './Empty'
import {GaugeWidget} from './Gauge'
import {GridWidget} from './Grid'
import {ImageWidget} from './Image'
import {MyStatsWidget} from './MyStats'
import {NewsWidget} from './News'
import {SatisfactionWidget} from './Satisfaction'
import {SingleWidget} from './Single'
import {SummaryWidget} from './Summary'
import {TextWidget} from './Text'
import {TimeSeriesWidget} from './TimeSeries'
import {Unknown} from './Unknown'


const WIDGETS_CONFIG: Record<SupervisorWidgetTypes, React.FC<any>> = {
    grid: GridWidget,
    datetime: DateTimeWidget,
    empty: EmptyWidget,
    gauge: GaugeWidget,
    image: ImageWidget,
    news: NewsWidget,
    single: SingleWidget,
    text: TextWidget,
    summary: SummaryWidget,
    break_time: BreakTimeWidget,
    dispositions: DispositionsWidget,
    my_stats: MyStatsWidget,
    satisfaction: SatisfactionWidget,
    agenda: AgendaWidget,
    time_series: TimeSeriesWidget,
}

export function EditWidgetResolver(
    props: BpWallboardWidgetResolverEditProps<SupervisorWidget>,
) {
    const {Card, config, subscriptionData, removeWidget} = props

    const {type} = config
    const Widget = WIDGETS_CONFIG[type] || Unknown
    const cardStyles = useCardStyles(props)

    return (
        <Card {...props} className={cardStyles} header={<WidgetHeaderView config={config} removeWidget={removeWidget} />}>
            <Widget config={config} subscriptionData={subscriptionData} />
        </Card>
    )
}

export function ViewWidgetResolver(
    props: BpWallboardWidgetResolverViewProps<SupervisorWidget>,
) {
    const {Card, config, subscriptionData} = props

    const {type} = config
    const Widget = WIDGETS_CONFIG[type] || Unknown

    const cardStyles = useCardStyles(props)


    return (
        <Card {...props} className={cardStyles} header={<WidgetHeaderView config={config} />}>
            <Widget config={config} subscriptionData={subscriptionData} />
        </Card>
    )
}


function WidgetHeaderView({
    removeWidget,
    config,
}: {
    config: SupervisorWidget
    removeWidget?: (config: SupervisorWidget) => void
}) {
    const buttonClassName = tcn('flex h-6 w-6 items-center justify-center opacity-0',
        'transition-all duration-300 hover:!opacity-100',
        'group-focus-within:opacity-70 group-hover:opacity-70')
    return (
        <div className="flex w-full items-center justify-between gap-4">
            <div className="text-xl">{config.title}</div>
            {removeWidget && (
                <div className="flex items-center gap-1">
                    <Button
                        transparent
                        onClick={() => removeWidget(config)}
                        className={buttonClassName}
                    >
                        <IconTrash className="h-5" />
                    </Button>
                </div>
            )}
        </div>
    )
}

function useCardStyles<W extends SupervisorWidget>({
    config,
    subscriptionData,
}:
| BpWallboardWidgetResolverViewProps<W>
| BpWallboardWidgetResolverEditProps<W>) {
    const isAlertedSingle = useMemo(() => {
        if (config.type !== 'single') {
            return false
        }

        if (
            !config.alertThreshold ||
            !subscriptionData ||
            !config.subscription?.req[0]
        ) {
            return false
        }
        const subscription: SupervisorWidgetsSubscriptionDataItem = subscriptionData

        const value = parseInt(
            subscription.stats_totals?.[config.subscription.req[0]] || '0',
        )
        return value >= config.alertThreshold
    }, [config, subscriptionData])

    return useMemo(() => {
        switch (config.type) {
            case 'single':
                if (isAlertedSingle) {
                    return tcn('group [&>div]:border-[--wallboard-alerted-border] [&>div]:bg-[--wallboard-alerted-background]')
                }
        }
        return tcn('group')
    }, [isAlertedSingle, config.type])
}

