import React, {useEffect, useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'
import {cloneDeep, pick} from 'lodash-es'

import {CheckboxField, TextField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {
    SupervisorAgentStats,
    SupervisorWidgetAgentGridSubscriptionData,
    SupervisorWidgetConfigGrid,
} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {useTeamsQuery} from '../../queries'
import {assertCorrectWidget} from '../helpers'
import type {ColumnsFiledType} from '../shared/GridFormsComponents/ColumnsField'
import {ColumnsField, createEmptyColumn} from '../shared/GridFormsComponents/ColumnsField'
import type {FormValues} from '../types'
import type {Limit} from './AgentsSelect'
import {AgentsSelect} from './AgentsSelect'


type AgentGridModalSettingsFormValues = ColumnsFiledType & {
    title: string
    teamIds: string[]
    agentStats?: SupervisorAgentStats
    columnTitleEnabled: boolean
    maxItemsToShow: number
    limit?: Limit
}


export interface AgentGridWidgetSettingsModalParams extends ModalBase {
    type: 'agent_grid_settings'
    replacingWidgetId: string
    formValues?: ConfigAgentGrid
    onSaved?: () => void
}

type AgentGridModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: ConfigAgentGrid
    onSaved?: () => void
}

type ConfigAgentGrid = SupervisorWidgetConfigGrid & {subscription: SupervisorWidgetAgentGridSubscriptionData}

export function assertFormValuesValidForConfigAgentGrid(
    fv: FormValues,
): asserts fv is ConfigAgentGrid {
    if (!(
        'subscription' in fv &&
        'title' in fv &&
        'columtitle' in fv &&
        'agent_stats' in fv &&
        'limit' in fv &&
        typeof fv.subscription === 'object' &&
        fv.subscription !== null &&
        'type' in fv.subscription &&
        fv.subscription.type !== 'service_grid'
    )) {
        throw new Error('Form values is not valid for config agent grid')
    }
}


const convertToForm = (config: ConfigAgentGrid): Partial<AgentGridModalSettingsFormValues> => {
    const newColumns = config?.subscription?.req?.columns.map((col, index) => {
        const existingOrder = config?.subscription?.req?.order?.find(({by}) => {
            return Number(by) === index + 1
        })
        return {
            ...col,
            dir: (existingOrder?.dir as 'DESC' | 'ASC') || 'EMPT',
            order: index + 1,
        }
    })
    return {
        title: config?.title,
        columnTitleEnabled: config?.columtitle,
        teamIds: cloneDeep(config?.subscription?.req?.team_ids),
        agentStats: cloneDeep(config?.agent_stats),
        limit: cloneDeep(config?.limit),
        maxItemsToShow: cloneDeep(config?.subscription?.req?.limit),
        columns: newColumns,
    }


}

type FormWidgetConfig = Pick<SupervisorWidgetConfigGrid, 'title' | 'columtitle' | 'agent_stats' | 'limit' | 'subscription'>

const convertToBackend = (formValues: AgentGridModalSettingsFormValues, id: string): FormWidgetConfig => {
    const newOrder = formValues.columns.reduce<{ by: string; dir: 'ASC' | 'DESC' }[]>((acc, column, index) => {
        if (column.dir && column.dir !== 'EMPT') {
            return [...acc, {by: String(index + 1), dir: column.dir}]
        }
        return acc
    }, [])
    return {
        title: formValues.title || '',
        columtitle: formValues.columnTitleEnabled,
        agent_stats: formValues.agentStats,
        limit: formValues.limit,
        subscription: {
            type: 'agent_grid',
            req: {
                id,
                team_ids: formValues.teamIds,
                limit: formValues.maxItemsToShow,
                columns: formValues.columns.map((col, ind) => ({...col, id: String(ind + 1)})),
                order: newOrder,
            },
        },
    }
}

const DEFAULT_COLUMN_MAX = 6

export const AgentGridModal: React.FC<AgentGridModalProps> = ({
    onClose,
    replacingWidgetId,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()

    const formValuesParsed = useMemo(() => {
        if (formValues) {
            return convertToForm(formValues)
        }

    }, [formValues])

    const {
        control,
        handleSubmit,
        setValue,
    } = useForm<AgentGridModalSettingsFormValues>({
        defaultValues: {
            title: formValuesParsed?.title || t.WALLBOARD_WIDGET_EMPTY.agent_grid(),
            teamIds: formValuesParsed?.teamIds || [],
            agentStats: {
                busy: formValuesParsed?.agentStats?.busy !== false,
                ready: formValuesParsed?.agentStats?.ready !== false,
                after_call_work: formValuesParsed?.agentStats?.after_call_work !== false,
                not_ready: formValuesParsed?.agentStats?.not_ready !== false,
            },
            limit: {
                subteam: formValuesParsed?.limit?.subteam || false,
                logged_in_only: formValuesParsed?.limit?.logged_in_only || false,
            },
            columnTitleEnabled: formValuesParsed?.columnTitleEnabled !== false,
            maxItemsToShow: formValuesParsed?.maxItemsToShow || DEFAULT_COLUMN_MAX,
            columns: formValuesParsed?.columns || [createEmptyColumn(0, 'agent_grid')],
        },
    })

    const {data: teams} = useTeamsQuery()

    useEffect(() => {
        const teamIds = (teams || []).map(({id}) => id)
        setValue('teamIds', teamIds)
    }, [setValue, teams])

    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            type: 'grid',
            ...convertToBackend(formData, widget.id),
            ...pick(
                widget,
                'id',
                'x',
                'y',
                'width',
                'height',
                'show_settings',
            ),
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                {t.WALLBOARD_WIDGETS_GRIDS.agent_grid()}
            </Modal.Header>
            <Modal.Content>
                <div className="mb-4 flex gap-4">
                    <TextField
                        fluid
                        label={t.GLOBAL.title()}
                        name="title"
                        placeholder={t.GLOBAL.title()}
                        className="grow [&>label]:font-bold"
                        control={control}
                    />
                    <TextField
                        fluid
                        label={t.WALLBOARD_WIDGETS_GRIDS.max_to_show()}
                        name="maxItemsToShow"
                        type="number"
                        className="shrink-0 [&>label]:font-bold"
                        placeholder={t.WALLBOARD_WIDGETS_GRIDS.max_to_show()}
                        control={control}
                    />
                    <CheckboxField
                        label={t.WALLBOARD_WIDGETS_GRIDS.column_headings()}
                        name="columnTitleEnabled"
                        toggle
                        className="mt-4 shrink-0 self-center"
                        control={control}
                    />
                </div>
                <AgentsSelect control={control} setValue={setValue} />
                <ColumnsField control={control} setValue={setValue} type="agent_grid" />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()} enabledInOffline>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
