import React, {useRef} from 'react'

import {useSessionStateMethods} from '@bpinc/ad-session-context'
import {useFocusElement} from '@bpinc/lib-helpers'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'


export const LogoutModal: React.FC<CommonModalProps> = ({onClose: closeLogoutModal, ...props}) => {
    const {t} = useLocalizationState()
    const {logout} = useSessionStateMethods()

    const buttonRef = useRef<HTMLButtonElement>(null)

    useFocusElement(buttonRef, {timeout: 0})

    return (
        <Modal
            {...props}
            onClose={closeLogoutModal}
            size="small"
            className="max-w-full"
            aria-labelledby="logoutModalHeader"
        >
            <Modal.Header role="heading" id="logoutModalHeader">
                { t.LOGOUT_DIALOG.sign_out_text()}
            </Modal.Header>
            <Modal.Content>
                {t.LOGOUT_DIALOG.body_text()}
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => logout()} primary ref={buttonRef}>
                    {t.LOGOUT_DIALOG.sign_out_text()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
