import type {SyntheticEvent} from 'react'
import React, {useCallback, useMemo} from 'react'
import {useParams} from 'react-router-dom'

import {truthy} from '@bpinc/lib-types-helpers'
import type {DropdownItemProps, DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useWallboardsQuery} from '../../queries'
import {useGoToWallboard} from '../context/RouterContext'


export const WallboardSelect: React.FC = () => {
    const {t} = useLocalizationState()
    const {wallboardId} = useParams()
    const {data} = useWallboardsQuery()
    const goToWallboard = useGoToWallboard()
    const options = useMemo(() => {
        const personal: DropdownItemProps[] = []
        const global: DropdownItemProps[] = []
        data?.forEach(({id, name, is_global}) => {
            is_global ? global.push({value: id, text: name}) : personal.push({value: id, text: name})
        })
        return truthy([
            personal.length ? {disabled: true, text: t.WALLBOARD.wallboard_select_personal()} : undefined,
            ...personal,
            global.length ? {disabled: true, text: t.WALLBOARD.wallboard_select_global()} : undefined,
            ...global,
        ])
    }, [data, t.WALLBOARD])

    const handleSelectWallboard = useCallback((_e: SyntheticEvent<HTMLElement, Event>, {value}: DropdownProps) => {
        if (typeof value !== 'string') {
            throw new Error('WallboardSelect: value must be a string')
        }

        goToWallboard(value)
    }, [goToWallboard])


    return (
        <Dropdown
            options={options}
            className="text-2xl text-white"
            value={wallboardId}
            onChange={handleSelectWallboard}
        />
    )
}
