import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {updatingWidgetDataAtom} from '../../../../atoms'
import {SelectUserModal} from '../../../SelectUserModal'


export const useAgendaWidget = ({
    onSaved,
}: {
    onSaved: () => void
}) => {
    const {updateWidget} = useAtomValue(updatingWidgetDataAtom)
    const {t} = useLocalizationState()

    const openSelectUserModal = useModalComponentOpener(SelectUserModal)

    return useCallback((widgetConfig: SupervisorWidget) => {
        openSelectUserModal({
            onSelected: (userId, userName) => {
                updateWidget({
                    ...widgetConfig,
                    type: 'agenda',
                    title: t.WALLBOARD_AGENDA_WIDGET.name(userName),
                    user_id: userId,
                    userName,
                })
            },
            currentWidgetConfig: widgetConfig,
        })
        onSaved()
    }, [onSaved, openSelectUserModal, t.WALLBOARD_AGENDA_WIDGET, updateWidget])
}
