import type {ButtonHTMLAttributes} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'


export const ColumnFieldRow = React.forwardRef<HTMLDivElement, ButtonHTMLAttributes<HTMLDivElement>>(
    ({children, className, ...restProps}, ref) => {
        return (
            <div
                ref={ref}
                {...restProps}
                className={tcn(className, 'grid grid-cols-[minmax(0,2fr)_24px_1fr_48px] gap-4')}
            >
                {children}
            </div>
        )
    },
)
