import type {SupervisorWidgetsSubscriptionRequest} from '@bpinc/lib-widgets-urls'


const addExtraKeys = (key: string, ...extraKeys: unknown[]) => {
    const resultKeys = [key]
    extraKeys.forEach(extraKey => {
        if (extraKey !== undefined && extraKey !== null) {
            resultKeys.push(String(extraKey))
        }
    })
    return resultKeys
}

export const queryKey = {
    agents: () => ['agents'],
    services: () => ['services'],
    subscribe: (
        subscriptionData?:
        | Record<number, SupervisorWidgetsSubscriptionRequest>
        | undefined,
    ) =>
        subscriptionData
            ? [
                'subscribe',
                //This is a hack to make sure that the query key is unique for each subscription
                //The subscription data is an object with the widget id as the key and the subscription data as the value
                //If subscription data hasn't changed, the query key will be the same and the query won't be refetched
                Object.entries(subscriptionData || {}).reduce(
                    (acc, [key, value]) => acc + key + JSON.stringify(value),
                    '',
                ),
            ]
            : ['subscribe'],
    subscribe_data: () => ['subscribe-data'],
    tenant_config: () => ['tenant-config'],
    agenda: () => ['agenda'],
    teams: () => ['teams'],
    time_series: (statType: string, interval: string) => addExtraKeys('time_series', statType, interval),
    wallboard: (wallboardId?: string) => addExtraKeys('wallboard', wallboardId),
    wallboards: () => ['wallboards'],
    wallboard_templates: () => ['wallboard-templates'],
}
