import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'
import {pick} from 'lodash-es'

import {TextAreaField, TextField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {assertCorrectWidget} from '../helpers'
import type {FormValues} from '../types'


interface TextWidgetFormValues {
    title: string
    text: string
}

export interface TextWidgetSettingsModalParams extends ModalBase {
    type: 'text_widget_settings'
    replacingWidgetId: string
    formValues?: TextWidgetFormValues
    onSaved?: () => void
}

export function assertFormValuesValidForTextWidgetSettingsModal(
    fv: FormValues,
): asserts fv is TextWidgetFormValues {
    if (!(
        'title' in fv &&
        'text' in fv
    )) {
        throw new Error('Form values are not valid for text widget settings modal')
    }
}

type TextSettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: TextWidgetFormValues
    onSaved?: () => void
}

export const TextSettingsModal: React.FC<TextSettingsModalProps> = ({
    replacingWidgetId,
    onClose,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()

    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const {
        control,
        handleSubmit,
    } = useForm<TextWidgetFormValues>({
        defaultValues: {
            title: formValues?.title || t.WALLBOARD_WIDGET_TEXT.text(),
            text: formValues?.text || '',
        },
    })

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            type: 'text',
            ...formData,
            ...pick(
                widget,
                'id',
                'x',
                'y',
                'width',
                'height',
                'show_settings',
            ),
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                { t.WALLBOARD_WIDGET_EMPTY.text() }
            </Modal.Header>
            <Modal.Content>
                <TextField
                    name="title"
                    fluid
                    placeholder={t.WALLBOARD_WIDGET_TEXT.title()}
                    className="mb-2"
                    control={control}
                />
                <TextAreaField
                    name="text"
                    placeholder={t.WALLBOARD_WIDGET_TEXT.text()}
                    className="w-full rounded border border-solid border-gray-300 p-3"
                    control={control}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()} enabledInOffline>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
