import type {ReactElement, ReactFragment} from 'react'
import React, {useCallback, useEffect, useMemo, useState} from 'react'
import {useParams} from 'react-router-dom'
import produce from 'immer'
import {isEqual} from 'lodash-es'
import type {Context} from 'use-context-selector'
import {createContext, useContextSelector} from 'use-context-selector'

import type {FCWithChildren, StateSetter} from '@bpinc/lib-types-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'

import {NEW_WALLBOARD_ID} from '../constants'
import {
    useCreateWallboardMutation,
    useUpdateWallboardNameMutation,
    useUpdateWallboardWidgetsMutation,
} from '../mutations'
import {useSelectedWallboardQuery} from '../queries'
import {getWallboardConfigFontScale, getWallboardWidgets} from '../selectors'
import {useEditMode} from '../ui-context/UIContext'


interface EditingContextValue {
    name: string
    setName: (newName: string) => void
    fontScale: number
    setFontScale: StateSetter<number>
    widgets: SupervisorWidget[]
    isSaving: boolean
    updateWidget: (updatedWidgetConfig: SupervisorWidget) => void
    addWidget: (newWidgetConfig: SupervisorWidget) => void
    removeWidget: (removingWidgetId: string) => void
    setWidgets: StateSetter<SupervisorWidget[]>
    save: () => void
}

let EditingContext: Context<EditingContextValue>

export const EditingContextProvider: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const {data: selectedWallboard} = useSelectedWallboardQuery()
    const [name, setName] = useState('')
    const [fontScale, setFontScale] = useState(1)
    const {wallboardId} = useParams()
    const isNewWallboard = wallboardId === NEW_WALLBOARD_ID

    const [widgets, setWidgets] = useState<SupervisorWidget[]>([])

    const editMode = useEditMode()

    useEffect(() => {
        if (selectedWallboard) {
            setWidgets(getWallboardWidgets(selectedWallboard))
            setName(selectedWallboard.name)
            setFontScale(getWallboardConfigFontScale(selectedWallboard))
        }
    }, [
        editMode,
        selectedWallboard,
    ])

    const addWidget = useCallback((newWidgetConfig: SupervisorWidget) => {
        setWidgets(currentWidgets => [
            ...currentWidgets,
            newWidgetConfig,
        ])
    }, [])

    const updateWidget = useCallback((updatedWidgetConfig: SupervisorWidget) => {
        setWidgets(produce(draft => {
            const widgetIndex = draft.findIndex(
                w => w.id === updatedWidgetConfig.id,
            ) ?? -1

            if (widgetIndex === -1) {
                return
            }

            const currentWidget = draft[widgetIndex]

            if (!currentWidget) {
                return
            }

            Object.assign(currentWidget, updatedWidgetConfig)
        }))
    }, [])

    const removeWidget = useCallback((removingWidgetId: string) => {
        setWidgets(currentWidgets => currentWidgets.filter(
            w => w.id !== removingWidgetId,
        ))
    }, [setWidgets])

    const {
        mutateAsync: updateWallboardConfig,
        isPending: isSavingWidgets,
    } = useUpdateWallboardWidgetsMutation()

    const updateWidgets = useCallback(async () => {
        assertDefined(selectedWallboard)

        await updateWallboardConfig({
            wallboardId: selectedWallboard.id,
            updatedWallboardWidgets: widgets,
            updatedStyle: {
                ...(selectedWallboard.config.style || {}),
                fontScaleValue: fontScale,
            },
        })

        editMode.setFalse()

    }, [selectedWallboard, updateWallboardConfig, widgets, fontScale, editMode])

    const {
        mutateAsync: updateWallboardName,
        isPending: isSavingName,
    } = useUpdateWallboardNameMutation()

    const {mutate: createWallboard} = useCreateWallboardMutation()

    const updateName = useCallback(async () => {
        assertDefined(selectedWallboard)

        await updateWallboardName({
            wallboardId: selectedWallboard.id,
            name,
        })
    }, [selectedWallboard, updateWallboardName, name])

    const save = useCallback(async () => {
        if (isNewWallboard) {
            createWallboard({
                name,
                widgets,
            })
        } else {
            //First array item is a condition, second is a promise to run if condition is true
            const promisesConfig = [[widgetsWereChanged, updateWidgets], [nameWasChanged, updateName]] as const

            const promisesToRun = promisesConfig.filter(([shouldRun]) => shouldRun()).map(([, promise]) => promise())

            await Promise.allSettled(promisesToRun)
        }

        editMode.setFalse()

        function widgetsWereChanged() {
            return !isEqual(getWallboardWidgets(selectedWallboard), widgets) || getWallboardConfigFontScale(selectedWallboard) !== fontScale
        }

        function nameWasChanged() {
            return selectedWallboard?.name !== name
        }

    }, [isNewWallboard, fontScale, editMode, createWallboard, name, widgets, selectedWallboard, updateWidgets, updateName])

    const isSaving = useMemo(() => isSavingWidgets || isSavingName, [
        isSavingWidgets,
        isSavingName,
    ])

    const contextValue = useMemo<EditingContextValue>(
        () => ({
            name,
            setName,
            fontScale,
            setFontScale,
            widgets,
            setWidgets,
            isSaving,
            updateWidget,
            addWidget,
            removeWidget,
            save,
        }),
        [
            name,
            fontScale,
            widgets,
            isSaving,
            addWidget,
            updateWidget,
            removeWidget,
            save,
        ],
    )

    if (!EditingContext) {
        EditingContext = createContext<EditingContextValue>(contextValue)
        EditingContext.displayName = 'EditingContext'
    }

    return (
        <EditingContext.Provider value={contextValue}>
            {children}
        </EditingContext.Provider>
    )
}

export const useUpdateWidget = () => useContextSelector(EditingContext, value => value.updateWidget)
export const useAddWidget = () => useContextSelector(EditingContext, value => value.addWidget)
export const useRemoveWidget = () => useContextSelector(EditingContext, value => value.removeWidget)
export const useSave = () => useContextSelector(EditingContext, value => value.save)
export const useIsSaving = () => useContextSelector(EditingContext, value => value.isSaving)
export const useWidgets = () => useContextSelector(EditingContext, value => value.widgets)
export const useName = () => useContextSelector(EditingContext, value => value.name)
export const useSetName = () => useContextSelector(EditingContext, value => value.setName)
export const useFontScale = () => useContextSelector(EditingContext, value => value.fontScale)
export const useSetFontScale = () => useContextSelector(EditingContext, value => value.setFontScale)
export const useSetWidgets = () => useContextSelector(EditingContext, value => value.setWidgets)

