import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'
import {pick} from 'lodash-es'

import {TextField} from '@bpinc/lib-forms-ui'
import {MS_IN_DAY} from '@bpinc/lib-helpers'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigTimeSeries} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {assertCorrectWidget} from '../helpers'
import {FormLabel} from '../shared/FormLabel'
import {GridRow} from '../shared/GridRow'
import type {FormValues} from '../types'
import {SeriesTypeField} from './SeriesType'
import type {Statistics} from './Statistics'
import {StatisticsField, TEAMS_STATISTICS_DEFAULT} from './Statistics'
import {TimeRangeField} from './TimeRange'


interface TimeSeriesModalFormValues {
    title: string
    seriesType: 'teams' | 'services'
    statistics: Statistics
    timeRange: number
}

export function assertFormValuesValidForTimeSeriesModal(
    fv: FormValues,
): asserts fv is SupervisorWidgetConfigTimeSeries {
    if (!(
        'title' in fv &&
        'series_type' in fv &&
        'value' in fv &&
        'timeRange' in fv
    )) {
        throw new Error('Form values are not valid for time series modal')
    }
}

export interface TimeSeriesWidgetSettingsModalParams extends ModalBase {
    type: 'time_series_settings'
    replacingWidgetId: string
    //TODO: Why formValues doesn't have type TimeSeriesModalFormValues?
    formValues?: SupervisorWidgetConfigTimeSeries
    onSaved?: () => void
}

type TimeSeriesModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: SupervisorWidgetConfigTimeSeries
    onSaved?: () => void
}

const TIME_RANGE_DEFAULT = MS_IN_DAY * 7


const convertToBackend = (formData: TimeSeriesModalFormValues): Pick<SupervisorWidgetConfigTimeSeries, 'series_type' | 'value' | 'title' | 'timeRange'> => {
    return {
        series_type: formData.seriesType,
        value: formData.statistics,
        title: formData.title,
        timeRange: String(formData.timeRange),
    }
}

const convertToForm = (config: SupervisorWidgetConfigTimeSeries): TimeSeriesModalFormValues => {
    return {
        seriesType: config.series_type,
        title: config.title,
        statistics: (config.value as Statistics),
        timeRange: Number(config.timeRange),
    }
}

export const TimeSeriesModal: React.FC<TimeSeriesModalProps> = ({
    onClose,
    replacingWidgetId,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()
    const parsedForm = useMemo(() => {
        if (formValues) {
            return convertToForm(formValues)
        }
    }, [formValues])
    const {
        control,
        handleSubmit,
        setValue,
    } = useForm<TimeSeriesModalFormValues>({
        defaultValues: {
            title: parsedForm?.title || t.WALLBOARD_WIDGET_TIME_SERIES.name(),
            seriesType: parsedForm?.seriesType || 'teams',
            statistics: parsedForm?.statistics || TEAMS_STATISTICS_DEFAULT,
            timeRange: parsedForm?.timeRange || TIME_RANGE_DEFAULT,
        },
    })

    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            type: 'time_series',
            ...convertToBackend(formData),
            ...pick(
                widget,
                'id',
                'x',
                'y',
                'width',
                'height',
                'show_settings',
            ),
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                {t.WALLBOARD_WIDGETS_GRIDS.name()}
            </Modal.Header>
            <Modal.Content>
                <GridRow>
                    <FormLabel text={t.GLOBAL.title()} />
                    <TextField
                        fluid
                        name="title"
                        placeholder={t.GLOBAL.title()}
                        control={control}
                    />
                </GridRow>
                <SeriesTypeField control={control} />
                <StatisticsField control={control} setValue={setValue} />
                <TimeRangeField control={control} />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => onClose()} enabledInOffline>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
