import {isArray, mergeWith} from 'lodash-es'


interface Options {
    data: {value: number; name: string; color?: string; formatter?: Highcharts.FormatterCallbackFunction<Highcharts.AxisLabelsFormatterContextObject>}[]
    dataName: string
}

function mergeCustomizer<T>(srcValue: T, objValue: T): T | undefined {
    if (isArray(srcValue)) {
        return objValue
    }
}


export function barOptions({data, dataName}: Options, options?: Partial<Highcharts.Options>): Highcharts.Options {
    return mergeWith({
        chart: {
            type: 'bar',
            backgroundColor: 'transparent',
            width: null,
            height: null,
        },
        plotOptions: {
            series: {
                pointPadding: 0.01,
                groupPadding: 0.01,
                borderColor: 'transparent',
                dataLabels: {
                    enabled: true,
                    style: {
                        color: 'var(--wallboard-text-secondary)',
                        fontSize: '14px',
                        fontWeight: 'normal',
                        textOutline: '0',
                    },
                },
            },
        },
        title: {
            text: '',
            margin: 0,
        },
        xAxis: {
            categories: data.map(({name}) => name),
            title: {
                text: '',
            },
            labels: {
                style: {
                    color: 'var(--wallboard-text-secondary)',
                    fontSize: '14px',
                    fontWeight: 'normal',
                },
            },
            lineColor: 'transparent',
            tickColor: 'transparent',
            gridLineColor: 'transparent',
        },
        yAxis: {
            title: {text: ''},
            lineColor: 'transparent',
            gridLineColor: 'transparent',
            labels: {format: ' '},
        },
        credits: {
            enabled: false,
        },
        colors: ['#BAD027'],
        legend: {
            enabled: false,
        },
        series: [
            {
                name: dataName,
                data: data.map(({value, color}) => ({y: value, color})),
            },
        ],
    }, options, mergeCustomizer)
}
