import type {SyntheticEvent} from 'react'
import React, {useCallback, useMemo, useState} from 'react'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {DropdownItemProps, DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown, Loader, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useSetFontScale, useSetName, useSetWidgets} from '../editing-context'
import {useGoToNewWallboard} from '../layout/context/RouterContext'
import {useWallboardTemplatesQuery} from '../queries'
import {getWallboardWidgets} from '../selectors'
import {useEditMode} from '../ui-context/UIContext'


export const NewWallboardModal: React.FC<CommonModalProps> = ({
    onClose: closeNewWallboardModal,
    ...props
}) => {
    const {t} = useLocalizationState()
    const goToNewWallboard = useGoToNewWallboard()
    const [selectedTemplateIndex, setSelectedTemplateIndex] = useState(0)
    const editMode = useEditMode()
    const setWidgets = useSetWidgets()
    const setName = useSetName()
    const setFontScale = useSetFontScale()

    const {data: templates, isLoading} = useWallboardTemplatesQuery()

    const addNewWallboard = useCallback(() => {
        assertDefined(templates)
        editMode.setTrue()
        setName(templates[selectedTemplateIndex].name)
        setWidgets(getWallboardWidgets(templates[selectedTemplateIndex]))
        setFontScale(1)
        goToNewWallboard()
        closeNewWallboardModal()
    }, [closeNewWallboardModal, editMode, selectedTemplateIndex, setFontScale, setName, setWidgets, templates, goToNewWallboard])

    const options: DropdownItemProps[] = useMemo(() => {
        return templates?.map((template, index) => ({
            value: index,
            text: template.name,
        })) || []
    }, [templates])

    const handleSelectChange = useCallback((_evt: SyntheticEvent, {value}: DropdownProps) => {
        if (typeof value === 'number') {
            setSelectedTemplateIndex(value)
        }
    }, [])


    return (
        <Modal
            {...props}
            onClose={closeNewWallboardModal}
            size="tiny"
            className="max-w-full"
            aria-labelledby="newWallboardModalHeader"
            data-testid="new-wallboard-modal"
        >
            <Modal.Header role="heading" id="newWallboardModalHeader">
                {t.WALLBOARD.new_wallboard()}
            </Modal.Header>
            <Modal.Content>
                <label className="mr-4">{t.WALLBOARD.new_wallboard_type()}</label>
                {isLoading ? (
                    <Loader active color="primary" size="medium" />
                ) : (
                    <Dropdown options={options} value={selectedTemplateIndex} onChange={handleSelectChange} />
                )}
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={() => closeNewWallboardModal()} enabledInOffline>
                    {t.GLOBAL.cancel()}
                </SemanticButton>
                <SemanticButton onClick={addNewWallboard} primary disabled={!templates} data-testid="new-wallboard-ok">
                    {t.GLOBAL.ok()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
