import React, {useMemo} from 'react'
import type {Control} from 'react-hook-form'
import {noop} from 'lodash-es'

import {SelectField} from '@bpinc/lib-forms-ui'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {ImageWidgetSettingsModalFormValues} from './ImageSettingsModal'


interface ImageStyleSelectProps {
    control: Control<ImageWidgetSettingsModalFormValues>
}

export const ImageStyleSelect: React.FC<ImageStyleSelectProps> = ({
    control,
}) => {
    const {t} = useLocalizationState()

    const options = useMemo(() => [
        {
            key: 'cover',
            value: 'cover',
            text: t.WALLBOARD_WIDGET_IMAGE.style_cover(),
            onClick: noop,
        },
        {
            key: 'contain',
            value: 'contain',
            text: t.WALLBOARD_WIDGET_IMAGE.style_contain(),
            onClick: noop,
        },
    ], [t])

    return (
        <SelectField
            name="style"
            fluid
            upward
            control={control}
            search
            selection
            options={options}
            placeholder={t.WALLBOARD_WIDGET_IMAGE.style()}
        />
    )
}
