import type {FC} from 'react'
import React, {useMemo} from 'react'

import {Chart} from '@bpinc/lib-highcharts'
import type {KVArray} from '@bpinc/lib-transport-conversion'
import {convertKVArray} from '@bpinc/lib-transport-conversion'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigDispositions} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {barOptions} from '../../charts'
import type {WidgetProps} from '../../types'


type DispositionsData = KVArray<DispositionItem>

type DispositionItem = {
    count: number
    name: string
}
export const DispositionsWidget: FC<WidgetProps<SupervisorWidgetConfigDispositions>> = ({
    config,
    subscriptionData,
}) => {
    const dispositionsData = useMemo(() => {
        if (!subscriptionData) {
            return undefined
        }

        const dispositionsSubscriptionConfig =
            config.subscription?.req.columns.find(
                c => c.statName === 'top_dispositions_per_day',
            )

        if (!dispositionsSubscriptionConfig?.id) {
            return undefined
        }

        const stringifiedData =
            subscriptionData?.agent_grids?.[0].values?.[0]?.[
                +dispositionsSubscriptionConfig.id
            ]

        if (!stringifiedData) {
            return undefined
        }

        try {
            const parsedData: DispositionsData = JSON.parse(stringifiedData)
            return convertKVArray(parsedData, (item: DispositionItem) => ({
                name: item.name,
                value: Number(item.count),
            }))
        } catch (error) {
            return undefined
        }
    }, [config.subscription?.req.columns, subscriptionData])

    const {t} = useLocalizationState()

    const options = useMemo(() => {
        if (!dispositionsData?.length) {
            return undefined
        }

        return barOptions({
            dataName: t.WALLBOARD_DISPOSITION_WIDGET.count(),
            data: dispositionsData,
        })
    }, [dispositionsData, t.WALLBOARD_DISPOSITION_WIDGET])


    return (
        <WithNoData noData={!options} >
            <Chart options={options} />
        </WithNoData>
    )
}
