import {useCallback} from 'react'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {EmptyWidgetSettingsModal} from './EmptyWidgetSettingsModal'


interface Props {
    onClose?: () => void
    widget?: SupervisorWidget | null
}
export const useHandleCancelWidgetModal = ({onClose, widget}: Props) => {
    const openEmptyWidgetSettingsModal = useModalComponentOpener(EmptyWidgetSettingsModal)
    return useCallback(() => {
        if (widget && widget.type === 'empty' && !document.querySelector('[data-id="settings-widgets"]')) {
            openEmptyWidgetSettingsModal({
                replacingWidgetId: widget.id,
            })
            onClose?.()
        }
        onClose?.()
    }, [onClose, openEmptyWidgetSettingsModal, widget])
}
