import React, {useCallback, useMemo, useState} from 'react'

import {DoubleColumnSelect, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useServicesQuery} from '../queries'


type ServicesSelectModalProps = CommonModalProps & {
    services: string[]
    onSelect: (services: string[]) => void
}

export const ServicesSelectModal: React.FC<ServicesSelectModalProps> = ({services, onSelect, onClose, ...commonModalProps}) => {
    const {t} = useLocalizationState()
    const [selectedServices, setSelectedServices] = useState(services)

    const handleOnComplete = useCallback(() => {
        onSelect(selectedServices)
        onClose()
    }, [onClose, onSelect, selectedServices])

    const {data: allServices} = useServicesQuery()

    const options = useMemo(() => {
        return (allServices || []).map(service => ({
            value: service.id,
            text: service.name,
        }))
    }, [allServices])

    return (
        <Modal
            onClose={onClose}
            size="tiny"
            className="max-w-full"
            {...commonModalProps}
        >
            <Modal.Header>
                {t.WALLBOARD_SERVICES_MODAL.title()}
            </Modal.Header>
            <Modal.Content>
                <DoubleColumnSelect
                    className="max-h-[70vh] min-h-[280px]"
                    value={selectedServices}
                    setValue={setSelectedServices}
                    options={options}
                    availableTitle={t.GLOBAL.available()}
                    selectedTitle={t.GLOBAL.included()}
                    noSearch
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    primary
                    onClick={handleOnComplete}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
