import type {CSSProperties} from 'react'
import React, {
    useEffect,
    useLayoutEffect,
    useMemo,
    useRef,
    useState,
} from 'react'
import {useAtomValue} from 'jotai'
import {isUndefined} from 'lodash-es'

import {useResizeObserver} from '@bpinc/lib-helpers'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigGauge} from '@bpinc/lib-widgets-urls'

import {audioNotifyAtom} from '../../atoms'
import {formatStatValue} from '../../stats/helpers'
import {useGetStatsConfig} from '../../stats/useGetStatsConfig'
import type {WidgetProps} from '../../types'
import {WithNoAvailable} from './WithNoAvailable'


export const SingleWidget: React.FC<WidgetProps<SupervisorWidgetConfigGauge>> = ({
    config,
    subscriptionData,
}) => {
    const getStatsConfig = useGetStatsConfig()
    const audioAlert = useAtomValue(audioNotifyAtom)

    const ref = useRef<HTMLDivElement>(null)
    const [cardStyle, setCardStyle] = useState<CSSProperties>({fontSize: `${ref.current?.clientHeight}%`})
    const resizeObserverEntry = useResizeObserver(ref)

    useLayoutEffect(() => {
        if (!resizeObserverEntry) {
            setCardStyle(state => ({...state, fontSize: `${ref.current?.clientHeight}%`}))
            return
        }
        setCardStyle(state => ({...state, fontSize: `${resizeObserverEntry.contentRect.height}%`}))
    }, [resizeObserverEntry])

    const valueStatConfig = useMemo(() => {
        if (!config.subscription?.req) {
            return undefined
        }
        return getStatsConfig('statsSingle', config.subscription.req[0])
    }, [config.subscription?.req, getStatsConfig])

    const value = useMemo(() => {
        if (!subscriptionData || !config.subscription?.req[0]) {
            return undefined
        }

        const stat = config.subscription.req[0]
        return subscriptionData.stats_totals?.[stat]
    }, [config.subscription?.req, subscriptionData])

    useEffect(() => {
        if (!config.warning_sound || !value) {
            return
        }
        const inverted = valueStatConfig?.alertInverted || false
        const parsedValue = parseInt(value)
        const shouldNotify =
            (!inverted && parsedValue >= config.alertThreshold) ||
            (inverted && parsedValue <= config.alertThreshold)

        if (!shouldNotify || !audioAlert) {
            return
        }

        const {stop} = audioAlert.start()

        return () => {
            stop()
        }
    }, [
        audioAlert,
        config.alertThreshold,
        config.warning_sound,
        value,
        valueStatConfig?.alertInverted,
    ])

    const valueFormatted = useMemo(() => {
        if (!value) {
            return undefined
        }

        return formatStatValue(value, valueStatConfig?.format)
    }, [value, valueStatConfig?.format])

    const optValueFormatted = useMemo(() => {
        if (!subscriptionData || !config.subscription?.req[1]) {
            return undefined
        }

        const stat = config.subscription.req[1]
        const statConfig = getStatsConfig('statsSingle', stat)
        return formatStatValue(
            subscriptionData.stats_totals?.[stat],
            statConfig.format,
        )
    }, [config.subscription, getStatsConfig, subscriptionData])

    const noData = isUndefined(value && optValueFormatted)

    return (
        <div ref={ref} style={cardStyle} className="h-full w-full">
            <WithNoData
                noData={noData}
                className={tcn({'justify-start': !noData})}
            >
                <div className="flex h-4/5 w-full flex-col">
                    <ValueParagraph bigText>
                        {valueFormatted}
                    </ValueParagraph>
                    <ValueParagraph>
                        {optValueFormatted}
                    </ValueParagraph>
                </div>
            </WithNoData>
        </div>
    )
}

interface ValueParagraphProps {
    children: string | undefined
    bigText?: boolean
}

const ValueParagraph: React.FC<ValueParagraphProps> = ({
    children,
    bigText,
}) => {
    return (
        <p
            className={tcn(
                {
                    [`text-[250%]`]: bigText,
                    [`text-[150%]`]: !bigText,
                },
                'm-0 shrink-0 truncate text-center',
            )}
            title={children}
        >
            <WithNoAvailable>{children}</WithNoAvailable>
        </p>
    )
}
