import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {updatingWidgetDataAtom} from '../../../../atoms'
import {createMyStatsWidgetSubscription} from '../../../helpers'
import {SelectUserModal} from '../../../SelectUserModal'


export const useMyStatsWidget = ({
    onSaved,
}: {
    onSaved: () => void
}) => {
    const {updateWidget} = useAtomValue(updatingWidgetDataAtom)
    const {t} = useLocalizationState()
    const openSelectUserModal = useModalComponentOpener(SelectUserModal)

    return useCallback((currentWidgetConfig: SupervisorWidget) => {
        openSelectUserModal({
            onSelected: (userId, userName) => {
                updateWidget({
                    ...currentWidgetConfig,
                    type: 'my_stats',
                    title: t.WALLBOARD_MY_STATS_WIDGET.name(userName),
                    user_id: userId,
                    userName,
                    subscription: createMyStatsWidgetSubscription(currentWidgetConfig.id, userId),
                })
            },
            currentWidgetConfig,
        })
        onSaved()
    }, [openSelectUserModal, onSaved, t.WALLBOARD_MY_STATS_WIDGET, updateWidget])
}
