import {useCallback} from 'react'
import {useAtomValue} from 'jotai'

import {updatingWidgetDataAtom} from '../../../../atoms'
import {assertCorrectWidget} from '../../../helpers'


export const useCreateTimeWidget = ({
    onSaved,
    replacingWidgetId,
}: {
    onSaved: () => void
    replacingWidgetId: string
}) => {
    const {updateWidget, widget} = useAtomValue(updatingWidgetDataAtom)

    return useCallback(() => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            ...widget,
            type: 'datetime',
        })
        onSaved()
    }, [widget, replacingWidgetId, updateWidget, onSaved])
}
