import {useEffect, useMemo} from 'react'

import {secondsToMilliseconds} from '@bpinc/lib-helpers'

import {useSelectedWallboardQuery, useWallboardsQuery} from '../queries'
import {useEditMode, useWallboardRotation} from '../ui-context/UIContext'
import {useGoToWallboard} from './context/RouterContext'


export function useWallboardRotationEffect() {
    const goToWallboard = useGoToWallboard()
    const {value: isEditMode} = useEditMode()
    const {value: {wallboards: rotationWallboards, enabled: isRotationEnabled, period}} = useWallboardRotation()
    const {data} = useWallboardsQuery()
    const {data: selectedWallboard} = useSelectedWallboardQuery()

    const wallboards = useMemo(() => {
        return (data || [])
            .filter(({id}) => rotationWallboards.includes(id))
            .map(({id}) => id)
    }, [data, rotationWallboards])

    const nextWallboard = useMemo(() => {
        const currentWallboardIndex = wallboards.indexOf(selectedWallboard?.id || '')

        if (currentWallboardIndex <= 0) {
            return wallboards[wallboards.length - 1]
        }
        if (currentWallboardIndex >= wallboards.length - 1) {
            return wallboards[0]
        }
        return wallboards[currentWallboardIndex + 1]
    }, [selectedWallboard?.id, wallboards])

    const isEnabled = !!nextWallboard && !!selectedWallboard && isRotationEnabled && !isEditMode

    useEffect(() => {
        if (!isEnabled) {
            return
        }
        const timer = setTimeout(() => {
            goToWallboard(nextWallboard)
        }, secondsToMilliseconds(period))

        return () => clearTimeout(timer)
    }, [isEnabled, goToWallboard, nextWallboard, period])
}
