import {useCallback, useMemo} from 'react'

import type {BooleanManager} from '@bpinc/lib-helpers'
import {downloadJsonFile, readFromJsonFile} from '@bpinc/lib-helpers'
import {assertDefined, truthy} from '@bpinc/lib-types-helpers'
import type {PopupMenuOption} from '@bpinc/lib-ui-elements'
import type {WallboardConfig} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener, useModalsStateMethods} from '@bpinc/state-modals-context'

import {useSetName, useSetWidgets} from '../../editing-context'
import {LogoutModal, NewWallboardModal, PullFromGlobalModal, WallboardRotationModal} from '../../modals'
import {useCreateWallboardCopyMutation, useDeleteWallboardMutation, useToggleGlobalMutation} from '../../mutations'
import {useSelectedWallboardQuery} from '../../queries'
import {getWallboardWidgets} from '../../selectors'
import {useEditMode, usePageConfig, useShowTitleBar} from '../../ui-context/UIContext'
import {useGoToNewWallboard} from '../context/RouterContext'
import {useChangeUserWidgets} from './useChangeUserWidgets'


export function useMenuOptions(
    showMenuOptions: BooleanManager,
): PopupMenuOption[] {
    const {openModal} = useModalsStateMethods()
    const openLogoutModal = useModalComponentOpener(LogoutModal)
    const openNewWallboardModal = useModalComponentOpener(NewWallboardModal)
    const openWallboardRotationModal = useModalComponentOpener(WallboardRotationModal)
    const openPullFromGlobalModal = useModalComponentOpener(PullFromGlobalModal)
    const {mutate: toggleGlobal} = useToggleGlobalMutation()
    const {data: wallboard} = useSelectedWallboardQuery()
    const showTitleBar = useShowTitleBar()
    const editMode = useEditMode()
    const updateWidgets = useSetWidgets()
    const updateName = useSetName()
    const {t} = useLocalizationState()
    const {mutate: duplicateWallboard} = useCreateWallboardCopyMutation()
    const goToNewWallboard = useGoToNewWallboard()
    const changeUserWidgets = useChangeUserWidgets()
    const pageConfig = usePageConfig()
    const {mutate: deleteWallboard} = useDeleteWallboardMutation()

    const handleDuplicate = useCallback(() => {
        assertDefined(wallboard)
        duplicateWallboard({wallboardId: wallboard.id, isGlobal: wallboard.is_global})
        showMenuOptions.setFalse()
    }, [duplicateWallboard, wallboard, showMenuOptions])

    const handleLogout = useCallback(() => {
        showMenuOptions.setFalse()
        openLogoutModal()
    }, [openLogoutModal, showMenuOptions])

    const createNewWallboard = useCallback(() => {
        showMenuOptions.setFalse()
        openNewWallboardModal()
    }, [openNewWallboardModal, showMenuOptions])

    const openRotationModal = useCallback(() => {
        showMenuOptions.setFalse()
        openWallboardRotationModal()
    }, [openWallboardRotationModal, showMenuOptions])

    const handleExport = useCallback(() => {
        showMenuOptions.setFalse()
        assertDefined(wallboard)
        downloadJsonFile(wallboard, 'config.json')
    }, [wallboard, showMenuOptions])

    const handleImport = useCallback(async () => {
        try {
            const parsedWb = await readFromJsonFile<WallboardConfig>()
            assertDefined(parsedWb)
            goToNewWallboard()
            updateWidgets(getWallboardWidgets(parsedWb))
            updateName(parsedWb.name)
            editMode.setTrue()
        } catch (e) {
            console.error(e)
        } finally {
            showMenuOptions.setFalse()
        }
    }, [goToNewWallboard, updateWidgets, updateName, editMode, showMenuOptions])

    const pushToGlobal = useCallback(() => {
        assertDefined(wallboard)
        toggleGlobal({wallboardId: wallboard?.id, isGlobal: true})
        showMenuOptions.setFalse()
    }, [wallboard, showMenuOptions, toggleGlobal])

    const pullFromGlobal = useCallback(() => {
        openPullFromGlobalModal()
        showMenuOptions.setFalse()
    }, [openPullFromGlobalModal, showMenuOptions])

    const handleDelete = useCallback(() => {
        assertDefined(wallboard)
        openModal({
            title: t.WALLBOARD.confirm_delete_title(wallboard.name),
            actions: {
                ok: {
                    action() {
                        deleteWallboard({wallboardId: wallboard.id})
                    },
                    title: t.WALLBOARD.delete(),
                },
                cancel: {
                    title: t.WALLBOARD.cancel(),
                },
            },
        })
        showMenuOptions.setFalse()
    }, [openModal, deleteWallboard, wallboard, showMenuOptions, t.WALLBOARD])

    const openSelectUserModal = useCallback(() => {
        changeUserWidgets()
        showMenuOptions.setFalse()
    }, [changeUserWidgets, showMenuOptions])

    return useMemo(() => {
        return truthy([
            {children: t.WALLBOARD.menu_new(), onClick: createNewWallboard},
            {children: t.WALLBOARD.menu_edit(), onClick: editMode.setTrue},
            {children: t.WALLBOARD.menu_duplicate(), onClick: handleDuplicate},
            {
                children: t.WALLBOARD.delete(),
                onClick: handleDelete,
                disabled: !wallboard || wallboard?.is_global,
            },
            {
                children: t.WALLBOARD.menu_export(),
                onClick: handleExport,
                disabled: !wallboard,
            },
            {children: t.WALLBOARD.menu_import(), onClick: handleImport},
            {
                children: t.WALLBOARD.menu_push_to_global(),
                onClick: pushToGlobal,
                disabled: !wallboard || wallboard?.is_global,
            },
            {
                children: t.WALLBOARD.menu_pull_from_global(),
                onClick: pullFromGlobal,
                disabled: !wallboard || !wallboard.is_global,
            },
            {children: t.WALLBOARD.menu_rotation(), onClick: openRotationModal},
            !pageConfig?.deniedActions?.changeTheme
                ? showTitleBar.value
                    ? {children: t.WALLBOARD.menu_hide_title_bar(), onClick: showTitleBar.setFalse}
                    : {children: t.WALLBOARD.menu_show_title_bar(), onClick: showTitleBar.setTrue}
                : undefined,
            {children: t.WALLBOARD.menu_user_widgets(), onClick: openSelectUserModal},
            !pageConfig?.deniedActions?.changeTheme
                ? {children: t.WALLBOARD.menu_logout(), onClick: handleLogout}
                : undefined,
        ])
    }, [
        pageConfig,
        handleDuplicate,
        createNewWallboard,
        editMode,
        handleDelete,
        handleExport,
        handleLogout,
        openRotationModal,
        pullFromGlobal,
        pushToGlobal,
        showTitleBar.setFalse,
        showTitleBar.setTrue,
        showTitleBar.value,
        t.WALLBOARD,
        wallboard,
        openSelectUserModal,
        handleImport,
    ])
}
