import type {ReactElement} from 'react'
import React, {useMemo, useState} from 'react'
import {noop} from 'lodash-es'
import {createContext, useContextSelector} from 'use-context-selector'

import {useLocalStorageForState} from '@bpinc/ad-local-settings-context'
import type {BooleanManager} from '@bpinc/lib-helpers'
import {initialBooleanManager, useBoolean} from '@bpinc/lib-helpers'
import type {StateSetter} from '@bpinc/lib-types-helpers'

import type {WallboardPageConfig, WallboardRotationType} from '../types'


interface UIContext {
    config?: WallboardPageConfig
    darkTheme: BooleanManager
    editMode: BooleanManager
    showTitleBar: BooleanManager
    rotation: {
        value: WallboardRotationType
        setValue: StateSetter<WallboardRotationType>
    }
}

const initialValue: UIContext = {
    config: undefined,
    darkTheme: initialBooleanManager(),
    editMode: initialBooleanManager(),
    showTitleBar: initialBooleanManager(true),
    rotation: {
        value: {
            enabled: false,
            period: 0,
            wallboards: [],
        },
        setValue: noop,
    },
}
const UIContext = createContext<UIContext>(initialValue)

UIContext.displayName = 'WallboardUIContext'

type UIProviderProps = {
    children: ReactElement
}

export const UIProvider: React.FC<UIProviderProps> = ({
    children,
}) => {
    const darkTheme = useBoolean()
    const editMode = useBoolean()
    const showTitleBar = useBoolean(true)
    const [rotationValue, setRotationValue] = useState<WallboardRotationType>(initialValue.rotation.value)
    const rotation = useMemo<UIContext['rotation']>(() => ({value: rotationValue, setValue: setRotationValue}), [rotationValue])

    const value = useMemo<UIContext>(() => ({
        darkTheme,
        editMode,
        showTitleBar,
        rotation,
    }), [darkTheme, editMode, rotation, showTitleBar])

    useLocalStorageForState(
        darkTheme.value,
        'adWallboardDarkTheme',
        darkTheme.toggle,
        window.matchMedia?.('(prefers-color-scheme: dark)').matches,
    )

    useLocalStorageForState(
        editMode.value,
        'adWallboardEditMode',
        editMode.toggle,
        initialValue.editMode.value,
    )

    useLocalStorageForState(
        showTitleBar.value,
        'adWallboardShowTitleBar',
        showTitleBar.toggle,
        initialValue.showTitleBar.value,
    )

    useLocalStorageForState(
        rotation.value,
        'adWallboardShowRotation',
        rotation.setValue,
        initialValue.rotation.value,
    )

    return (
        <UIContext.Provider value={value}>
            {children}
        </UIContext.Provider>
    )
}

export const useDarkTheme = () => useContextSelector(UIContext, value => value.darkTheme)
export const usePageConfig = () => useContextSelector(UIContext, value => value.config)
export const useEditMode = () => useContextSelector(UIContext, value => value.editMode)
export const useShowTitleBar = () => useContextSelector(UIContext, value => value.showTitleBar)
export const useWallboardRotation = () => useContextSelector(UIContext, value => value.rotation)
