import {getMomentUTC, MS_IN_DAY, MS_IN_HOUR, MS_IN_SECOND} from '@bpinc/lib-helpers'

import type {StatsFormatType} from './types'


export function formatStatValue(value: string | number | undefined, format: StatsFormatType | undefined) {
    if (!format) {
        return value?.toString() || ''
    }
    switch (format) {
        case 'percent':
            return presentFormatter(value)
        case 'duration': {
            return durationFormatter(value)
        }
    }
}


function durationFormatter(value: string | number | undefined) {
    if (!value || value === '') {
        return ''
    }

    const resultInMs = parseInt(value.toString()) * MS_IN_SECOND
    if (resultInMs >= MS_IN_DAY) {
        return `${Math.floor(resultInMs / MS_IN_DAY)}d ${getMomentUTC(resultInMs, 'HH:mm:ss')}`
    } else if (resultInMs >= MS_IN_HOUR) {
        return getMomentUTC(resultInMs, 'HH:mm:ss')
    } else {
        return getMomentUTC(resultInMs, 'mm:ss')
    }
}

function presentFormatter(value: string | number | undefined = 0) {
    if (!value || value === '') {
        return ''
    }
    const numberValue = parseInt(value.toString())

    if (numberValue > 0 && numberValue < 1) {
        return `${numberValue * 100}%`
    }
    return `${numberValue}%`
}
