import type {SupervisorWidgetConfigGauge} from '@bpinc/lib-widgets-urls'


interface Options {
    value: number
    config: SupervisorWidgetConfigGauge
    inverted: boolean
    getFormattedValue: (value: number) => string
}
export function gaugeChartOptions({value, config, inverted, getFormattedValue}: Options): Highcharts.Options {
    return {
        chart: {
            type: 'solidgauge',
            animation: false,
            backgroundColor: undefined,
        },
        title: {
            text: `<span id="gaugeValue" style="font-size:25px;color: var(--wallboard-widget-gauge-color)">${getFormattedValue(value)}</span>`,
            align: 'center',
            verticalAlign: 'middle',
        },
        pane: {
            center: ['50%', '60%'],
            size: '100%',
            startAngle: -120,
            endAngle: 120,
            background: [{
                backgroundColor: '#EEE',
                innerRadius: '85%',
                outerRadius: '100%',
                shape: 'arc',
            }],

        },
        tooltip: {
            enabled: false,
        },
        yAxis: {
            min: 0,
            max: getMaxPositions(config, inverted),
            title: {
                y: -70,
                text: undefined,
            },
            stops: getGaugeStops(config, inverted),
            lineWidth: 0,
            minorTickInterval: null,
            tickAmount: 2,
            tickPositions: getTickPositions(config, inverted),
            endOnTick: false,
            labels: {
                y: 25,
                style: {
                    color: 'var(--wallboard-widget-gauge-color)',
                },
                padding: 5,
                formatter: function(this: Highcharts.AxisLabelsFormatterContextObject) {
                    return getFormattedValue(this.value)
                },
            },
        },
        credits: {
            enabled: false,
        },
        plotOptions: {
            solidgauge: {
                dataLabels: {
                    y: -30,
                    borderWidth: 0,
                    useHTML: true,
                    allowOverlap: true,
                },
                innerRadius: '85%',
            },
        },
        series: [{
            type: 'solidgauge',
            data: [value],
            dataLabels: {
                enabled: false,
            },

        }],
        exporting: {enabled: false},
    }
}


function getGaugeStops({alertThreshold, warningThreshold, maxThreshold}: SupervisorWidgetConfigGauge, inverted: boolean): [number, string][] {
    if (inverted) {
        return [
            [alertThreshold / warningThreshold, '#DF5353'], // red
            [alertThreshold / warningThreshold + (1 - alertThreshold / warningThreshold) / 2, '#DDDF0D'], // yellow
            [1, '#55BF3B'], // green
        ]
    }
    return [
        [(warningThreshold / maxThreshold) / 2, '#55BF3B'], // green
        [warningThreshold / maxThreshold, '#DDDF0D'], // yellow
        [alertThreshold / maxThreshold, '#DF5353'], // red
    ]
}

function getTickPositions(config: SupervisorWidgetConfigGauge, inverted: boolean) {
    return inverted ? [0, config.warningThreshold] : [0, config.maxThreshold]
}

function getMaxPositions(config: SupervisorWidgetConfigGauge, inverted: boolean) {
    return inverted ? config.warningThreshold : config.maxThreshold
}

