import React from 'react'

import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {Options} from './Options'


type EmptyWidgetSettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
}

export const EmptyWidgetSettingsModal: React.FC<EmptyWidgetSettingsModalProps> = ({
    replacingWidgetId,
    onClose,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()
    return (
        <Modal
            flex
            data-id="settings-widgets"
            {...commonModalProps}
            size="small"
            onClose={onClose}
            className="max-h-[500px] flex-col"
        >
            <Modal.Header role="heading">
                { t.WALLBOARD_WIDGET_EMPTY.name() }
            </Modal.Header>
            <Modal.Content className="overflow-auto">
                <Options
                    replacingWidgetId={replacingWidgetId}
                    onSaved={onClose}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton enabledInOffline onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
