import React from 'react'

import {SmButtonGroup} from '@bpinc/lib-external-ui-elements'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'

import {useFontScale, useIsSaving, useSetFontScale} from '../../../editing-context'


const MAX_FONT_SCALE = 7
const MIN_FONT_SCALE = 1

export const FontScale: React.FC = () => {
    const fontScale = useFontScale()
    const isSaving = useIsSaving()
    const setFontScale = useSetFontScale()

    return (
        <SmButtonGroup className="ml-6 flex items-center">
            <SemanticButton
                disabled={fontScale <= MIN_FONT_SCALE || isSaving}
                onClick={() => setFontScale(curFontScale => {
                    if (curFontScale <= MIN_FONT_SCALE) {
                        return MIN_FONT_SCALE
                    }
                    return curFontScale - 1
                })}
                className={tcn(
                    'm-0 h-8 w-8 p-0',
                    'flex items-center justify-center bg-white',
                    'border border-solid border-main-border',
                )}
            >
              A
            </SemanticButton>
            <SemanticButton
                disabled={fontScale >= MAX_FONT_SCALE || isSaving}
                onClick={() => setFontScale(curFontScale => {
                    if (curFontScale >= MAX_FONT_SCALE) {
                        return MAX_FONT_SCALE
                    }
                    return curFontScale + 1
                })}
                className={tcn(
                    'm-0 h-8 w-8 p-0',
                    'flex items-center justify-center bg-white',
                    'border border-solid border-main-border',
                )}
            >
              A+
            </SemanticButton>
        </SmButtonGroup>
    )
}
