import React, {useCallback} from 'react'

import {assertDefined} from '@bpinc/lib-types-helpers'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useCreateWallboardCopyMutation, useToggleGlobalMutation} from '../mutations'
import {useSelectedWallboardQuery} from '../queries'


export const PullFromGlobalModal: React.FC<CommonModalProps> = ({onClose, ...props}) => {
    const {t} = useLocalizationState()
    const {data: wallboard} = useSelectedWallboardQuery()
    const {mutate: toggleGlobal} = useToggleGlobalMutation()
    const {mutate: createWallboardCopy} = useCreateWallboardCopyMutation()

    const pullFromGlobal = useCallback(() => {
        assertDefined(wallboard)
        toggleGlobal({wallboardId: wallboard?.id, isGlobal: false})
        onClose()
    }, [onClose, wallboard, toggleGlobal])

    const handleDuplicate = useCallback(() => {
        assertDefined(wallboard)
        createWallboardCopy({wallboardId: wallboard?.id, isGlobal: false})
        onClose()
    }, [createWallboardCopy, onClose, wallboard])

    return (
        <Modal
            {...props}
            onClose={onClose}
            size="tiny"
            className="max-w-full"
            aria-labelledby="pullFromGlobalModalHeader"
        >
            <Modal.Header role="heading" id="pullFromGlobalModalHeader">
                { t.WALLBOARD.pull_from_global_modal_title()}
            </Modal.Header>
            <Modal.Actions>
                <SemanticButton onClick={pullFromGlobal}>
                    {t.WALLBOARD.pull()}
                </SemanticButton>
                <SemanticButton onClick={handleDuplicate}>
                    {t.WALLBOARD.duplicate()}
                </SemanticButton>
                <SemanticButton onClick={() => onClose()} primary enabledInOffline>
                    {t.WALLBOARD.cancel()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
