import type {ReactElement, ReactFragment} from 'react'
import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'

import {useDocumentTitleEffect} from '../layout/useDocumentTitleEffect'
import {useWallboardRotationEffect} from '../layout/useWallboardRotationEffect'
import {useDarkTheme} from '../ui-context/UIContext'

import styles from './page.module.less'


export const PageEffects: FCWithChildren<ReactElement | ReactFragment> = ({children}) => {
    const darkTheme = useDarkTheme()

    useWallboardRotationEffect()
    useDocumentTitleEffect()


    return (
        <div
            className={tcn(
                styles.page,
                {
                    'dark': darkTheme.value,
                },
            )}
        >
            {children}
        </div>
    )
}
