import React, {useMemo} from 'react'

import {Chart} from '@bpinc/lib-highcharts'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigSummary} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {pieChartOptions} from '../../charts'
import type {WidgetProps} from '../../types'


export const SummaryWidget: React.FC<WidgetProps<SupervisorWidgetConfigSummary>> = ({
    config,
    subscriptionData,
}) => {
    const {t} = useLocalizationState()
    const summaryData = useMemo(() => {
        if (!subscriptionData) {
            return undefined
        }

        const summaryConfigId = config.subscription.req.columns.find(
            c => c.statName === 'acd_state',
        )?.id

        if (!summaryConfigId) {
            return undefined
        }

        const states = subscriptionData?.agent_grids?.[0].values?.map(
            value => value[+summaryConfigId],
        )

        const keys = ['busy', 'ready', 'after_call_work', 'not_ready'] as const

        const values = states?.reduce<number[]>(
            (acc, state) => {
                const index = keys.findIndex(key => key === state)
                if (index !== -1) {
                    acc[index] = acc[index] + 1
                }
                return acc
            },
            Array.from({length: keys.length}, () => 0),
        )

        if (!values?.length) {
            return undefined
        }

        return {
            values,
            names: keys.map(key => t.WALLBOARD_AGENT_STATES[key]()),
        }
    }, [
        config.subscription.req.columns,
        subscriptionData,
        t.WALLBOARD_AGENT_STATES,
    ])

    const options = useMemo(() => {
        if (!summaryData) {
            return undefined
        }

        return pieChartOptions({
            names: summaryData.names,
            values: summaryData.values,
            options: {
                legend: {
                    align: 'right',
                    verticalAlign: 'middle',
                    labelFormat: '{y} {name}',
                    layout: 'vertical',
                },
                colors: [
                    'var(--wallboard-agent-busy-state-color)',
                    'var(--wallboard-agent-ready-state-color)',
                    'var(--wallboard-agent-acw-state-color)',
                    'var(--wallboard-agent-not-ready-state-color)',
                ],
                tooltip: {
                    pointFormat: '<span style="color:{point.color}">\u25CF</span> {point.name} {point.y}',
                    headerFormat: '',
                },
            },
        })
    }, [summaryData])


    return (
        <WithNoData noData={!summaryData?.values.length}>
            <Chart options={options} />
        </WithNoData>
    )
}
