export const createEmptyColumn = (currentLength: number, type: 'agent_grid' | 'service_grid'): ColumnFieldType => {
    return {
        id: String(currentLength + 1),
        statName: type === 'agent_grid' ? 'first_last_name' : 'name',
        dir: chooseDir(currentLength, type),
        coltitle: '',
    }
}

const chooseDir = (currentLength: number, type: 'agent_grid' | 'service_grid'): ColumnFieldType['dir'] => {
    if (currentLength === 0 && type === 'agent_grid') {
        return 'DESC'
    }
    if (currentLength === 0 && type === 'service_grid') {
        return 'ASC'
    }
    return 'EMPT'
}

export type ColumnFieldType = {
    id?: string
    statName?: string
    dir?: 'DESC' | 'ASC' | 'EMPT'
    coltitle?: string
}

export type ColumnsFiledType = {
    columns: ColumnFieldType[]
}

