import React from 'react'

import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigSatisfaction} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {WidgetProps} from '../../types'
import {SatisfactionChart} from './SatisfactionChart'
import {useSatisfactionOption} from './useSatisfactionOption'


export const SatisfactionWidget: React.FC<WidgetProps<SupervisorWidgetConfigSatisfaction>> = (
    {
        config,
        subscriptionData,
    },
) => {
    const {t} = useLocalizationState()
    const resolutionOptions = useSatisfactionOption('agent_fcr', config, subscriptionData?.agent_grids, t.WALLBOARD_STATS_SINGLE.agent_fcr_name())
    const netPromoterScoreOptions = useSatisfactionOption('agent_nps', config, subscriptionData?.agent_grids, t.WALLBOARD_STATS_SINGLE.agent_nps_name())
    const demeanorOptions = useSatisfactionOption('agent_csat', config, subscriptionData?.agent_grids, t.WALLBOARD_STATS_SINGLE.agent_csat_name())

    return (
        <WithNoData
            className="h-full"
            noData={!subscriptionData}
        >
            <SatisfactionChart
                options={resolutionOptions}
                className="left-0 top-1/2"
            />
            <SatisfactionChart
                options={netPromoterScoreOptions}
                className="left-1/2 top-1/2"
            />
            <SatisfactionChart
                options={demeanorOptions}
                className="bottom-1/4 left-1/4 top-0"
            />
        </WithNoData>
    )
}
