import React, {useCallback} from 'react'

import {Button, Svg} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'

import type {WidgetViewOption} from '../../types'
import {useHandleWidgetSelect} from './useHandleWidgetSelect'


type OptionProps = {
    option: WidgetViewOption
    replacingWidgetId: string
    onSaved: () => void
}

export const Option: React.FC<OptionProps> = ({
    option,
    replacingWidgetId,
    onSaved,
}) => {
    const {t} = useLocalizationState()

    const selectWidget = useHandleWidgetSelect({
        onSaved,
        replacingWidgetId,
    })

    const handleClick = useCallback((e: React.SyntheticEvent) => {
        e.preventDefault()
        selectWidget(option.type)
    }, [option.type, selectWidget])

    const title = t.WALLBOARD_WIDGET_EMPTY[option.type]()

    return (
        <Button
            className="flex max-h-[115px] max-w-[90px] flex-col items-center justify-start p-[5px] opacity-70 transition-all hover:opacity-100"
            onClick={handleClick}
        >
            <Svg
                className="flex h-[50px] w-[50px]"
                src={option.image}
            />

            <div className="mt-2 text-center font-bold ">
                {title}
            </div>
        </Button>
    )
}
