import React from 'react'

import type {WidgetViewOption} from '../types'
import {Option} from './Option'


interface WidgetOptionsGroupProps {
    title: string
    widgets: WidgetViewOption[]
    replacingWidgetId: string
    onSaved: () => void
}

export const WidgetOptionsGroup: React.FC<WidgetOptionsGroupProps> = ({title, widgets, replacingWidgetId, onSaved}) => {
    return (
        <>
            <h4>{title}</h4>
            <div className="mb-5 grid grid-cols-4 gap-5">
                {widgets.map(it => (
                    <Option
                        key={it.type}
                        option={it}
                        replacingWidgetId={replacingWidgetId}
                        onSaved={onSaved}
                    />
                ))}
            </div>
        </>
    )
}
