import {useMutation, useQueryClient} from '@tanstack/react-query'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {WallboardConfig} from '@bpinc/lib-widgets-urls'
import {WallboardApiServices} from '@bpinc/lib-widgets-urls'

import {useGoToWallboard} from '../layout/context/RouterContext'
import {queryKey} from '../queries'


interface CreateWallboardCopy {
    wallboardId: string
    isGlobal?: boolean
}

export function useCreateWallboardCopyMutation() {
    const queryClient = useQueryClient()
    const goToWallboard = useGoToWallboard()
    return useMutation({
        async mutationFn({wallboardId, isGlobal}: CreateWallboardCopy) {
            const existingWallboard = queryClient.getQueryData<WallboardConfig>(queryKey.wallboard(wallboardId))

            if (!existingWallboard) {
                return
            }

            return WallboardApiServices.createWallboard({
                requestBody: {
                    ...existingWallboard,
                    name: `${existingWallboard.name}(1)`,
                    is_global: isGlobal ?? existingWallboard?.is_global,
                },
            })
        },
        onSuccess(newWallboard) {
            assertDefined(newWallboard)

            queryClient.invalidateQueries({queryKey: queryKey.wallboards()}).then(() => {
                goToWallboard(newWallboard?.id)
            })

        },

    })
}
