import React, {useCallback, useMemo, useState} from 'react'

import {DoubleColumnSelect, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useTeamsQuery} from '../queries'


type TeamsSelectModalProps = CommonModalProps & {
    teams: string[]
    onSelect: (teams: string[]) => void
}

export const TeamsSelectModal: React.FC<TeamsSelectModalProps> = ({teams, onSelect, onClose, ...commonModalProps}) => {
    const {t} = useLocalizationState()
    const [selectedTeams, setSelectedTeams] = useState(teams)

    const handleOnComplete = useCallback(() => {
        onSelect(selectedTeams)
        onClose()
    }, [onClose, onSelect, selectedTeams])

    const {data: allTeams} = useTeamsQuery()

    const options = useMemo(() => {
        return (allTeams || []).map(team => ({
            value: team.id,
            text: team.name,
        }))
    }, [allTeams])

    return (
        <Modal
            onClose={onClose}
            size="tiny"
            className="max-w-full"
            {...commonModalProps}
        >
            <Modal.Header>
                {t.WALLBOARD_TEAMS_MODAL.title()}
            </Modal.Header>
            <Modal.Content>
                <DoubleColumnSelect
                    className="max-h-[70vh] min-h-[280px]"
                    value={selectedTeams}
                    setValue={setSelectedTeams}
                    options={options}
                    availableTitle={t.GLOBAL.available()}
                    selectedTitle={t.GLOBAL.included()}
                    noSearch
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton
                    enabledInOffline
                    onClick={() => onClose()}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    primary
                    onClick={handleOnComplete}
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
