import {useCallback} from 'react'

import {assertDefined} from '@bpinc/lib-types-helpers'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {useWidgets} from '../../editing-context'
import {useUpdateWallboardWidgetsMutation} from '../../mutations'
import {useSelectedWallboardQuery} from '../../queries'
import {SelectUserModal} from '../../settings-modals/SelectUserModal'
import {userWidgetTypes} from '../../settings-modals/types'
import {useWidgetOptions} from '../../settings-modals/useWidgetOptions'


export const useChangeUserWidgets = () => {
    const {mutateAsync: updateWallboardWidgets} = useUpdateWallboardWidgetsMutation()
    const widgetOptions = useWidgetOptions()
    const {data: selectedWallboard} = useSelectedWallboardQuery()
    const currentWidgets = useWidgets()
    const openSelectUserModal = useModalComponentOpener(SelectUserModal)

    return useCallback(() => {
        openSelectUserModal({
            onSelected: async (userId: string, userName: string) => {
                assertDefined(selectedWallboard)
                const widgets: SupervisorWidget[] = currentWidgets.map(currentWidget => {
                    const type = userWidgetTypes.find(item => item === currentWidget.type)

                    if (!type) {
                        return currentWidget
                    }

                    return {
                        ...currentWidget,
                        type,
                        title: widgetOptions[type].title(userName),
                        subscription: widgetOptions[type].subscription(currentWidget.id, userId),
                        user_id: userId,
                        userName,
                    } as SupervisorWidget
                })

                if (widgets.length && selectedWallboard) {
                    await updateWallboardWidgets({
                        wallboardId: selectedWallboard.id,
                        updatedWallboardWidgets: widgets,
                    })
                }
            },
        })
    }, [currentWidgets, openSelectUserModal, selectedWallboard, updateWallboardWidgets, widgetOptions])
}

