/* tslint:disable */
/* eslint-disable */
import type { AgendaResponse } from '../models/AgendaResponse';
import type { StatsAPIAgent } from '../models/StatsAPIAgent';
import type { StatsAPIService } from '../models/StatsAPIService';
import type { StatsAPITeam } from '../models/StatsAPITeam';
import type { StatsAPITimeSeriesData } from '../models/StatsAPITimeSeriesData';
import type { SupervisorWidgetsSubscriptionData } from '../models/SupervisorWidgetsSubscriptionData';
import type { SupervisorWidgetsSubscriptionRequest } from '../models/SupervisorWidgetsSubscriptionRequest';
import type { TenantAgentConfiguration } from '../models/TenantAgentConfiguration';

import type { CancelablePromise } from '../core/CancelablePromise';
import { OpenAPI } from '../core/OpenAPI';
import { request as __request } from '../core/request';

export class SupervisorWidgetsApiServices {

    /**
     * Get a collection of team names and IDs
     * Returns a collection of team names and IDs for all teams in the system.
     * @returns StatsAPITeam OK
     * @throws ApiError
     */
    public static getTeams(): CancelablePromise<Array<StatsAPITeam>> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/teams',
        });
    }

    /**
     * Delete supervisor widgets subscription
     * @throws ApiError
     */
    public static deleteSupervisorWidgetsSubscription(): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'DELETE',
            url: '/statsapi/subscription/active',
        });
    }

    /**
     * Retrieve user's agenda
     * @returns AgendaResponse OK
     * @throws ApiError
     */
    public static getAgenda({
requestBody,
}: {
/**
 * List of user IDs
 */
requestBody: Array<string>,
}): CancelablePromise<AgendaResponse> {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/statsapi/subscription/agenda',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * Create supervisor widgets subscription
     * @throws ApiError
     */
    public static createSupervisorWidgetsSubscription({
requestBody,
}: {
requestBody: SupervisorWidgetsSubscriptionRequest,
}): CancelablePromise<void> {
        return __request(OpenAPI, {
            method: 'POST',
            url: '/statsapi/subscription',
            body: requestBody,
            mediaType: 'application/json',
        });
    }

    /**
     * Get supervisor widgets subscription data
     * @returns SupervisorWidgetsSubscriptionData Wallboard subscription data
     * @throws ApiError
     */
    public static getSupervisorWidgetsSubscriptionData(): CancelablePromise<SupervisorWidgetsSubscriptionData> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/subscription/data',
        });
    }

    /**
     * @returns any 
     * @throws ApiError
     */
    public static getTenantConfiguration(): CancelablePromise<{
agentConfiguration: TenantAgentConfiguration;
}> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/tenant_configuration',
        });
    }

    /**
     * @returns StatsAPIAgent List of agents
     * @throws ApiError
     */
    public static getAgents(): CancelablePromise<Array<StatsAPIAgent>> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/agents',
        });
    }

    /**
     * @returns StatsAPIService List of services
     * @throws ApiError
     */
    public static getServices(): CancelablePromise<Array<StatsAPIService>> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/cfg/services',
        });
    }

    /**
     * @returns StatsAPITimeSeriesData Time series widget data
     * @throws ApiError
     */
    public static getTimeSeriesData({
measurement,
statistic,
start,
end,
timeInterval,
}: {
measurement: string,
statistic: string,
start: string,
end: string,
timeInterval: string,
}): CancelablePromise<StatsAPITimeSeriesData> {
        return __request(OpenAPI, {
            method: 'GET',
            url: '/statsapi/timeseries/{measurement}?statistic={statistic}&start={start}&end={end}&interval={timeInterval}',
            path: {
                'measurement': measurement,
                'statistic': statistic,
                'start': start,
                'end': end,
                'timeInterval': timeInterval,
            },
        });
    }

}
