import {atom} from 'jotai'
import {noop} from 'lodash-es'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'


type AlertNotifyType = {
    start: () => { stop: () => void }
}

export const audioNotifyAtom = atom<AlertNotifyType | null>(null)
export const updatingWidgetDataAtom = atom<{
    widget: SupervisorWidget | null
    updateWidget: (widget: SupervisorWidget) => void
}>({
            widget: null,
            updateWidget: noop,
        })
