import React, {useCallback} from 'react'
import {useWatch} from 'react-hook-form'
import {defaultAnimateLayoutChanges, useSortable} from '@dnd-kit/sortable'
import {CSS} from '@dnd-kit/utilities'

import type {SmICONS} from '@bpinc/lib-external-ui-elements'
import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {HiddenField, SelectField, TextField} from '@bpinc/lib-forms-ui'
import {tcn} from '@bpinc/lib-tailwind-classnames'
import {SemanticButton} from '@bpinc/lib-ui-elements'

import {useStatsOptions} from '../../../../stats/useStatsOptions'
import {ColumnFieldRow} from './ColumnFieldRow'
import type {ColumnFieldType, ColumnsFiledType} from './index'


interface ColumnProps {
    id: string
    index: number
    remove: (ind: number) => void
    type: 'agent_grid' | 'service_grid'
}

export const ColumnField = <TValues, >({
    id,
    index,
    control,
    setValue,
    remove,
    type,
}: ColumnProps & FieldControlProps<TValues, ColumnsFiledType, 'control' | 'setValue'>) => {
    const onRemove = useCallback(() => {
        remove(index)
    }, [remove, index])

    const options = useStatsOptions(type === 'agent_grid' ? 'statsAgent' : 'statsService')

    const {
        attributes,
        listeners,
        setNodeRef,
        transform,
        transition,
        setActivatorNodeRef,
        isDragging,
    } = useSortable({
        id,
        animateLayoutChanges: defaultAnimateLayoutChanges,
    })

    const style = {
        transform: CSS.Transform.toString(transform),
        transition,
    }

    const dir = useWatch({
        control,
        name: `columns.${index}.dir`,
    })

    const handleChangeSort = useCallback(() => {
        setValue(`columns.${index}.dir`,
            dir === 'DESC'
                ? 'ASC'
                : dir === 'EMPT'
                    ? 'DESC'
                    : 'EMPT',
        )
    }, [dir, setValue, index])

    return (
        <ColumnFieldRow
            ref={setNodeRef}
            style={style}
            {...attributes}
            className={tcn(
                'group/stat-row bg-main-bg py-1', {
                    'z-10': isDragging,
                },
            )}
        >
            <HiddenField name={`columns.${index}.id`} control={control} />
            <SelectField search options={options} name={`columns.${index}.statName`} control={control} />
            <div className="self-center justify-self-center">
                <SemanticButton
                    onClick={handleChangeSort}
                    transparent
                    className={tcn('mr-0 flex items-center text-[20px]',
                        '[&>i]:flex [&>i]:items-center [&>i]:justify-center [&>i]:outline-none',
                        'opacity-0 hover:opacity-100',
                        {'opacity-100': dir !== 'EMPT'})}
                    icon={getSortIcon(dir)}
                    enabledInOffline
                />
                <HiddenField name={`columns.${index}.dir`} control={control} />
            </div>
            <TextField fluid name={`columns.${index}.coltitle`} control={control} />
            <div className="flex self-center justify-self-center">
                <SemanticButton
                    onClick={onRemove}
                    enabledInOffline
                    transparent
                    className="hidden items-center text-lg group-hover/stat-row:flex
                    [&>i]:flex [&>i]:items-center [&>i]:justify-center"
                    icon="x"
                />
                <SemanticButton
                    ref={setActivatorNodeRef}
                    enabledInOffline
                    transparent
                    className="hidden items-center text-lg group-hover/stat-row:flex
                    [&>i]:flex [&>i]:items-center [&>i]:justify-center"
                    icon="bars"
                    {...listeners}
                />
            </div>
        </ColumnFieldRow>
    )
}

function getSortIcon(sort: ColumnFieldType['dir']): SmICONS {
    if (sort === 'ASC') {
        return 'caret up'
    }
    if (sort === 'DESC') {
        return 'caret down'
    }
    return 'circle outline'
}
