import React, {useCallback} from 'react'
import {useController} from 'react-hook-form'

import type {FieldControlProps} from '@bpinc/lib-forms-base'
import {SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {ServicesSelectModal} from '../ServicesSelectModal'


export const ServicesSelect = <TValues, >({control, setValue}: FieldControlProps<TValues, {serviceIds: string[]}, 'control' | 'setValue'>) => {
    const {
        field: {
            value: serviceIds,
        },
    } = useController({
        name: 'serviceIds',
        control,
    })

    const openServicesSelectModal = useModalComponentOpener(ServicesSelectModal)
    const handleSelectServices = useCallback(() => {
        openServicesSelectModal({
            services: serviceIds,
            onSelect: updatedServices => {
                setValue('serviceIds', updatedServices)
            },
        })
    }, [openServicesSelectModal, serviceIds, setValue])

    const {t} = useLocalizationState()
    return (
        <SemanticButton
            basic
            onClick={handleSelectServices}
            enabledInOffline
            className="mb-4"
        >
            {t.WALLBOARD_WIDGETS_GRIDS.services()}
        </SemanticButton>
    )
}
