import React, {useMemo} from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'
import {WithNoData} from '@bpinc/lib-ui-elements'
import type {SupervisorWidgetConfigGrid} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {useGetStatsConfig} from '../../stats/useGetStatsConfig'
import type {WidgetProps} from '../../types'
import {processGridData} from './utils'


function tdClassName(isEven = true) {
    return tcn(
        'px-[5px] py-1 first:sticky first:left-0 first:z-10',
        isEven
            ? 'bg-[--wallboard-table-even-row-bg:]'
            : 'bg-[--wallboard-table-odd-row-bg]',
    )
}

export const GridWidget: React.FC<WidgetProps<SupervisorWidgetConfigGrid>> = ({
    config,
    subscriptionData,
}) => {
    const {t} = useLocalizationState()
    const getStatsConfig = useGetStatsConfig()
    const subscriptionType = config.subscription.type

    const columnsList = useMemo(() => {
        if (!subscriptionData) return

        switch (subscriptionType) {
            case 'agent_grid':
                return processGridData({
                    config,
                    t,
                    getStatsConfig,
                    dataGrids: subscriptionData?.agent_grids,
                })
            case 'service_grid':
                return processGridData({
                    config,
                    t,
                    getStatsConfig,
                    dataGrids: subscriptionData?.service_grids,
                })
        }
    }, [subscriptionData, subscriptionType, config, t, getStatsConfig])
    return (
        <WithNoData
            noData={!columnsList?.length}
            className="!justify-start"
        >
            <div className="h-full w-full overflow-auto">
                <table className="w-full border-t border-[--wallboard-grid-border] text-left text-[1.7vm]">
                    <thead>
                        {config.columtitle && (
                            <tr className="border-b border-[--wallboard-grid-border] px-[5px] font-bold text-[1.7vm]">
                                {config.subscription.req.columns.map(
                                    ({coltitle, id}) => (
                                        <th
                                            className={tdClassName()}
                                            key={id}
                                        >
                                            {coltitle}
                                        </th>
                                    ),
                                )}
                            </tr>
                        )}
                    </thead>
                    <tbody>
                        {columnsList?.map((elems, i) => (
                            <tr
                                key={elems[0].id}
                            >
                                {elems.map(data => (
                                    <td
                                        className={tdClassName(i % 2 === 1)}
                                        key={data.id}
                                    >
                                        {data.value}
                                    </td>
                                ))}
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </WithNoData>
    )
}
