import React from 'react'

import {sanitizeHtml} from '@bpinc/lib-html-utils'
import type {SupervisorWidgetConfigNews} from '@bpinc/lib-widgets-urls'

import type {WidgetProps} from '../../types'


export const NewsWidget: React.FC<WidgetProps<SupervisorWidgetConfigNews>> = ({
    subscriptionData,
}) => {
    return (
        <div className="h-full overflow-y-auto p-2">
            {subscriptionData?.chat_messages?.map(msgItem => (
                <div
                    key={msgItem.id}
                    dangerouslySetInnerHTML={{__html: sanitizeHtml(msgItem.content)}}
                />
            ))}
        </div>
    )
}

