import {useCallback} from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'

import {STATS_CONFIG} from './config'
import type {StatsConfigType, StatsConfigTypeKeys, StatsDataType, StatsMessagesDeclarationTypeMapper} from './types'
import {STAT_LOCALIZATION_MAP} from './types'


export function useGetStatsConfig() {
    const {t} = useLocalizationState()

    return useCallback(
        <ConfigType extends StatsConfigTypeKeys, Stat extends keyof StatsConfigType[ConfigType]>
        (type: ConfigType, stat: Stat) => {
            const localizationPart = getLocalizationPart()
            const statData = getStatData()
            return {
                format: statData?.format,
                align: statData?.align,
                alertInverted: statData?.alertInverted,
                name: localizationPart[statData?.localizationKey],
            }

            function getLocalizationPart() {
                const part = t[STAT_LOCALIZATION_MAP[type]]

                return part as StatsMessagesDeclarationTypeMapper<ConfigType>
            }

            function getStatData() {
                return STATS_CONFIG[type][stat] as unknown as StatsDataType<StatsMessagesDeclarationTypeMapper<ConfigType>>
            }
        }, [t])
}

