import {useQuery} from '@tanstack/react-query'

import {MS_IN_DAY} from '@bpinc/lib-helpers'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'

import {queryKey} from './query-keys'
import wallboardTemplates from './wallboard-templates.json'


type WallboardTemplates = {
    name: string
    config: {
        widgets: {
            config: SupervisorWidget
        }[]
    }
}[]

export function useWallboardTemplatesQuery() {
    return useQuery({
        queryFn: () => wallboardTemplates as WallboardTemplates,
        queryKey: queryKey.wallboard_templates(),
        gcTime: MS_IN_DAY,
        staleTime: MS_IN_DAY,
    })
}
