import {highcharts} from '@bpinc/lib-highcharts'


export type DataType = Array<{ name: string; data: Array<{x: number; y: number}> }>

export function getChartConfig(data: DataType, yGridVisible: boolean, xGridVisible: boolean) {
    return {
        chart: {
            type: 'spline',
            // @ts-expect-error: For some reason "svg" property is missing in highcharts typings but exists in the implementation
            animation: highcharts.svg, // don't animate in old IE
            marginRight: 10,
            backgroundColor: '',
        },
        title: {
            text: '',
        },
        xAxis: {
            gridLineWidth: xGridVisible ? 1 : 0,
            gridLineColor: '#808080',
            type: 'datetime',
            tickPixelInterval: 150,
        },
        yAxis: {
            allowDecimals: false,
            gridLineWidth: yGridVisible ? 1 : 0,
            gridLineColor: '#808080',
            title: {
                text: 'Value',
            },
            plotLines: [{
                value: 0,
                width: 1,
                color: '#808080',
            }],
        },
        tooltip: {
            formatter: function(this: any) {
                return '<b>' + this.series.name + '</b><br/>' +
                    highcharts.dateFormat('%Y-%m-%d %H:%M:%S', this.x) + '<br/>' +
                    highcharts.numberFormat(this.y, 2)
            },
        },
        legend: {
            enabled: true,
            itemStyle: {
                'fontFamily': 'Muli',
                'color': 'var(--wallboard-text-secondary)',
            },
        },
        exporting: {
            enabled: false,
        },
        plotOptions: {
            series: {
                marker: {
                    enabled: false,
                },
                turboThreshold: 5000, //larger threshold or set to 0 to disable
            },
        },
        credits: {enabled: false},
        series: data,
    }
}
