import {isArray, mergeWith} from 'lodash-es'


interface PieChartOptions {
    names: string[]
    values: number[]
    formatter?: (this: Highcharts.TooltipFormatterContextObject) => string
    colors?: string[]
    enableDataLabels?: boolean
    options?: Partial<Highcharts.Options>
}

function mergeCustomizer<T>(srcValue: T, objValue: T): T | undefined {
    if (isArray(srcValue)) {
        return objValue
    }
}


export function pieChartOptions({
    names,
    values,
    formatter,
    enableDataLabels = false,
    options = {},
}: PieChartOptions): Highcharts.Options {
    return mergeWith({
        chart: {type: 'pie', backgroundColor: 'transparent'},
        exporting: {enabled: false},
        plotOptions: {
            pie: {
                allowPointSelect: false,
                cursor: 'pointer',
                innerSize: '60%',
                borderColor: 'tranparent',
                dataLabels: {
                    enabled: enableDataLabels,
                    distance: 20,
                    style: {
                        fontWeight: 'normal',
                        fontSize: '1.5vmin',
                        color: 'var(--wallboard-text-secondary)',
                        fontFamily:
                            'Lato, Helvetica Neue, Arial, Helvetica, sans-serif',
                        textOutline: '0',
                    },
                    formatter,
                },
                showInLegend: true,
            },
        },
        title: {text: '', margin: 0},
        credits: {enabled: false},
        colors: ['#BAD027', '#00A6E6'],
        legend: {
            itemStyle: {
                fontWeight: 'normal',
                fontSize: '1.5vmin',
                color: 'var(--wallboard-chart-legend-color)',
            },
            itemHoverStyle: {
                color: 'var(--wallboard-chart-legend-color-hover)',
            },
        },
        tooltip: {
            formatter,
        },
        series: [
            {
                data: names.map((v, i) => ({name: v, y: values[i]})),
                type: 'pie',
            },
        ],
    }, options, mergeCustomizer)
}
