import type {SyntheticEvent} from 'react'
import React, {useCallback, useMemo, useState} from 'react'

import {useSessionState} from '@bpinc/ad-session-context'
import {assertDefined} from '@bpinc/lib-types-helpers'
import type {DropdownItemProps, DropdownProps} from '@bpinc/lib-ui-elements'
import {Dropdown, Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps} from '@bpinc/state-modals-types'

import {useAgentsQuery} from '../queries'
import {FormLabel} from './shared/FormLabel'
import {GridRow} from './shared/GridRow'
import {useHandleCancelWidgetModal} from './useHandleCancelWidgetModal'


type SelectUserModalProps = CommonModalProps & {
    onSelected?: (userId: string, userName: string) => void
    currentWidgetConfig?: SupervisorWidget
}

export const SelectUserModal: React.FC<SelectUserModalProps> = ({
    onClose,
    onSelected,
    currentWidgetConfig,
    ...restProps
}) => {
    const {t} = useLocalizationState()
    const selfUserId = useSessionState('userId')
    assertDefined(selfUserId)

    const [selectedUser, setSelectedUser] = useState<string>(selfUserId)

    const selfUserOption = useMemo(() => {
        return {
            value: selfUserId,
            text: t.WALLBOARD.logged_in_user(),
        }
    }, [selfUserId, t.WALLBOARD])

    const {data: agents, isLoading} = useAgentsQuery()

    const options = useMemo(() => {
        const combinedOptions = (agents ?? []).reduce<DropdownItemProps[]>(
            (acc, agent) => {
                if (agent.id === selfUserOption.value) {
                    return acc
                }

                acc.push({
                    value: agent.id,
                    text: agent.name,
                })

                return acc
            },
            [],
        )

        return [
            selfUserOption,
            ...combinedOptions.sort((a, b) => {
                return typeof a.text === 'string' && typeof b.text === 'string'
                    ? a.text.toLocaleLowerCase().localeCompare(b.text.toLocaleLowerCase())
                    : 0
            }),
        ]
    }, [agents, selfUserOption])

    const handleOk = useCallback(() => {
        const user = agents?.find(({id}) => id === selectedUser)
        if (user) {
            onSelected?.(user.id, user.name)
        }
        onClose()
    }, [agents, onClose, onSelected, selectedUser])

    const handleSelectUser = useCallback((_evt: SyntheticEvent, {value}: DropdownProps) => {
        if (typeof value === 'string') {
            setSelectedUser(value)
        }
    }, [])

    const handleCancelWidgetModal = useHandleCancelWidgetModal({onClose, widget: currentWidgetConfig})

    return (
        <Modal
            aria-labelledby="selectUserModalHeader"
            onClose={onClose}
            size="tiny"
            className="max-w-full"
            {...restProps}
        >
            <Modal.Header role="heading" id="selectUserModalHeader">
                {t.WALLBOARD.agent_select_title()}
            </Modal.Header>
            <Modal.Content>
                <GridRow>
                    <FormLabel text={t.WALLBOARD.agent_select_label()} />
                    <Dropdown
                        onChange={handleSelectUser}
                        value={selectedUser}
                        options={options}
                        search
                        openOnFocus={false}
                        selectOnBlur={false}
                        loading={isLoading}
                        disabled={isLoading}
                    />
                </GridRow>
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton onClick={handleOk} primary>
                    {t.GLOBAL.ok()}
                </SemanticButton>
                <SemanticButton onClick={handleCancelWidgetModal} enabledInOffline>
                    {t.GLOBAL.cancel()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
