import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import type {ModalBase} from '@bpinc/state-modals-types'

import type {SupervisorWidgetTypes} from '../types'
import type {AgentGridWidgetSettingsModalParams} from './AgentGridModal'
import type {AgentStatesModalParams} from './AgentStatesModal'
import type {ImageWidgetSettingsModalParams} from './ImageSettingsModal'
import type {NewsWidgetSettingsModalParams} from './NewsSettingsModal'
import type {ServiceGridWidgetSettingsModalParams} from './ServiceGridModal'
import type {TextWidgetSettingsModalParams} from './TextSettingsModal'
import type {TimeSeriesWidgetSettingsModalParams} from './TimeSeriesModal'


interface UserSelectionModal extends ModalBase {
    type: 'select_user'
    onSelected: (userId: string) => void
}

interface EmptyWidgetSettingsModal extends ModalBase {
    type: 'empty_widget_settings'
    replacingWidgetId: string
}

interface SummaryWidgetSettingsModal extends ModalBase {
    type: 'summary'
    replacingWidgetId: string
}

interface SingleOrGaugeWidgetSettingsModal extends ModalBase {
    type: 'single_or_gauge_widget_settings'
    replacingWidgetId: string
    widgetType: Extract<SupervisorWidgetTypes, 'gauge' | 'single'>
    onSave: (widget: SupervisorWidget) => void
}

interface CopyWidgetModal extends ModalBase {
    type: 'copy_widget'
    replacingWidgetId: string
    onSave: (widget: SupervisorWidget) => void
}

interface ServicesSelectModal extends ModalBase {
    type: 'services_select'
    services: string[]
    onSelect: (services: string[]) => void
}

interface TeamsSelectModal extends ModalBase {
    type: 'teams_select'
    teams: string[]
    onSelect: (teams: string[]) => void
}

interface TeamsSelectModal extends ModalBase {
    type: 'teams_select'
    teams: string[]
    onSelect: (team: string[]) => void
}


type Modal =
    | EmptyWidgetSettingsModal
    | ImageWidgetSettingsModalParams
    | TextWidgetSettingsModalParams
    | NewsWidgetSettingsModalParams
    | CopyWidgetModal
    | UserSelectionModal
    | SummaryWidgetSettingsModal
    | SingleOrGaugeWidgetSettingsModal
    | ServicesSelectModal
    | TeamsSelectModal
    | AgentGridWidgetSettingsModalParams
    | ServiceGridWidgetSettingsModalParams
    | AgentStatesModalParams
    | TimeSeriesWidgetSettingsModalParams


type FormValuesUnion<T extends Modal> = T extends { formValues?: infer V } ? V : never
export type FormValues = FormValuesUnion<Modal>

export const userWidgetTypes = ['dispositions', 'satisfaction', 'my_stats', 'break_time', 'agenda'] as const

export type UserWidgetsType = typeof userWidgetTypes[number]
