import type {ChangeEvent, FC} from 'react'
import React, {useCallback} from 'react'

import type {SmInputOnChangeData} from '@bpinc/lib-external-ui-elements'
import {SmInput} from '@bpinc/lib-external-ui-elements'

import {useIsSaving, useName, useSave, useSetName} from '../../editing-context'
import {useDarkTheme, useEditMode, usePageConfig} from '../../ui-context/UIContext'
import {FontScale} from './FontScale'
import {HeaderButton} from './HeaderButton'


export const EditHeaderContent: FC = () => {
    const name = useName()
    const editMode = useEditMode()
    const pageConfig = usePageConfig()
    const darkTheme = useDarkTheme()
    const isSaving = useIsSaving()
    const setName = useSetName()


    const changeWallboardName = useCallback((_e: ChangeEvent<HTMLInputElement>, {value}: SmInputOnChangeData) => {
        setName(value)
    }, [setName])

    const handleCancel = useCallback(() => {
        editMode.setFalse()
    }, [editMode])

    const save = useSave()
    const handleSubmit = useCallback(() => {
        save()
    }, [save])

    return (
        <>
            <SmInput
                value={name}
                onChange={changeWallboardName}
                className="w-full flex-1"
                disabled={isSaving}
            />
            <section className="ml-8 flex flex-1 items-center justify-end gap-6">
                <FontScale />
                {!pageConfig?.deniedActions?.changeTheme && (
                    <HeaderButton
                        icon={darkTheme.value ? 'sun outline' : 'moon outline'}
                        onClick={darkTheme.toggle}
                    />
                )}
                <div className="flex gap-2">
                    <HeaderButton
                        icon="check"
                        color="green"
                        loading={isSaving}
                        onClick={handleSubmit}
                        className="!ml-2 h-8 w-8 !bg-white p-0"
                    />
                    <HeaderButton
                        icon="close"
                        color="red"
                        onClick={handleCancel}
                        disabled={isSaving}
                        className="h-8 w-8 !bg-white p-0"
                    />
                </div>
            </section>
        </>
    )
}
