import React from 'react'

import {useLocalizationState} from '@bpinc/state-localization-context'

import {standartWidgets, timeSeriesWidgets, userWidgets, widgetsFromOtherWallboard} from './optionsList'
import {WidgetOptionsGroup} from './WidgetOptionsGroup'


const options = [
    {title: 'standardWidgets', widgets: standartWidgets},
    {title: 'userWidgets', widgets: userWidgets},
    {title: 'timeSeriesWidgets', widgets: timeSeriesWidgets},
    {title: 'copyFromExisting', widgets: widgetsFromOtherWallboard},
] as const

export const Options: React.FC<{
    replacingWidgetId: string
    onSaved: () => void
}> = ({replacingWidgetId, onSaved}) => {
    const {t} = useLocalizationState()

    return (
        <div>
            {options.map(option => (
                <WidgetOptionsGroup
                    key={option.title}
                    title={t.WALLBOARD_WIDGET_EMPTY[option.title]()}
                    widgets={option.widgets}
                    replacingWidgetId={replacingWidgetId}
                    onSaved={onSaved}
                />
            ))}
        </div>
    )
}
