import type {ReactElement} from 'react'
import React, {useCallback, useEffect} from 'react'
import {useNavigate, useParams} from 'react-router-dom'
import type {Context} from 'use-context-selector'
import {createContext, useContextSelector} from 'use-context-selector'

import {useBoolean} from '@bpinc/lib-helpers'
import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {assertDefined} from '@bpinc/lib-types-helpers'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {NEW_WALLBOARD_ID} from '../../constants'
import {NewWallboardModal} from '../../modals'
import {useWallboardsQuery} from '../../queries'
import {getNewWallboardRoute, getWallboardRoute} from '../../utils'


interface RouterContextValue{
    setNewWallboardTemplatesSelected: () => void
    goFromRemovedWallboard: (wallboardId: string) => void
    goToWallboard: (wallboardId: string) => void
    goToNewWallboard: () => void
    loading: boolean
}

let RouterContext: Context<RouterContextValue>

export const RouterContextProvider: FCWithChildren<ReactElement> = ({children}) => {
    const navigate = useNavigate()
    const {wallboardId} = useParams()
    const openNewWallboardModal = useModalComponentOpener(NewWallboardModal)

    const {value: newWallboardTemplatesSelected, setTrue: setNewWallboardTemplatesSelected, setFalse: setNewWallboardTemplatesUnselected} = useBoolean(false)

    const {status: wallboardStatus, data: wallboards} = useWallboardsQuery()

    const goToWallboard = (newWallboardId: string) => {
        navigate(getWallboardRoute(newWallboardId))
        setNewWallboardTemplatesUnselected()
    }

    const goToNewWallboard = useCallback(() => {
        setNewWallboardTemplatesSelected()
        navigate(getNewWallboardRoute())
    }, [setNewWallboardTemplatesSelected, navigate])

    useEffect(() => {
        if (wallboardStatus === 'pending') {
            return
        }
        if (!wallboards) {
            return
        }
        if (!wallboardId) {
            const defaultWallboard = wallboards?.find(w => w.is_default) || wallboards?.[0]
            if (defaultWallboard) {
                navigate(getWallboardRoute(defaultWallboard.id))
            } else {
                navigate(getNewWallboardRoute())
            }
            return
        }
        if (wallboardId === NEW_WALLBOARD_ID && !newWallboardTemplatesSelected) {
            openNewWallboardModal()
            return
        }
        if (wallboardId !== NEW_WALLBOARD_ID) {
            const selectedWallboard = wallboards?.find(w => w.id === wallboardId)
            if (!selectedWallboard) {
                openNewWallboardModal()
                goToNewWallboard()
            }
        }
    }, [wallboardId, wallboards, navigate, newWallboardTemplatesSelected, openNewWallboardModal, goToNewWallboard, wallboardStatus])

    const goFromRemovedWallboard = useCallback((deletedWallboardId: string) => {
        assertDefined(wallboards)
        if (deletedWallboardId === wallboardId) {
            const wallboardIndex = wallboards?.findIndex(w => w.id === deletedWallboardId)

            const wallboardToGo = wallboards[(wallboardIndex === wallboards.length - 1) ? 0 : wallboardIndex + 1]
            if (wallboardToGo) {
                navigate(getWallboardRoute(wallboardToGo.id))
            } else {
                navigate(getNewWallboardRoute())
            }
        }
    }, [wallboards, navigate, wallboardId])

    const contextValue = {
        setNewWallboardTemplatesSelected,
        goFromRemovedWallboard,
        goToWallboard,
        goToNewWallboard,
        loading: wallboardStatus === 'pending',
    }

    if (!RouterContext) {
        RouterContext = createContext<RouterContextValue>(contextValue)
        RouterContext.displayName = 'RouterContext'
    }

    return (
        <RouterContext.Provider value={contextValue}>
            {children}
        </RouterContext.Provider>
    )
}

export const useGoFromRemovedWallboard = () => useContextSelector(RouterContext, value => value.goFromRemovedWallboard)
export const useGoToWallboard = () => useContextSelector(RouterContext, value => value.goToWallboard)
export const useGoToNewWallboard = () => useContextSelector(RouterContext, value => value.goToNewWallboard)
export const useIsLoading = () => useContextSelector(RouterContext, value => value.loading)
