import React from 'react'

import type {SupervisorWidgetConfigBase} from '@bpinc/lib-widgets-urls'

import type {WidgetProps} from '../../types'


export const Unknown: React.FC<WidgetProps<SupervisorWidgetConfigBase>> = ({
    config,
}) => {
    return (
        <div className="h-full overflow-auto p-2">
                Unknown widget type
            <pre>{JSON.stringify(config, null, 2)}</pre>
        </div>
    )
}

