import React from 'react'

import {tcn} from '@bpinc/lib-tailwind-classnames'

import {useEditMode, useShowTitleBar} from '../../ui-context/UIContext'
import {BaseHeaderContent} from './BaseHeaderContent'
import {EditHeaderContent} from './EditHeaderContent'


type Props = {
    className?: string
}

export const Header: React.FC<Props> = ({className}) => {
    const editMode = useEditMode()
    const showTitleBar = useShowTitleBar()

    return (
        <header
            className={tcn(
                'group',
                {'absolute inset-x-0 z-10 h-8': !showTitleBar.value},
            )}
            data-testid="wallboard-header"
        >
            <div
                className={tcn('flex h-[50px] items-center justify-between px-3 py-1.5',
                    className,
                    'bg-[--wallboard-header-bg]',
                    {
                        'opacity-0 group-hover:opacity-100': !showTitleBar.value,
                        'translate-y-[-100%] group-hover:translate-y-0': !showTitleBar.value,
                        'transition-all duration-500': !showTitleBar.value,
                    },
                )}
            >
                {editMode.value
                    ? <EditHeaderContent />
                    : <BaseHeaderContent />
                }
            </div>
        </header>
    )
}
