import {useQuery, useQueryClient} from '@tanstack/react-query'

import {MS_IN_SECOND} from '@bpinc/lib-helpers'
import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {SupervisorWidgetsApiServices} from '@bpinc/lib-widgets-urls'

import {getWidgetsSubscriptionData} from '../selectors'
import {queryKey} from './index'


export function useWidgetsSubscriptionQuery(widgets: SupervisorWidget[]) {
    // Get subscription data from widgets
    const subscriptionData = getWidgetsSubscriptionData(widgets)
    const queryClient = useQueryClient()

    // Create subscription
    const {data: resubscribed, isRefetching} = useQuery({
        queryKey: queryKey.subscribe(subscriptionData),
        queryFn: async () => {
            // Cancel previous subscription queries
            queryClient.cancelQueries({queryKey: queryKey.subscribe()})
            // Invalidate previous subscription data
            queryClient.invalidateQueries({queryKey: queryKey.subscribe_data()})

            // Delete previous subscription
            await SupervisorWidgetsApiServices.deleteSupervisorWidgetsSubscription()

            // Create new subscription
            await SupervisorWidgetsApiServices.createSupervisorWidgetsSubscription({requestBody: subscriptionData})
            return true
        },
        gcTime: MS_IN_SECOND * 5,
        staleTime: MS_IN_SECOND * 5,
        // Refetch only if subscription data exists
        enabled: !!Object.keys(subscriptionData).length,
    })

    // Get subscription data
    return useQuery({
        queryKey: queryKey.subscribe_data(),
        queryFn: SupervisorWidgetsApiServices.getSupervisorWidgetsSubscriptionData,
        // Refetch every 5 seconds
        refetchInterval: MS_IN_SECOND * 5,
        retry(failureCount) {
            if (failureCount > 2) {
                queryClient.invalidateQueries({queryKey: queryKey.subscribe(subscriptionData)})
                return false
            }

            return true
        },
        // Refetch only if subscription was created
        enabled: !!resubscribed && !isRefetching,
    })
}

