import {useMemo} from 'react'

import {typedKeys} from '@bpinc/lib-types-helpers'
import {useLocalizationState} from '@bpinc/state-localization-context'

import {STATS_CONFIG} from './config'
import type {StatsConfigTypeKeys, StatsDataType, StatsMessagesDeclarationTypeMapper} from './types'
import {STAT_LOCALIZATION_MAP} from './types'


export function useStatsOptions<StatsConfigType extends StatsConfigTypeKeys>(type: StatsConfigType) {
    const {t} = useLocalizationState()

    return useMemo(() => {
        const statsData = STATS_CONFIG[type]
        const part = t[STAT_LOCALIZATION_MAP[type]]

        return typedKeys(statsData).map(key => {
            const sData = statsData[key] as unknown as StatsDataType<StatsMessagesDeclarationTypeMapper<StatsConfigType>>
            return ({text: (part[sData.localizationKey as keyof typeof part] as unknown as () => string)(), value: key})
        })
    }, [t, type])
}

