import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'

import {TextField} from '@bpinc/lib-forms-ui'
import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {assertCorrectWidget} from '../helpers'
import type {FormValues} from '../types'


interface NewsWidgetFormValues {
    title: string
    newsCount?: number
}

export interface NewsWidgetSettingsModalParams extends ModalBase {
    type: 'news_widget_settings'
    replacingWidgetId: string
    formValues?: NewsWidgetFormValues
    onSaved?: () => void
}

export function assertFormValuesValidForNewsWidgetSettingsModal(
    fv: FormValues,
): asserts fv is NewsWidgetFormValues {
    if (!(
        'title' in fv &&
        'newsCount' in fv
    )) {
        throw new Error('Form values are not valid for news widget settings modal')
    }
}

type NewsSettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: NewsWidgetFormValues
    onSaved?: () => void
}

export const NewsSettingsModal: React.FC<NewsSettingsModalProps> = ({
    replacingWidgetId,
    onClose,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()

    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const {
        control,
        handleSubmit,
    } = useForm<NewsWidgetFormValues>({
        defaultValues: {
            title: formValues?.title || t.WALLBOARD_WIDGET_EMPTY.news(),
            newsCount: formValues?.newsCount || 6,
        },
    })

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)

        updateWidget({
            type: 'news',
            title: formData.title,
            subscription: {
                type: 'chat_messages',
                req: {
                    filter: '',
                    limit: Number(formData.newsCount),
                },
            },
            id: widget.id,
            x: widget.x,
            y: widget.y,
            width: widget.width,
            height: widget.height,
            show_settings: widget.show_settings,
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                { t.WALLBOARD_WIDGET_EMPTY.news() }
            </Modal.Header>
            <Modal.Content>
                <TextField
                    name="title"
                    fluid
                    placeholder={t.WALLBOARD_WIDGET_NEWS.title()}
                    className="mb-2"
                    control={control}
                />
                <TextField
                    name="newsCount"
                    type="number"
                    placeholder={t.WALLBOARD_WIDGET_NEWS.newsCount()}
                    fluid
                    control={control}
                />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton enabledInOffline onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
