import {cloneDeep} from 'lodash-es'

import type {AcdState} from '@bpinc/ad-transport-types'
import {acdStates} from '@bpinc/ad-transport-types'
import {toHHMMSS} from '@bpinc/lib-helpers'
import {uuid} from '@bpinc/lib-uuid'
import type {
    AgentGridValuesColumn,
    AgentStatsType,
    ServiceStatsType,
    SupervisorWidgetConfigGrid,
} from '@bpinc/lib-widgets-urls'
import type {MessagesDeclaration} from '@bpinc/state-localization'

import type {StatsConfigType, StatsConfigTypeKeys} from '../../stats/types'


function mapAcdState(state: string, t: MessagesDeclaration) {
    switch (state) {
        case 'busy':
            return t.WALLBOARD_WIDGETS_GRIDS.state_busy()
        case 'ready':
            return t.WALLBOARD_WIDGETS_GRIDS.state_ready()
        case 'after_call_work':
            return t.WALLBOARD_WIDGETS_GRIDS.state_after_call_work()
        case 'not_ready':
            return t.WALLBOARD_WIDGETS_GRIDS.state_not_ready()
        default:
            return t.WALLBOARD_WIDGETS_GRIDS.state_default()
    }
}
type StatType<
    ConfigType extends StatsConfigTypeKeys,
    Stat extends keyof StatsConfigType[ConfigType]
> = (
    type: ConfigType,
    stat: Stat
) => {
    format?: 'duration' | 'percent' | undefined
    align?: 'left'
    alertInverted?: boolean
}
type GridData = {
    value: string
    id: string
}[][]

type ProcessGridDataPrams = {
    config: SupervisorWidgetConfigGrid
    dataGrids: AgentGridValuesColumn[] | undefined
    t: MessagesDeclaration
    getStatsConfig: StatType<'statsService', ServiceStatsType> & StatType<'statsAgent', AgentStatsType>
}

export function processGridData({config, dataGrids: dataGridsRaw, getStatsConfig, t}: ProcessGridDataPrams): GridData | undefined {
    const type = config.subscription.type
    const {columns: configuredColumns, limit: maxItemsToShow, id: reqId} = cloneDeep(config.subscription.req)
    const dataGrids = cloneDeep(dataGridsRaw || [])
    const targetDataGrid = dataGrids.find(grid => grid?.id === reqId)

    const currentData = dataGrids.filter(item => item.id === reqId)[0]
    if (type == 'service_grid' && currentData && Array.isArray(currentData.values)) {
        currentData.values = currentData.values.slice(0, maxItemsToShow)
    }
    if (type == 'agent_grid' && currentData && Array.isArray(currentData.values)) {
        const stateCol = configuredColumns.length + 1
        let currentValues = currentData.values.filter(item => {
            const state = item[stateCol] as AcdState
            return acdStates.includes(state)
        })
        currentValues = currentValues.map(item => {
            delete item[stateCol]
            return item
        })
        currentValues = currentValues.slice(0, maxItemsToShow)

        const totalContacts = configuredColumns.find(column => column.statName === 'total_contacts')
        if (totalContacts) {
            currentValues.forEach(value => {
                const id = parseInt(totalContacts?.id ? totalContacts.id : '0')
                const v1 = value[id]
                const v2 = value[id + 1]
                value[id] = '' + parseInt(v1) + parseInt(v2)
                delete value[id + 1]
            })
        }
        currentData.values = currentValues
    }

    configuredColumns.forEach(column => {
        const statsConfig = type === 'service_grid' ?
            getStatsConfig('statsService', column.statName as ServiceStatsType) :
            getStatsConfig('statsAgent', column.statName as AgentStatsType)

        const tempId = parseInt(column?.id || '')
        const widgetSubscriptionType = type
        if (
            (widgetSubscriptionType === 'agent_grid' && column.statName && statsConfig === undefined) ||
            (widgetSubscriptionType === 'service_grid' && column.statName && statsConfig === undefined)
        ) {
            console.warn('Widget: \'' + config.title + '\' uses unknown statistic name: ' + column.statName)
            return
        }

        if (
            (widgetSubscriptionType === 'agent_grid' && column.statName && statsConfig.format === 'duration') ||
            (widgetSubscriptionType === 'service_grid' && column.statName && statsConfig.format === 'duration')
        ) {
            targetDataGrid?.values?.forEach(val => {
                if (val[tempId] === null) {
                    val[tempId] = '0'
                }
                val[tempId] = toHHMMSS(Number(val[tempId]))
            })
        }

        if (
            (widgetSubscriptionType == 'agent_grid' && statsConfig.format == 'percent') ||
            (widgetSubscriptionType == 'service_grid' && statsConfig.format == 'percent')
        ) {
            targetDataGrid?.values?.forEach(val => {
                if (val[tempId] == null) {
                    val[tempId] = '0'
                }
                val[tempId] = val[tempId] + ' %'
            })
        }

        if (
            (widgetSubscriptionType === 'agent_grid' && statsConfig.align) ||
            (widgetSubscriptionType === 'service_grid' && statsConfig.align)
        ) {
            targetDataGrid?.values?.forEach(val => {
                if (val[tempId] === null || val[tempId] === '0') {
                    val[tempId] = ''
                }
            })
        }

        if (column.statName === 'acd_state') {
            targetDataGrid?.values?.forEach(val => {
                val[Number(column.id)] = mapAcdState(val[Number(column.id)], t)
            })
        }
    })

    const result = targetDataGrid?.values?.map(obj => {
        return Object.values(obj).map(prop => {
            return {value: prop == null ? '0' : prop, id: uuid()}
        })
    })

    return result
}
