import {useQuery} from '@tanstack/react-query'

import {MS_IN_MINUTE} from '@bpinc/lib-helpers'
import {SupervisorWidgetsApiServices} from '@bpinc/lib-widgets-urls'

import {queryKey} from './index'


export function useServicesQuery() {
    return useQuery({
        queryKey: queryKey.services(),
        queryFn: SupervisorWidgetsApiServices.getServices,
        staleTime: MS_IN_MINUTE * 5,
    })
}
