import {useCallback} from 'react'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import {SummarySettingsModal} from '../../../SummarySettingsModal'


export const useSummaryWidget = ({
    onSaved,
}: {
    onSaved: () => void
}) => {
    const openSummarySettingsModal = useModalComponentOpener(SummarySettingsModal)

    return useCallback((widgetConfig: SupervisorWidget) => {

        openSummarySettingsModal({
            replacingWidgetId: widgetConfig.id,
        })
        onSaved()

    }, [onSaved, openSummarySettingsModal])
}
