import {assertDefined, assertTrue} from '@bpinc/lib-types-helpers'
import type {SupervisorWidget, SupervisorWidgetAgentGridSubscriptionData} from '@bpinc/lib-widgets-urls'


export function assertCorrectWidget(widget: SupervisorWidget | null, currentWidgetId: string): asserts widget is SupervisorWidget {
    assertDefined(widget)
    assertTrue(widget.id === currentWidgetId, 'Wrong widget id for update')
}

export function getAgentGridSubscriptionDefaults() {
    return {
        filter_by: '1',
        filter_operation: 'NE',
        filter_value: '',
        limit: 1000,
        order_by: '1',
        order_dir: 'ASC',
        team_ids: [],
    }
}

export function createDispositionsWidgetSubscription(widgetId: string, userId: string): SupervisorWidgetAgentGridSubscriptionData {
    return {
        type: 'dispositions',
        req: {
            ...getAgentGridSubscriptionDefaults(),
            columns: [
                {
                    id: '1',
                    statName: 'top_dispositions_per_day',
                },
            ],
            id: widgetId,
            user_id: userId,
        },
    }
}

export function createSatisfactionWidgetSubscription(widgetId: string, userId: string): SupervisorWidgetAgentGridSubscriptionData {
    return {
        type: 'satisfaction',
        req: {
            ...getAgentGridSubscriptionDefaults(),
            columns: [
                {
                    id: '1',
                    statName: 'agent_fcr',
                },
                {
                    id: '2',
                    statName: 'agent_nps',
                },
                {
                    id: '3',
                    statName: 'agent_csat',
                },
            ],
            id: widgetId,
            user_id: userId,
        },
    }
}

