import {useCallback} from 'react'

import {uuid} from '@bpinc/lib-uuid'
import type {SupervisorWidget, SupervisorWidgetConfigEmpty} from '@bpinc/lib-widgets-urls'

import type {Size} from '../../types'


type Prams = {
    addWidget: (widget: SupervisorWidget) => void
    minCardSize: Size
}

export function useCreateEmptyWidget({addWidget, minCardSize}: Prams) {
    return useCallback(({start, end}: {
        start: {x: number; y: number}
        end: {x: number; y: number}
    }) => {
        if (!start || !end) {
            return
        }

        const id = uuid()
        const widget: SupervisorWidgetConfigEmpty = {
            id,
            type: 'empty',
            title: 'New Card',
            x: start.x,
            y: start.y,
            width: Math.max(end.x - start.x + 1, minCardSize.width),
            height: Math.max(end.y - start.y + 1, minCardSize.height),
            show_settings: true,
        }

        addWidget(widget)
    }, [addWidget, minCardSize.height, minCardSize.width])
}
