import type {ReactElement, ReactFragment} from 'react'
import React from 'react'
import {useParams} from 'react-router-dom'

import type {FCWithChildren} from '@bpinc/lib-types-helpers'
import {Loader} from '@bpinc/lib-ui-elements'

import {EditingContextProvider} from '../editing-context'
import {useIsLoading} from '../layout/context/RouterContext'
import {PageEffects} from './PageEffects'


export const PagePreloader: FCWithChildren<ReactElement | ReactFragment> = React.memo(({children}) => {
    const isLoading = useIsLoading()

    const {wallboardId} = useParams()

    if (isLoading || !wallboardId) {
        return <Loader active color="primary" size="big" />
    }

    return (
        <EditingContextProvider>
            <PageEffects>
                {children}
            </PageEffects>
        </EditingContextProvider>
    )
})
