import {useCallback} from 'react'

import type {SupervisorWidget} from '@bpinc/lib-widgets-urls'
import {useModalComponentOpener} from '@bpinc/state-modals-context'

import type {SupervisorWidgetTypes} from '../../../../types'
import {SingleOrGaugeSettingsModal} from '../../../SingleOrGaugeSettingsModal'


export const useSingleOrGaugeWidget = ({
    onSaved,
}: {
    onSaved: () => void
}) => {
    const openSingleOrGaugeSettingsModal = useModalComponentOpener(SingleOrGaugeSettingsModal)

    return useCallback((widgetType: Extract<SupervisorWidgetTypes, 'gauge' | 'single'>, widget: SupervisorWidget) => {
        openSingleOrGaugeSettingsModal({
            widgetType,
            replacingWidgetId: widget.id,
            onSaved,
        })
        onSaved()

    }, [onSaved, openSingleOrGaugeSettingsModal])
}
