import React, {useEffect, useState} from 'react'
import dayjs from 'dayjs'


export const DateTimeWidget: React.FC = () => {
    const [now, setNow] = useState(Date.now())

    useEffect(() => {
        const interval = setInterval(() => {
            setNow(Date.now())
        }, 1000)

        return () => {
            clearInterval(interval)
        }
    }, [])

    const date = dayjs(now).format('D MMMM YYYY')
    const time = dayjs(now).format('HH:mm:ss')


    return (
        <div className="flex h-full w-full flex-col justify-center text-center text-[4vmin]">
            <div>
                {date}
                <br />
                {time}
            </div>
        </div>
    )
}

