import React, {useMemo} from 'react'
import {useForm} from 'react-hook-form'
import {useAtomValue} from 'jotai'
import {pick} from 'lodash-es'

import {Modal, SemanticButton} from '@bpinc/lib-ui-elements'
import {useLocalizationState} from '@bpinc/state-localization-context'
import type {CommonModalProps, ModalBase} from '@bpinc/state-modals-types'

import {updatingWidgetDataAtom} from '../../atoms'
import {assertCorrectWidget} from '../helpers'
import type {FormValues} from '../types'
import {ImageStyleSelect} from './ImageStyleSelect'
import {SelectImage} from './SelectImage'
import type {ImageWidgetStyle} from './types'


export interface ImageWidgetSettingsModalFormValues {
    style: ImageWidgetStyle
    imageName: string
    imageUrl: string
}

export function assertFormValuesValidForImageWidgetSettingsModal(
    fv: FormValues,
): asserts fv is ImageWidgetSettingsModalFormValues {
    if (!(
        'style' in fv &&
        'imageName' in fv &&
        'imageUrl' in fv
    )) {
        throw new Error('Form values is not valid for image widget settings modal')
    }
}


export interface ImageWidgetSettingsModalParams extends ModalBase {
    type: 'image_widget_settings'
    replacingWidgetId: string
    formValues?: ImageWidgetSettingsModalFormValues
    onSaved?: () => void
}

type ImageSettingsModalProps = CommonModalProps & {
    replacingWidgetId: string
    formValues?: ImageWidgetSettingsModalFormValues
    onSaved?: () => void
}

export const ImageSettingsModal: React.FC<ImageSettingsModalProps> = ({
    replacingWidgetId,
    onClose,
    onSaved,
    formValues,
    ...commonModalProps
}) => {
    const {t} = useLocalizationState()
    const {widget, updateWidget} = useAtomValue(updatingWidgetDataAtom)

    const {
        control,
        handleSubmit,
    } = useForm<ImageWidgetSettingsModalFormValues>({
        defaultValues: {
            style: formValues?.style || 'cover',
            imageName: formValues?.imageName || '',
            imageUrl: formValues?.imageUrl || '',
        },
    })

    const save = useMemo(() => handleSubmit(async formData => {
        assertCorrectWidget(widget, replacingWidgetId)
        updateWidget({
            type: 'image',
            ...formData,
            ...pick(
                widget,
                'id',
                'x',
                'y',
                'width',
                'height',
                'title',
                'show_settings',
            ),
        })

        onSaved?.()
        onClose()
    }), [handleSubmit, widget, replacingWidgetId, updateWidget, onSaved, onClose])

    return (
        <Modal
            {...commonModalProps}
            className="max-w-full"
            onClose={onClose}
        >
            <Modal.Header role="heading">
                { t.WALLBOARD_WIDGET_IMAGE.name() }
            </Modal.Header>
            <Modal.Content>
                <SelectImage control={control} />
                <ImageStyleSelect control={control} />
            </Modal.Content>
            <Modal.Actions>
                <SemanticButton enabledInOffline onClick={() => onClose()}>
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonCancel()}
                </SemanticButton>
                <SemanticButton
                    onClick={save}
                    primary
                >
                    {t.WINDOW_POPUP_TITLES_AND_MESSAGES.window_buttonOk()}
                </SemanticButton>
            </Modal.Actions>
        </Modal>
    )
}
